.container {
  @include container (1476px, 60px 20px 0);
  width: 100%;

  @include desktop-medium {
    max-width: 100%;
  }

  @include tablet {
    margin-top: 10vh;

    &--advantages {
      margin-top: 0;
    }
  }

  @include mobile-wide {
    margin-top: 0;
  }

  @include mobile {
    @include container ($mobile-width, 30px 15px);
  }

  &--d-flex {
    display: flex;
  }
}

.container--dealers {
  padding-top: 22px;
  margin-bottom: calc(50px + 100px);

  @include desktop-medium {
    max-width: 100%;
    padding-top: 1.1vw;
    margin-bottom: calc(5.2vw + 3vw);
    padding-left: 12.2vw;
    padding-right: 8.9vw;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 0;
      padding-bottom: 5px;
    }
  }
    
  @include tablet {
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 30px;
    margin-bottom: 65px;
    padding-top: 6vh;
  }

  @include mobile-wide {
    display: flex;
    flex-direction: column;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 25px;
    margin-bottom: 7px;
  }
}

.container--main {
  padding: 0 0 0 70px;

  @include mobile-wide {
    padding-bottom: 118px;
  }
}

.container--1620 {
  max-width: 1620px;

  @include desktop-medium {
    padding-left: 11%;
    padding-right: 7.4%;
    max-width: 100%;
  }

  @include tablet {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 6vh;
  }

  @include mobile-wide {
    padding-top: 0;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.container--advantages {
  position: relative;
  padding: 0 0 0 90px;
  margin-bottom: 5px;

  @include desktop-medium {
    padding: 0 0 0 4.7vw;
    padding-left: 12.2vw;
    padding-right: 7.4%;
    margin-bottom: 0.2vw;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      margin-bottom: 0;
    }
  }

  @include tablet {
    padding: 0 20px;
    // padding-top: 6vh;
  }

  @include mobile {
    padding: 0 15px;
  }
  padding-top: 0;
}

.container--news {
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 53px;

  @include desktop-medium {
    padding-left: 12.2vw;
    padding-right: 12.2vw;
    padding-top: 2.7vw;
    max-width: 100%;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 18px;
    }
  }
    
  @include tablet {
    padding-left: 0;
    padding-right: 0;
    padding-top: 6vh;
  }

  @include mobile-wide {
    padding: 0;

    .integrate__item--media & {
      padding-bottom: 38px;
    }
  }
}

.container--1660 {
  max-width: 1660px;

  @include desktop-medium {
    padding-left: 7.4vw;
    padding-right: 7.1vw;
    max-width: 100%;
    padding-top: 3.1vw;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 18px;
    }
  }
    
  @include tablet {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 6vh;
  }

  @include mobile-wide {
    flex-direction: column;
    padding-left: 15px;
    padding-right: 15px;

    .integrate__item--about-us & {
      padding-top: 48px;
      padding-bottom: 14px;
    }
  }
}


.container--1536 {
  max-width: 1536px;

  @include desktop-medium {
    padding-right: 10.5vw;
    padding-left: 10.9vw;
    max-width: 100%;
    padding-top: 3.1vw;
  }

  @include desktop-medium {
    @media (max-height: 800px) {
      padding-top: 18px;
    }
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 18px;
    }
  }
    
  @include tablet {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 6vh;
  }

  @include mobile-wide {
    flex-direction: column;
    padding-top: 30px;
    padding-left: 0;
    padding-right: 0;
  }
}
