.desktop-delivery {
  .desktop-delivery__container {
    @include min-width(320px) {
      padding: 30px 15px;
      width: 100%;
      margin: 0 auto;
    }
    @include min-width(500px) {
      padding: 30px 30px;
    }
    @include min-width(992px) {
      padding: 56px 0px;
      max-width: 896px;
    }
    @include min-width(1350px) {
      max-width: 1194px;
    }
    & > h1 {
      @include min-width(320px) {
        font-size: 24px;
        font-family: $font-bebasneue;
        font-weight: 100;
        line-height: 107.1%;
      }
      @include min-width(992px) {
        font-size: 50px;
      }
      @include min-width(1600px) {
        position: relative;
        left: get-vw(-150px);
      }
    }
    .desktop-delivery__content {
      font-size: 24px;
      line-height: 28px;

      @include min-width(320px) {
        font-family: $font-merel;
        padding-top: 10px;
        max-width: 1194px;
        width: 100%;
        margin: 0 auto;
        padding-bottom: 40px;
        font-size: 14px;
        line-height: 16px;
      }
      @include min-width(992px) {
        padding-top: 60px;
        font-size: 24px;
        line-height: 30px;
        padding-bottom: 80px;
      }
      & > p {
        @include min-width(320px) {
          padding-bottom: 15px;
        }
        @include min-width(992px) {
          padding-bottom: 24px;
        }
      }
      p:last-child {
        padding-bottom: 0;
      }
      .desktop-delivery__content_address-link {
        @include min-width(320px) {
          color: white;
          padding: 17px 22px;
          font-size: 18px;
          margin: 0px auto 25px;
          max-width: 290px;
          display: block;
          background: linear-gradient(180.02deg, #464654 0.09%, #363640 99.91%),
            linear-gradient(180.02deg, #3b3b47 41.16%, #2b2b33 99.91%), #33333d;
          box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
        }
        @include min-width(992px) {
          padding: 20px 52px;
          max-width: 432px;
          font-size: 24px;
          margin: 25px auto 50px;
        }
      }
    }
  }
}
