.desktop-news {
  .news__container {
    max-width: 1680px;
    padding: 0;

    .news__title {
      @include min-width(320px) {
        padding-top: 30px;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        font-family: $font-bebasneue;
        text-transform: uppercase;
        padding-left: 15px;
      }
      @include min-width(768px) {
        padding-left: 80px;
        font-size: 50px;
        padding-top: 54px;
      }
    }
  }

  // section 1
  .general-news {
    @include min-width(992px) {
      padding-top: 60px;
      font-family: $font-merel;
      font-size: 18px;
      // line-height: 1px;
      display: flex;
      justify-content: space-between;
    }
    @include min-width(1500px) {
      padding-top: 60px;
      font-family: $font-merel;
      font-size: 26px;
      display: flex;
      justify-content: space-between;
    }

    .news__item {
      @include min-width(320px) {
        max-width: 708px;
      }
      @include min-width(768px) {
        margin-right: 30px;
      }
      @include min-width(1600px) {
        margin-right: 0px;
      }

      &:last-child {
        margin-right: 0;
      }

      .news__item_container {
        font-family: $font-merel;
        font-weight: 100;
        padding: 0px 27px;
        line-height: 16px;

        .news__item_title {
          @include min-width(992px) {
            margin-top: 10px;
            font-size: 21px;
            line-height: get-vw(37px);
            text-transform: none;
            font-weight: 100;
            height: get-vw(145px);
          }
          @include min-width(1500px) {
            font-size: 31px;
            height: get-vw(158px);
          }
        }
        .news__item_desc {
          @include min-width(320px) {
            color: #c4c4c4;
            padding-top: 5px;
          }
          @include min-width(992px) {
            padding-top: get-vw(11px);
            height: get-vw(104px);
            line-height: get-vw(31px);
          }
        }

        .news__item_footer {
          @include min-width(320px) {
            display: flex;
            justify-content: space-between;
            padding-top: 4px;
          }
          @include min-width(992px) {
            padding-top: get-vw(40px);
          }
          @include min-width(1200px) {
            padding-top: get-vw(10px);
          }
          @include min-width(1600px) {
            padding-top: 20px;
          }

          .footer__link_more {
            color: #ed8528;
          }
          .footer__date {
            color: #c4c4c4;
          }
        }
      }
    }
  }
  .general-news__mobile {
    @include min-width(320px) {
      padding: 10px 15px 0px;
      flex-direction: column;
    }
    @include min-width(480px) {
      padding: 10px 30px 0px;
    }
    @include min-width(650px) {
      padding: 10px 100px 0px;
    }
    @include min-width(992px) {
      display: none;
    }

    .news__item {
      margin-bottom: 20px;
      .news__item_container {
        padding: 0px 7px;
        .news__item_title {
          font-size: 14px;
          font-weight: 400;
          text-transform: none;
          line-height: 18px;
        }
        .news__item_desc {
          font-size: 12px;
          line-height: 14px;
          // line-height: ;
        }
        .news__item_footer {
          .footer__link_more {
            font-size: 12px;
            line-height: 14px;
          }
          .footer__date {
            font-size: 12px;
            line-height: 14px;
          }
        }
      }
    }
  }
  .additional-news__mobile {
    .additional-news {
      @include min-width(320px) {
        padding-bottom: 0px;
        padding-top: 10px;
      }
      @include min-width(768px) {
        padding-bottom: 30px;
      }
      .news__item {
        width: 255px;
        .news__item_container {
          padding: 0;
          .news__item_title {
            font-size: 14px;
            padding: 0px 5px 8px;
            text-align: center;
          }
          .news__item_footer {
            .footer__link_more {
              font-size: 12px;
            }
            .footer__date {
              font-size: 13px;
            }
          }
        }
      }
    }
  }
  .desktop-news-slider {
    @include min-width(320px) {
      display: none;
      margin: 0px 65px 83px;
    }
    @include min-width(992px) {
      display: block;
    }
    .desktop-news-slider__pagination {
      & > span {
        width: 100px;
        width: 81px;
        height: 7px;
        border-radius: 25px;
        background-color: #c4c4c4;
        opacity: 1;
        outline: 0;
      }
      .swiper-pagination-bullet-active {
        background-color: #4694d2;
      }
    }

    .swiper-pagination {
      bottom: 65px;
    }
    .desktop-news-slider__current-page {
      text-align: center;
      font-family: $font-merel;
      font-size: 24px;
      line-height: 28px;
      color: #c4c4c4;
      position: absolute;
      bottom: -3px;
      left: 50%;
      right: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%);
    }
  }
  // section 2
  .additional-news {
    @include min-width(992px) {
      padding-top: 60px;
      font-family: $font-merel;
      font-size: 14px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      padding-bottom: 90px;
    }
    @include min-width(1500px) {
      font-size: 17px;
    }

    .news__item {
      @include min-width(992px) {
        max-width: 270px;
        margin-bottom: 45px;
      }
      @include min-width(1225px) {
        max-width: 300px;
      }
      @include min-width(1346px) {
        max-width: 360px;
      }
      @include min-width(1500px) {
        max-width: 300px;
      }
      @include min-width(1500px) {
        max-width: 450px;
      }

      .news__item_container {
        .news__item_title {
          @include min-width(992px) {
            padding-top: 20px;
            padding-bottom: 25px;
            font-size: 16px;
            line-height: 23px;
            text-transform: none;
            font-weight: 100;
            background: #2d2d36;
            box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
            border-radius: 0px 0px 9px 9px;
            text-align: center;
          }
          @include min-width(1500px) {
            font-size: 20px;
          }
        }
        .news__item_desc {
          color: #c4c4c4;
          padding-top: 20px;
          padding-bottom: 25px;
          line-height: 20px;

          background: #494957;
          box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
          border-radius: 0px 0px 9px 9px;
          padding-left: 10px;
        }

        .news__item_footer {
          padding: 12px 10px 15px 10px;
          display: flex;
          justify-content: space-between;
          background: #24242b;
          border-radius: 0px 0px 9px 9px;

          .footer__link_more {
            color: #ed8528;
          }
          .footer__date {
            color: #c4c4c4;
          }
        }
      }
    }
  }
  #news {
    cursor: default;

    &:after {
      display: none;
    }
  }
}
