@font-face {
  font-weight: 400;
  font-family: "Merel";
  font-style: normal;
  font-display: swap;
  src:
    local("Merel Regular"),
    local("Merel-Regular"),
    url("../fonts/Merel-Regular.woff2") format("woff2"),
    url("../fonts/Merel-Regular.woff") format("woff");
}

