.menu-btn {
  @include button-reset;
  align-self: center;
  display: block;
  padding: 2px;
  padding-top: 0;
  transform: translateX(0);
  transition: transform 0.3s ease-in;
  color: #c4c4c4;
  width: 26px;
  height: 18px;

  &:focus {
    outline: none;
    color: rgba(#000000, 0.4);
  }

  @include hover-opacity;

  @include tablet {
    padding: 2px;
    width: 26px;
    height: 18px;
  }
}

.menu-btn::before {
  top: 0;
}

.menu-btn__bar {
  top: calc(50%);
  transform: translateY(-50%);
}

.menu-btn::after {
  bottom: 0;
  margin-bottom: 0;
}

.menu-btn::before,
.menu-btn::after,
.menu-btn__bar {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  margin-bottom: 0;
  border-radius: 2px;
  background-color: $color-light-gray;
  transition: transform 0.3s ease;
  position: absolute;
  left: 0;
}

.menu-btn--active {
  .menu-btn__bar {
    opacity: 0;
  }

  &::before {
    width: 141%;
    left: -19%;
    top: 42%;
    background-color: $color-orange;
    transform: rotate(45deg);
    transform-origin: center center;
  }

  &::after {
    width: 141%;
    left: -19%;
    top: 42%;
    background-color: $color-orange;
    transform: rotate(-45deg);
    transform-origin: center center;
  }
}
