.contacts--dealers {
  padding-top: 13px;
  margin-left: auto;
  margin-right: auto;
  font-family: $font-merel;
  font-weight: 500;

  @include desktop-medium {
    padding-top: 0.6vw;
    margin-left: auto;
    margin-right: auto;
    font-size: 0.625em;
  }

  @include desktop-small {
    margin-left: 0;
  }

  @include tablet {
    width: 30%;
    min-width: 300px;
    margin-left: 0;
    margin-right: 0.1vw;
  }

  @include mobile-wide {
    display: block;
    align-self: center;
    margin-left: 8px;
    padding-top: 2px;
    width: 30%;
    min-width: 370px;
  }

  @include mobile {
    margin-left: 0;
    min-width: 270px;
  }
}

.contacts__left {
  padding-top: 6px;
  margin-right: 56px;

  @include desktop-medium {
    padding-top: 0.3vw;
    margin-right: 2.9vw;
  }

  @include tablet {
    padding-top: 6px;
    margin-right: 1vw;
  }
}

.contacts__group {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  margin-right: 30px;

  &:last-of-type {
    margin-right: 0;
  }

  @include desktop-medium {
    margin-right: 1.5vw;

    &:last-of-type {
      margin-right: 0;
    }
  }

  @include tablet {
    margin-right: 1vw;

    &:last-of-type {
      margin-right: 0;
    }
  }
}

.contacts__moskow {
  margin: 0;
  font-size: 16px;
  line-height: 17px;

  a {
    color: white;
  }

  @include tablet {
    font-size: 13px;
    line-height: 15px;
  }

  @include mobile-wide {
    font-size: 14px;
    line-height: 16px;
  }
  @include max-width(1899px) {
    font-size: 13px;
  }
}

.contacts__moskow--footer {
  @include desktop-medium {
    font-size: 13px;
    line-height: 15px;
  }

  @include tablet {
    font-size: 13px;
    line-height: 15px;
  }

  @include mobile-wide {
    font-size: 14px;
    line-height: 16px;
  }
}

.contacts__sity {
  margin: 0;
  font-size: 20px;
  line-height: 22px;
  font-weight: 700;

  @include max-width(1899px) {
    font-size: 14px;
  }

  @include tablet {
    padding-top: 3px;
    font-size: 14px;
    line-height: 16px;
  }

  @include mobile-wide {
    padding: 0;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 4px;
  }
}

.contacts__sity--footer {
  @include desktop-medium {
    font-size: 14px;
    line-height: 16px;
  }

  @include tablet {
    padding-top: 3px;
    font-size: 14px;
    line-height: 16px;
  }

  @include mobile-wide {
    padding: 0;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 4px;
  }
}

.contacts__phone {
  font-size: 14px;
  line-height: 15px;
  text-decoration: none;
  color: $color-white;
  transition: all 0.3s ease;
  margin-top: 3px;

  &:hover {
    color: $color-orange;
  }

  @include max-width(1899px) {
    font-size: 12px;
  }

  @include tablet {
    font-size: 12px;
    line-height: 14px;
  }

  @include mobile-wide {
    font-size: 14px;
    line-height: 16px;
  }
}

.contacts__phone--footer {
  @include desktop-medium {
    font-size: 12px;
    line-height: 14px;
  }

  @include tablet {
    font-size: 12px;
    line-height: 14px;
  }

  @include mobile-wide {
    font-size: 14px;
    line-height: 16px;
  }
}

.contacts__top {
  text-align: left;

  @include tablet {
    text-align: center;
  }

  @include mobile-wide {
    margin-bottom: 20px;
  }
}

.contacts__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 12px;

  @include desktop-medium {
    margin-top: 0.6vw;
  }

  @include tablet {
    justify-content: space-around;
    margin-top: 0;
  }

  @include mobile-wide {
    margin-top: 12px;
    width: auto;
  }

  @include mobile {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
