.map {
  width: 1100px;
  height: 649px;

  @include list-reset;
  position: relative;

  @include desktop-medium {
    position: absolute;
    padding-top: 59%;
    width: 100%;
    height: 0;
    max-width: 57.8vw;
    max-height: 34.1vw;
  }

  @include tablet {
    max-width: none;
    max-height: none;
  }

  @include mobile-wide {
    display: none;
  }
}

.map__item {
  position: absolute;

  svg {
    fill: $color-blue-map;
    transition: 0.3s fill ease;
  }

  @include desktop-medium {
    svg {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }
}

.map__item--lighting {
  svg {
    fill: $color-blue-map-active;
  }
}

