@keyframes animation_visible {
  0% {
    opacity: 0;
    transform: translateY(-5%);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes animation_visible-reverse {
  0% {
    opacity: 1;
    transform: translate(0);
  }

  100% {
    opacity: 0;
    transform: translateY(5%);
  }
}

.animate {
  animation-name: animation_visible;
  animation-fill-mode: both;
  animation-duration: 1s;
}

.animate--reverse {
  animation-name: animation_visible-reverse;
  animation-fill-mode: both;
  animation-duration: 0.3s;
}
