.projects {
  padding-bottom: 100px;

  @include desktop-medium {
    padding-bottom: 5.2vw;
  }

  @include desktop-medium {
    @media (max-height: 800px) {
      padding-top: 0;
      padding-bottom: 0;
      margin-bottom: 0;
    }
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 0;
      padding-bottom: 0;
      margin-bottom: 0;
    }
  }
  
  @include tablet {
    padding-bottom: 60px;
  }

  @include mobile-wide {
    padding-bottom: 0;
  }
}

.projects__container {
  max-width: 1476px;
  margin: 0 auto;
  padding-top: 65px;
  padding-bottom: 45px;
  padding-left: 20px;
  padding-right: 20px;

  @include desktop-medium {
    max-width: 77.6vw;
    padding-top: 3.4vw;
    padding-bottom: 2.3vw;
    padding-left: 1vw;
    padding-right: 1vw;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 2vw; //0;
      padding-bottom: 0;
      margin-bottom: 0;
    }
  }
  
  @include tablet {
    max-width: none;
    padding-top: 5px;
    padding-bottom: 45px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @include mobile-wide {
    padding-top: 5px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 25px;
  }
}

.projects__title {
  margin-bottom: 28px;
  letter-spacing: 1px;
  line-height: 1;

  @include desktop-medium {
    margin-bottom: 1.4vw;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 0;
      padding-bottom: 0;
      margin-bottom: 0;
    }
  }
    
  @include mobile-wide {
    margin-bottom: 12px;
    font-size: 24px;
    line-height: 1;
    letter-spacing: normal;
  }
}

.projects__list {
  @include list-reset;
  display: flex;
  align-items: flex-start;

  @include mobile-wide {
    flex-direction: column;
  }
}

.projects__item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-start;
  width: 406px;
  margin-right: 110px;
  font-family: $font-merel;
  font-size: 20px;
  line-height: 28px;

  &:nth-child(3n) {
    margin-right: 0;
  }

  @include desktop-medium {
    width: 21.3vw;
    margin-right: 5.8vw;
    font-size: 0.5em;
    line-height: 1.45;
  }

  @include tablet {
    width: calc(100% / 3);
    margin-right: 40px;
    font-size: 11px;
    line-height: 17px;
  }

  @include mobile-wide {
    position: relative;
    width: auto;
    padding-bottom: 20px;
    margin-right: 0;
    font-size: 20px;
    line-height: 28px;

    &:not(:last-child)::before {
      content: "";
      display: block;
      position: absolute;
      left: -15px;
      right: -15px;
      bottom: 6px;
      border: 1px solid rgba(70, 70, 84, 0.5);
    }
  }
}

.projects__item-text {
  margin: 0;
  padding: 0;
  padding-top: 15px;
  padding-bottom: 28px;
  margin-bottom: auto;

  @include desktop-medium {
    padding-top: 0.7vw;
    padding-bottom: 1.5vw;
  }

  @include tablet {
    font-size: 11px;
    line-height: 17px;
  }

  @include mobile-wide {
    padding-top: 12px;
    padding-bottom: 12px;
    font-size: 14px;
    line-height: 19px;

    br {
      display: none;
    }
  }
}

.projects__item-author {
  font-size: 29px;
  line-height: 31px;
  letter-spacing: 0.8px;
  color: #1d83d5;

  @include desktop-medium {
    font-size: 1.45em;
    line-height: 1.068;
  }

  @include tablet {
    font-size: 16px;
    line-height: 17px;
  }

  @include mobile-wide {
    font-size: 18px;
    line-height: 21px;
    letter-spacing: normal;
  }
}

.projects__item-info {
  padding-top: 14px;
  font-size: 18px;
  line-height: 21px;

  @include desktop-medium {
    padding-top: 0.6vw;
    font-size: 17px;//0.9em;
    line-height: 1.2;
  }

  @include tablet {
    padding-top: 14px;
    font-size: 10px;
    line-height: 12px;
  }

  @include mobile-wide {
    padding-top: 2px;
    font-size: 14px;
    line-height: 18px;
  }
}

