.about-company {
  .about-company_background_triangle {
    @include min-width(992px) {
      opacity: 0;
      transform: translateY(50px);
      transition: all 1s ease-out;
    }
  }
  .about-company_desc {
    @include min-width(992px) {
      opacity: 0;
      transform: translateY(50px);
      transition: all 0.5s ease-out;
    }
  }
  .about-company_scope {
    @include min-width(992px) {
      opacity: 0;
      transition: all 0.5s ease-out;
    }
  }
  .fade-in {
    opacity: 1;
    transform: translateY(0);
    margin: 10px;
  }
}
