.site-nav {
  display: none;
  padding: 0;
  opacity: 0;
  background-color: transparent;
  transition: opacity 0.5s ease;
}

.site-nav--open {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 20px;
  padding-top: 145px;
  min-height: 100vh;
  margin: 0;
  z-index: 300;
  opacity: 1;
  color: $color-white;
  background-color: #363640;
  background-image: url("../img/background-interface@1x.png");
  animation: opacityChange 0.5s ease;

  @include retina {
    background: url("../img/background-interface@2x.png") repeat 0 0/cover $color-dark-gray-background;
    background-size: 1900px 2875px;
  }

  @include desktop-medium {
    padding: 1vw;
    padding-top: 7.6vw;
  }

  @include tablet {
    bottom: auto;
  }

  @include mobile-wide {
    bottom: 10px;
    overflow-y: scroll;
    height: 100vh;
    padding-top: 85px;
    padding-bottom: 20px;
    padding-right: 14px;
    background-image: none;
    background-color: #363640;

    &::before {
      content: "";
      display: block;
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      height: 70px;
      background-color: #363640;
    }
  }
}

.site-nav__container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1536px;
  padding-left: 78px;
  padding-right: 20px;
  margin: 0 auto;

  @include desktop-medium {
    max-width: 80.8vw;
    padding-left: 9.3vw;
    padding-right: 1vw;
  }

  @include tablet {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
  }

  @include mobile-wide {
    display: block;
    flex-direction: column;
    flex-wrap: nowrap;
    width: auto;
    margin-left: auto;
    margin-right: auto;
    padding-left: 0;
    padding-right: 0;
  }
}

.site-nav__col {
  width: 33.7%;
  padding-bottom: 30px;

  &:last-child {
    width: 29%;
  }

  &:first-child {
    width: auto;
    flex-grow: 1;
  }

  @include desktop-medium {
    padding-bottom: 2.1vw;

    &:last-child {
      flex-grow: 0;
    }
  }

  @include tablet {
    &:nth-child(n) {
      width: calc(100% / 3);
      padding-left: 20px;
      padding-bottom: 30px;
    }
  }

  @include tablet {
    &:nth-child(n) {
      width: 50%;
    }
  }

  @include mobile-wide {
    padding-bottom: 0;

    &:nth-child(n) {
      width: auto;
      padding-left: 0;
    }

    &:nth-child(3n) {
      margin-top: -12px;
    }

    &:last-child {
      //  ios
      padding-bottom: 60px;
    }
  }
}

.site-nav__list {
  @include list-reset;

  @include mobile-wide {
    padding-left: 20px;
    padding-bottom: 6px;
  }
}

.site-nav__item {
  padding-bottom: 10px;

  @include desktop-medium {
    padding-bottom: 0.5vw;
  }

  @include tablet {
    padding-bottom: 10px;
  }

  @include mobile-wide {
    padding-bottom: 28px;
  }
}

.site-nav__link {
  @include link-reset;
  font-family: $font-merel;
  font-size: 27px;
  line-height: 32px;
  color: $color-orange;

  @include hover-color (#ffffff);

  &:not(&--expand):active {
    opacity: 0.5;
  }

  @include desktop-medium {
    font-size: 1.4vw;
    line-height: 1.19;
  }

  @include mobile-wide {
    font-size: 20px;
    line-height: 22px;
  }
}

.site-nav__title,
.site-nav__link.site-nav__title {
  @include reset;
  display: block;
  padding-bottom: 35px;
  font-family: $font-bebasneue;
  font-weight: normal;
  font-size: 40px;
  line-height: 43px;
  color: #ffffff;

  @include desktop-medium {
    padding-bottom: 1.8vw;
    font-size: 2.1vw;
    line-height: 1.075;
  }

  @include mobile-wide {
    padding-bottom: 24px;
    font-size: 30px;
    line-height: 1;
  }
}

.site-nav__title.hidden-mobile {
  @include mobile-wide {
    display: none;
  }
}

.site-nav__title.hidden-desktop {
  display: none;

  @include mobile-wide {
    display: block;
  }
}

.site-nav__link.site-nav__title {
  padding-top: 45px;
  padding-bottom: 8px;

  @include hover-focus-opacity;

  &:active {
    opacity: 0.3;
  }

  @include desktop-medium {
    padding-top: 2.3vw;
    padding-bottom: 0.4vw;
  }

  @include mobile-wide {
    padding-top: 10px;
    padding-bottom: 50px;

    &:first-of-type {
      padding-top: 0;
    }
  }
}

.site-nav__expand-btn {
  @include button-reset;
  display: inline-block;
  margin-left: 8px;
  outline: none;
  transition: opacity $style-change-duration $style-change-function;

  .site-nav__link:hover & {
    opacity: 0.5;
  }

  @include desktop-medium {
    margin-left: 0.4vw;
  }

  @include mobile-wide {
    margin-left: 15px;
  }
}

.site-nav__expand-btn-icon {
  fill: none;

  &--up {
    display: none;
  }

  .site-nav__expand-btn--opened &--up {
    display: inline-block;
  }

  .site-nav__expand-btn--opened &--down {
    display: none;
  }
}

.site-nav__sublist {
  @include list-reset;
  height: 0;
  overflow: hidden;
  padding-top: 0;
  transition: all 0.5s ease-in-out;

  &--opened {
    height: auto;
    overflow: visible;
  }
}

.site-nav__subitem {
  padding-left: 20px;
  padding-bottom: 4px;

  &:first-child {
    padding-top: 14px;
  }

  @include desktop-medium {
    padding-left: 1vw;
    padding-bottom: 0.2vw;

    &:first-child {
      padding-top: 0.7vw;
    }
  }

  @include mobile-wide {
    padding-left: 10px;
    padding-bottom: 23px;
    line-height: 1;

    &:first-child {
      padding-top: 18px;
    }

    &:last-child {
      padding-bottom: 0;
    }
  }
}

.site-nav__sublink {
  @include link-reset;
  @include hover-focus-opacity;
  font-family: $font-merel;
  font-size: 24px;
  line-height: 28px;
  color: $color-light-gray;

  @include desktop-medium {
    font-size: 1.2vw;
    line-height: 1.166;
  }

  @include tablet {
    font-size: 24px;
    line-height: 28px;
  }

  @include mobile-wide {
    font-size: 20px;
    line-height: 26px;
  }
}


@keyframes opacityChange {
  from {
    opacity: 0;
  }
}
