.patents {
  position: relative;

  & .container {
    max-width: 1476px;
    padding-top: 53px;

    @include min-width (1024px) {
      max-width: 1100px;
      padding-top: 23px;
      padding-left: 4.2vw;
      padding-right: 4.2vw;
    }

    @include min-width (1366px) {
      max-width: 1400px;
      padding-left: 13.2vw;
      padding-right: 13.2vw;
    }

    @include min-width (1466px) {
      max-width: 100%;
      padding-left: 12.2vw;
      padding-right: 12.2vw;
    }

    @include min-width (1800px) {
      padding-left: 12.2vw;
      padding-right: 12.2vw;
    }

    @include min-width (1900px) {
      max-width: 1476px;
      padding-top: 53px;
      padding-right: 20px;
      padding-left: 20px;
    }

    @include tablet {
      padding-right: 5px;
      padding-bottom: 45px;

      @media (max-height: 500px) {
        padding-right: 20px;
      }
    }

    @include  mobile-wide {
      min-height: 418px;
      padding-bottom: 35px;
    }

    @include mobile {
      padding-left: 0;
      padding-top: 0;
    }
  }
}

.patents__header {
  display: flex;
  width: 100%;
  align-items: center;
  font-family: $font-bebasneue;

  & .patents__link {
    font-size: 2.5em;
    // line-height: 43px;
    // line-height: 1.075;
    line-height: 1.1;
    text-decoration: none;
    text-transform: uppercase;
    color: $color-blue;
    border-bottom: 1px dashed $color-blue;
    transition: all $style-change-duration $style-change-function;

    @include desktop-medium {
      padding: 0;
      font-size: 1.004em;
    }

    @include tablet {
      font-size: 24px;
      line-height: 1.1;
    }
  }

  & .patents__link--active {
    color: $color-white;
    border: none;
  }

  & .patents__link-patents {
    margin-right: 28px;

    @include laptop {
      margin-right: 37px;
    }

    @include mobile {
      margin-right: 23px;
    }
  }

  & .patents__btn {
    align-self: center;
    width: 220px;
    text-align: center;
    margin-left: auto;
    cursor: pointer;

    @include desktop-medium {
      width: 11.5vw;
      margin-top: 0.3vw;
    }

    @include tablet {
      position: absolute;
      width: 200px;
      margin-right: 0;
      padding: 17px 20px 13px;
      font-size: 16px;
      line-height: 16px;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
    }
  }

  @include mobile {
    padding-right: 0;
    padding-left: 15px;
    margin-bottom: 0;
    width: 320px;
    flex-shrink: 0;
  }

  @include min-width(481px) {
    padding-left: 0;
    padding-right: 10px;
  }
}

.patents__wrapper {
  margin-bottom: 33px;

  @include tablet {
    margin-bottom: 33px;
  }
}

.integrate__item--patents {
  & .patents__img {
    @include min-width (768px) {
      opacity: 0;
    }

    @include max-width (767px) {
      opacity: 1;
    }
  }
}

.patents__container {
  max-width: 1452px;
  padding-left: 15px;
  padding-top: 52px;

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 8px;
    }
  }

  @include mobile-wide {
    padding-top: 52px;
  }

  @include mobile {
    padding-left: 0;
    padding-top: 20px;
  }

  @include min-width(481px) {
    padding-left: 0;
  }
}

.patents__img {
  position: relative;
  min-width: 208px;
  min-height: 298px;
  max-height: 450px;
  max-width: 314px;
  padding: 2vh 0.6316vw;
  background-color: $color-white;
  border-radius: 10px;
  box-sizing: border-box;
  cursor: pointer;

  @media (max-height: 945px) {
    min-width: 100px;
    min-height: 100px;
  }

  @include tablet {
    width: 208px;
    flex-shrink: 0;
  }

  @include min-width(1024px) {

    &:hover .patents__img-full-size,
    &:focus .patents__img-full-size {
      opacity: 1;
    }
  }

  &:last-of-type {
    margin-right: 0;
  }
}

.patents__img-full-size {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 10px;
  background: linear-gradient(0deg, rgba(58, 58, 69, 0.5), rgba(59, 59, 71, 0.5)), linear-gradient(0deg, rgba(42, 50, 56, 0.5), rgba(42, 50, 56, 0.5));
  opacity: 0;
  transition: opacity $style-change-duration $style-change-function;

  & button {
    position: relative;
    width: 87.8%;
    padding: 0.9vw 2.6vh 0.74vw;
    font-family: $font-bebasneue;
    font-size: 1.563em;
    line-height: 21px;
    text-align: center;
    border: 2px solid $color-blue;
    background: transparent;
    border-radius: 100px;
    color: $color-white;
    outline: none;
    box-sizing: border-box;
    transition: all $style-change-duration $style-change-function;

    & svg {
      position: absolute;
      width: 17px;
      height: 17px;
      top: 50%;
      right: 30px;
      transform: translateY(-50%);
    }

    @media (min-aspect-ratio: 2/1) {
      svg {
        display: none;
      }
    }

    @include desktop-medium {
      font-size: 0.63em;
    }

    &:hover,
    &:focus {
      background: $color-blue;
    }
  }
}

.patents__slider-pagination {
  display: flex;
  justify-content: center;

  &.swiper-pagination.swiper-pagination-bullets {
    position: relative;
    bottom: 0;
    left: -4px;
  }

  & span {
    margin: 0;
    width: 81px;
    height: 7px;
    border-radius: 25px;
    background-color: $color-gray;
    opacity: 1;
    outline: none;
  }

  &.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0;
    margin-right: 9px;

    &:last-of-type {
      margin-right: 0;
    }
  }

  & .swiper-pagination-bullet-active {
    background: $color-gradient-blue;
  }

  @include tablet {
    display: none;
  }
}

.patents--animate {
  & .patents__link {
    @include min-width(1024px) {
      &:first-of-type {
        animation: slide-down-link 0.5s ease-in-out;
      }

      &:last-of-type {
        animation: slide-down-link 0.6s ease-in-out;
      }
    }
  }

  & .patents__btn {
    @include min-width(1024px) {
      animation: slide-right 0.7s ease-in-out;
    }
  }

  & .patents__slider-pagination {
    animation: pagination-opacity 1.4s ease-in-out;
  }
}

.patents--reverse {
  & .patents__link {
    @include min-width(1024px) {
      &:first-of-type {
        animation: slide-link-reverse 1s ease-in-out;
        animation-direction: reverse;
      }

      &:last-of-type {
        animation: slide-link-reverse 1.2s ease-in-out;
        animation-direction: reverse;
      }
    }
  }

  & .patents__btn {
    @include min-width(1024px) {
      animation: slide-right-reverse 1.3s ease-in-out;
      animation-direction: reverse;
    }
  }

  & .patents__slider-pagination {
    animation: pagination-opacity-reverse 1.4s ease-in-out;
    animation-direction: reverse;
  }
}


@keyframes slide-link-reverse {
  from {
    opacity: 0;
    transform: translateY(50px);
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.patents__slider--hidden {
  display: none;
}
