.desktop-contacts__how-to-go {
  @include min-width(320px) {
    display: block;
    justify-content: space-between;
    padding-top: get-vw(100px);
    padding-bottom: get-vw(60px);
  }
  @include min-width(992px) {
    display: flex;
  }

  & h3 {
    @include min-width(320px) {
      margin-bottom: get-vw(89px);
      padding-bottom: get-vw(10px);
      text-transform: none;
      font-size: 18px;
      line-height: 21px;
      padding-top: 20px;
      padding-left: 15px;
      font-weight: 100;
    }
    @include min-width(992px) {
      font-size: get-vw(30px);
      line-height: get-vw(35px);
      padding-top: get-vw(60px);
      border-bottom: 1px solid white;
      padding-left: 0px;
    }
  }

  .desktop-contacts-slider {
    @include min-width(320px) {
      margin: 0 auto;
      max-width: 320px;
    }
    @include min-width(992px) {
      margin: 0;
      max-width: get-vw(611px);
    }

    .swiper-container {
      @include min-width(320px) {
        height: 269px;
      }
      @include min-width(992px) {
        height: get-vw(490px);
      }

      .swiper-pagination {
        position: absolute;
        bottom: 0px;
        display: block;
      }
      .catalog__pagination {
        padding-left: 0;
        justify-content: center;
        left: 0;
      }
    }
    .swiper-pagination-bullets {
      position: absolute!important;
      
      .swiper-pagination-bullet {
        @include min-width(320px) {
          width: 44px;
        }
        @include min-width(992px) {
          width: get-vw(80px);
          height: get-vw(7px);
          bottom: 200px;
        }
      }
    }
    .address__map {
      @include min-width(320px) {
        max-height: 242px;
      }
      @include min-width(992px) {
        max-height: none;
      }
    }
  }
  .how-to-go__desc {
    @include min-width(320px) {
      margin-top: 30px;
      padding: 10px 15px;
      background: linear-gradient(180.27deg, #3b3b47 41.16%, #31313b 99.91%);
      box-shadow: 0px 0px 10px rgba(0, 17, 31, 0.25);
    }
    @include min-width(992px) {
      max-width: get-vw(999px);
      margin-top: 0px;
      padding: get-vw(50px) get-vw(60px);
      background: linear-gradient(180.08deg, #3b3b47 41.16%, #31313b 99.91%);
      box-shadow: 0px 0px 10px rgba(0, 17, 31, 0.25);
    }

    p {
      @include min-width(320px) {
        font-size: 14px;
        line-height: 16px;
        padding-top: 14px;
      }
      @include min-width(992px) {
        font-size: get-vw(20px);
        line-height: get-vw(30px);
        padding-top: get-vw(23px);
      }
    }
    :first-child {
      padding-top: 0px;
    }
    .how-to-go__desc_number {
      color: #ed8528;
    }
  }
}
