.desktop-contacts__address {
  @include min-width(320px) {
    padding-top: 20px;
    display: block;
    justify-content: space-between;
  }
  @include min-width(992px) {
    display: flex;
    padding-top: get-vw(60px);
  }

  .address__info {
    @include min-width(320px) {
      padding-left: 15px;
    }
    @include min-width(992px) {
      height: 48.78vw;
      max-width: 541px;
      width: 28.47vw;
      padding-left: 0px;
    }
    & a {
      @include min-width(320px) {
        font-size: 14px;
        line-height: 16px;
        color: #0075d1;
      }
      @include min-width(992px) {
        font-size: get-vw(22px);
        line-height: get-vw(26px);
      }
    }
    & h2 {
      @include min-width(320px) {
        text-transform: none;
        font-size: 18px;
        padding: get-vw(24px) 0 get-vw(16px);
        line-height: 21px;
      }
      @include min-width(992px) {
        font-size: get-vw(30px);
        line-height: get-vw(35px);
      }
    }
    :first-child > h2 {
      padding-top: 0px;
    }
    & p,
    address {
      @include min-width(320px) {
        font-size: 14px;
        line-height: 16px;
        font-style: normal;
      }
      @include min-width(992px) {
        font-size: get-vw(23px);
        line-height: get-vw(27px);
      }
    }
    .address__info_desc {
      p:first-child {
        padding-top: 0;
      }
      p {
        @include min-width(320px) {
          padding-top: 6px;
        }
        @include min-width(992px) {
          padding-top: get-vw(6px);
        }
      }
    }
    .address__info_clarify {
      color: rgba(255, 255, 255, 0.705);
    }
    .address__info_attention {
      @include min-width(320px) {
        font-size: 14px;
        line-height: 16px;
        color: #ed8528;
        padding-top: get-vw(15px);
      }
      @include min-width(992px) {
        font-size: get-vw(22px);
        line-height: get-vw(29px);
      }
    }
  }


  #map {

  }

  .address__map {
    @include min-width(320px) {
      position: relative;
      margin-top: 20px;
    }
    @include min-width(540px) {
      max-width: 512px;
      max-height: 433px;
      margin: 20px auto;
      display: block;
    }
    @include min-width(992px) {
      position: absolute;
      right: 0;
      width: get-vw(1095px);
      height: 45vw;
      margin-top: 0px;
      max-width: none;
      max-height: none;
    }
  }
}

[class*="ymaps-2"][class*="-ground-pane"] {
  filter: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'><filter id='grayscale'><feColorMatrix type='matrix' values='0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0'/></filter></svg>#grayscale");
  /* Firefox 3.5+ */
  -webkit-filter: grayscale(100%);
  /* Chrome 19+ & Safari 6+ */
}
