.starlink-main {
  .product__popup_video {
    background: rgba(42, 42, 50, 0.92);
    overflow: hidden;
    position: fixed;
    top: 0px;
    left: -1px;
    z-index: 999999999;
    width: 100%;
    height: 100%;

    .popup__video_content {
      margin: 0 auto;
      width: 67vw;
      height: 40vw;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);

      @include max-width(768px) {
        width: 90vw;
        height: 50vw;
      }

      .popup__video_close {
        background: none;
        border: none;
        position: absolute;
        top: -47px;
        right: 0;
        padding: 0;
        width: 26px;
        height: 26px;

        @include max-width(768px) {
          top: -33px;
          width: 18px;
          height: 18px;
        }
      }

      iframe {
        width: 100%;
        height: 100%;
        margin: 0 auto;
      }

      svg {
        fill: $color-white;
        width: 100%;
        height: 100%;
      }
    }
  }
}
