@keyframes animation_background-1 {
  from {
    background-position: 0 0, 0 0;
    background:rgba(0, 0, 0, 0.8);
  }

  50% {
    background-position: 0 0, 0 0;
    background: $color-dark-gray-background;
  }

  to {
    background-position: 0 100px, 0 200px;
  }
}

@keyframes animation_background-2 {
  from {
    background-position: 0 100px, 0 200px;
  }

  to {
    background-position: 0 0, 0 0;
  }
}

@keyframes animation_background-3 {
  from {
    background-position: 0 0, 0 0;
  }

  to {
    background-position: 0 -100px, 0 -200px;
  }
}

@keyframes animation_background-4 {
  from {
    background-position: 0 -100px, 0 -200px;
  }

  to {
    background-position: 0 -200px, 0 -300px;
  }
}

@keyframes animation_background-5 {
  from {
    background-position: 0 -200px, 0 -300px;
  }

  to {
    background-position: 0 -300px, 0 -300px;
  }
}

@keyframes animation_background-6 {
  from {
    background-position: 0 -300px, 0 -300px;
  }

  to {
    background-position: 0 -400px, 0 -350px;
  }
}

@keyframes animation_background-7 {
  from {
    background-position: 0 -400px, 0 -350px;
  }

  to {
    background-position: 0 -500px, 0 -400px;
  }
}

@keyframes animation_background-8 {
  from {
    background-position: 0 -500px, 0 -400px;
  }

  to {
    background-position: 0 -550px, 0 -500px;
  }
}

@keyframes animation_background-9 {
  from {
    background-position: 0 -550px, 0 -500px;
  }

  to {
    background-position: 0 -550px, 0 -600px;
  }
}

.main-theme {
  background-color: $color-dark-gray-background;
  min-height: calc(100vh - 2px);
  position: relative;
  z-index: -2;
  animation-fill-mode: both;
  animation-duration: 1s;

  @include desktop-medium {
    font-size: 2.1vw;
  }

  @include min-width ($laptop-width) {
    background: url("../img/back1.png"), url("../img/back2.png") repeat 0 0/cover $color-dark-gray-background;
  }

  @include mobile-wide {
    background-color: $color-mobile-background;
  }
}

.main-theme--dealers {
  padding: 0;
  margin: 0;
  font-family: "Arial", "Helvetica", sans-serif;
  position: relative;
  color: $color-white;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: space-between;
}

.main-theme--animation-1 {
  animation-name: animation_background-1;
  animation-duration: 2s;
  z-index: -2;

  @include mobile-wide {
    animation-name: none;
  }
}

.main-theme--animation-2 {
  animation-name: animation_background-2;

  @include mobile-wide {
    animation-name: none;
  }
}

.main-theme--animation-3 {
  animation-name: animation_background-3;

  @include mobile-wide {
    animation-name: none;
  }
}

.main-theme--animation-4 {
  animation-name: animation_background-4;

  @include mobile-wide {
    animation-name: none;
  }
}

.main-theme--animation-5 {
  animation-name: animation_background-5;

  @include mobile-wide {
    animation-name: none;
  }
}

.main-theme--animation-6 {
  animation-name: animation_background-6;

  @include mobile-wide {
    animation-name: none;
  }
}

.main-theme--animation-7 {
  animation-name: animation_background-7;

  @include mobile-wide {
    animation-name: none;
  }
}

.main-theme--animation-8 {
  animation-name: animation_background-8;

  @include mobile-wide {
    animation-name: none;
  }
}

.main-theme--animation-9 {
  animation-name: animation_background-9;
  max-height: 1000px;

  @include laptop {
    max-height: 917px;
  }

  @include mobile-wide {
    animation-name: none;
  }
}
