.integrate {
  @include list-reset;
}

.integrate__item {
  width: 100%;
}

.integrate__item--hidden {
  height: 0;
  overflow-y: hidden;
  visibility: hidden;

  .main-page__video-button,
  .round-button {
    opacity: 0;
  }

  @include mobile-wide {
    visibility: visible;
    height: auto;
    overflow-y: auto;

    .main-page__video-button,
    .round-button {
      opacity: 1;
    }
  }
}

.integrate__item--main {
  @include mobile-wide {
    background-color: $color-dark-gray-background;
    background-image: url("../img/background-mobile@1x.png");
    background-repeat: no-repeat;
    background-size: 100% auto;

    background-position: left top;

    @include retina {
      background-color: $color-dark-gray-background;
      background-image: url("../img/background-mobile@2x.png");
    }
  }
}

.integrate__item--clients {
  display: block;
  overflow: hidden;

  @include mobile {
    padding-top: 18px;
  }
}

.media-page {
  .integrate {
    display: flex;
    flex-direction: column;

    @media (min-height: 1001px) {
      flex-grow: 1;
    }

    @include desktop-small {
      @media (min-height: 740px) {
        flex-grow: 1;
      }
    }

    &__item {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
    }

    &__item--hidden {
      flex-shrink: 1;
      flex-grow: 0;
    }
  }
}
