.desk-norm {
  .choose-analog {
    position: relative;
    padding-left: 4.7vw;
    padding-right: 20px;
    font-family: $font-bebasneue;
    font-weight: 400;
    flex: 0 0 46.8%;

    & h2 {
      margin: 0;
      margin-bottom: 3.0457vh;
      font-size: 2.5em;
      line-height: 43px;

      @include desktop-medium {
        font-size: 1.004em;
      }

      @include tablet {
        margin-bottom: 9px;
        font-size: 24px;
        line-height: 24px;
      }
    }

    &::after {
      position: absolute;
      display: none;
      content: "";
      width: 1px;
      height: 54.8vh;
      top: 56%;
      right: -5px;
      background-color: $color-dark-gray-line;
      transform: translateY(-50%);

      @include min-width(1024px) {
        display: block;
      }
    }

    @include tablet {
      padding: 0;
      padding-bottom: 20px;
      border-bottom: 1px solid rgba(70, 70, 84, 0.5);
    }

    @include mobile {
      padding-left: 0;
    }
  }

  .cable-selection--animate {
    & .choose-analog {
      @include min-width(1024px) {
        &::after {
          animation: slide-after 0.7s ease-in-out;
        }
      }
    }
  }

  .cable-selection--reverse {
    & .choose-analog {
      @include min-width(1024px) {
        &::after {
          animation: slide-after-reverse 1.5s ease-in-out;
          animation-direction: reverse;
        }
      }
    }
  }

  @keyframes slide-after {
    from {
      top: 35.7%;
      height: 27.4vh;
      opacity: 0;
    }

    to {
      top: 56%;
      height: 54.8vh;
      opacity: 1;
    }
  }

  @keyframes slide-after-reverse {
    from {
      top: 35.7%;
      height: 27.4vh;
      opacity: 0;
    }

    to {
      top: 56%;
      height: 54.8vh;
      opacity: 1;
    }
  }

  .choose-analog__wrapper {
    position: relative;
    width: 84.5%;
    display: inline-block;
    vertical-align: middle;

    & svg {
      width: 1.316vw;
      height: 2.5vh;
      fill: $color-white;
      stroke: $color-white;

      @include tablet {
        width: 20px;
        height: 20px;
      }
    }

    @include tablet {
      width: 100%;
    }
  }

  .choose-analog__search-from {
    margin-bottom: 7.1066vh;

    & label {
      position: absolute;
      font-size: 0;
      top: 50%;
      right: 40px;
      transform: translateY(-50%);
      cursor: pointer;

      & svg {
        transition: all $style-change-duration $style-change-function;

        &:hover,
        &:focus {
          fill: $color-orange;
          stroke: $color-orange;
        }
      }

      @include tablet {
        right: 20px;
      }
    }

    & input {
      width: 100%;
      padding: 2.6vh 2.11vw 2vh;
      font-size: 1.5625em;
      line-height: 1em;
      background-color: $color-gray-blue-input;
      border: 1px solid $color-gray;
      border-radius: 100px;
      box-shadow: 0 1px 5px $color-box-shadow-input;
      color: $color-white;
      transition: opacity $style-change-duration $style-change-function;
      outline: none;

      @include desktop-medium {
        font-size: 0.63em;
      }

      @include tablet {
        padding: 17px 20px 13px 20px;
        font-size: 16px;
        line-height: 16px;
      }

      &:hover,
      &:focus {
        opacity: 0.7;
      }
    }

    @include tablet {
      margin-bottom: 30px;
    }
  }

  .choose-analog__search-list {
    @include list-reset;
    font-family: $font-merel;
    font-weight: 500;
    font-size: 1.25em;
    line-height: 23px;

    & li {
      margin-bottom: 1.7259vh;

      &:last-of-type {
        margin-bottom: 0;
      }

      @include desktop-medium {
        font-size: 0.41em;
      }

      @include tablet {
        margin-bottom: 11px;
      }
    }

    & a {
      text-decoration: none;
      color: $color-light-gray;

      @include hover-focus-opacity;

      @include tablet {
        font-size: 14px;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: -0.02em;
      }
    }
  }
}
