.header {
  width: 100%;
  background-color: rgba(42, 42, 50, 0.5);
  font-family: $font-merel;
  z-index: 400;
  transition: background $style-change-duration $style-change-function;

  .about-us-page & {
    background-color: rgba(42, 42, 50, 0);
  }

  @include desktop-medium {
    font-size: 0.625em;
  }

  @include tablet {
    font-size: auto;
  }

  @include mobile-wide {
    background-color: $color-mobile-background;
  }
}

input[type="search"]::-ms-clear {
  display: none;
}

.header__search-input::-webkit-search-decoration,
.header__search-input::-webkit-search-cancel-button,
.header__search-input::-webkit-search-results-button,
.header__search-input::-webkit-search-results-decoration {
  display: none;
  width: 0;
  height: 0;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-content: center;
  padding: 24px 82px;
  max-width: 1900px;
  margin: 0 auto;

  @include desktop-medium {
    padding: 1.2vw 4.3vw;
    max-width: 100%;
  }

  @include tablet {
    padding: 14px 20px;
  }

  @include mobile-wide {
    padding: 26px 19px 24px 17px;
  }
}

.header__left {
  width: 35%;
  display: flex;
  justify-content: space-between;
  align-content: center;
  position: relative;

  @include desktop-medium {
    width: 30%;
  }

  @include desktop-small {
    width: 24%;
  }

  @include tablet {
    width: 21%;
  }

  @include mobile-wide {
    width: 100%;
  }
}


.header__site-nav-button {
  position: relative;
  z-index: 350;
  padding: 0;
  background-color: transparent;
  border: none;
}

.header__nav {
  align-self: center;

  @include mobile-wide {
    order: 2;
  }
}

.header__search {
  align-self: center;
  cursor: pointer;
  position: relative;
  width: 89.3%;
  margin-left: 36px;
  display: flex;
  justify-content: flex-start;
  outline: none;
  height: 25px;

  svg {
    fill: $color-white;
    stroke: $color-gray;
  }

  @include desktop-medium {
    margin-left: 1.8vw;
    height: 1.3vw;
  }

  @include tablet {
    margin-left: 10px;
    height: 20px;
  }

  @include mobile-wide {
    margin-left: 0;
  }
}

.header__search-form {
  width: 100%;
  position: absolute;
  top: -24px;
  left: 0;
  opacity: 0;
  z-index: 100;
  transition: opacity 0.3s ease;
  outline: none;

  @include desktop-medium {
    top: -1.2vw;
  }

  @include tablet {
    top: -17px;
    width: calc(100% + 20px);
  }

  @include mobile-wide {
    width: 90%;
  }
}

.header__search-link {
  padding: 0;
  padding-right: 20px;
  height: 25px;
  width: 45px;
  display: inline-block;
  position: relative;
  transition: transform $style-change-duration $style-change-function;
  border: none;
  background: transparent;
  outline: none;
  z-index: 300;

  &::after {
    position: absolute;
    content: "";
    right: 0;
    top: -4px;
    width: 1px;
    height: calc(100% + 8px);
    background-color: $color-white;
  }

  svg {
    width: 100%;
    height: 100%;
  }

  @include desktop-medium {
    padding-right: 1vw;
    height: 1.3vw;
    width: 2.3vw;

    &::after {
      top: -0.2vw;
      height: calc(100% + 0.4vw);
    }
  }

  @include tablet {
    height: 20px;
    padding-right: 10px;
    width: 35px;

    &::after {
      height: calc(100% + 8px);
    }
  }

  @include mobile-wide {
    z-index: 450;
    padding-right: 0;
    width: 25px;

    &::after {
      display: none;
    }
  }
}

.header__search--opened .header__search-form {
  z-index: 350;
  opacity: 1;
}

.header__search--opened .header__search-button {
  display: block;
}

.header__search--opened ~ .header__logo-link {
  visibility: hidden;
}

.header__search--opened .header__search-link {
  z-index: 400;
  position: absolute;
  right: -50px;
  top: 0;

  svg {
    display: none;
  }

  &::after {
    top: 43%;
    left: 0;
    width: 20px;
    height: 2px;
    background-color: $color-white;
    transform: rotate(-45deg);
    transform-origin: center;
  }
  &::before {
    top: 43%;
    left: 0;
    position: absolute;
    content: "";
    width: 20px;
    height: 2px;
    background-color: $color-white;
    transform: rotate(45deg);
    transform-origin: center;
  }

  @include desktop-medium {
    right: -2.6vw;
  }
  @include tablet {
    right: -55px;
  }

  @include mobile-wide {
    right: 10px;
    &::after {
      display: block;
    }
  }

  @include mobile {
    right: 0;
  }
}

.header__search-input {
  box-sizing: border-box;
  padding: 16px 70px 15px 40px;
  border-radius: 100px;
  border: 1px solid $color-gray;
  width: 100%;
  background: transparent;
  font-size: 30px;
  line-height: 30px;
  color: $color-white;
  max-height: 73px;
  outline: none;

  &:focus {
    outline: none;
  }

  @include desktop-medium {
    padding: 0.8vw 3.6vw 0.7vw 2.1vw;
    border-radius: 5.2vw;
    font-size: 1.2em;
    line-height: 1;
    max-height: 3.8vw;
  }

  @include tablet {
    padding: 11px 45px 9px 15px;
    border-radius: 100px;
    font-size: 25px;
    line-height: 25px;
    max-height: 73px;
  }
}

.header__search-button {
  position: absolute;
  right: 40px;
  top: 25px;
  width: 25px;
  height: 25px;
  background: transparent;
  border: none;
  display: none;

  svg {
    fill: $color-white;
    stroke: $color-white;
    transition: all 0.3s ease;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  &:hover {
    svg {
      fill: $color-orange;
      stroke: $color-orange;
    }
  }

  @include desktop-medium {
    right: 2.1vw;
    top: 1.3vw;
    width: 1.3vw;
    height: 1.3vw;
  }

  @include tablet {
    top: 14px;
    right: 20px;
    width: 25px;
    height: 25px;
  }
}

.header__logo-link {
  width: 222px;
  height: 83px;
  position: absolute;
  right: 39%;
  bottom: -10px;
  padding-right: 222px;
  max-width: 222px;

  &::after {
    position: absolute;
    bottom: 0;
    right: -120%;
    content: "";
    width: 120%;
    height: 76px;
    z-index: 300;
  }

  @include desktop-medium {
    width: 11.6vw;
    height: 4.3vw;
    bottom: -0.5vw;
    padding-right: 11.6vw;
    max-width: 11.6vw;
    right: 20%;

    &::after {
      height: 4vw;
    }
  }

  @include desktop-small {
    right: 10%;
    bottom: 0;
  }

  @include tablet {
    display: block;
    left: 80px;
    width: calc(100% - 80px);
    max-width: calc(100% - 80px);
    padding-right: 0;
    bottom: 0;
    right: auto;
    height: 25px;

    &::after {
      right: -50%;
      width: 50%;
      height: 76px;
    }
  }

  @include mobile-wide {
    width: 145px;
    height: 55px;
    left: calc(50% - 72px);

    &::after {
      bottom: -15px;
      left: calc(72px - 50vw + 41px + 5px);
      width: calc(100vw - 36px - 51px - 8px);
      height: 55px;
    }
  }
}

.header__logo {
  position: absolute;
  bottom: 0;
  left: 0;

  @include tablet {
    bottom: 2px;
    width: 100%;
  }

  @include mobile-wide {
    bottom: -12px;
  }
}

.header__center {
  justify-content: flex-end;
  display: flex;
  align-content: center;
  width: 40%;
  padding-top: 5px;
  margin-right: auto;

  p {
    margin: 0;
  }

  @include desktop-medium {
    padding-top: 0.2vw;
    margin-right: 0.2vw;
    width: 50%;
  }

  @include desktop-small {
    width: 55%;
  }

  @include tablet {
    margin-right: 0;
    padding-top: 0;
    margin-left: 5vw;
    width: 53vw;
    justify-content: space-around;
  }

  @include mobile-wide {
    display: none;
  }
}

.header__right {
  width: 16.4%;
  display: flex;
  justify-content: space-between;
  align-content: center;

  @include tablet {
    width: 17%;
    min-width: 16.3%;
  }

  @include mobile-wide {
    display: none;
  }
}

.header__search--in-menu {
  width: 87.3%;
  margin-top: 80px;
  margin-left: 0;

  @include desktop-medium {
    margin-top: 4.1vw;
  }

  @include tablet {
    margin-top: 80px;
  }

  .header__search-form {
    position: static;
    opacity: 1;
  }

  .header__search-input {
    padding-top: 25px;
    padding-bottom: 20px;
    font-family: $font-bebasneue;
    font-size: 25px;
    line-height: 27px;
    color: #ffffff;
    background-color: $color-gray-blue-input;
    transition: 0.3s all ease;

    &::placeholder {
      font-family: $font-bebasneue;
      font-size: 25px;
      line-height: 27px;
      color: #ffffff;
    }

    @include desktop-medium {
      padding-top: 1.3vw;
      padding-bottom: 1vw;
      font-size: 1.3vw;
      line-height: 1.08;

      &::placeholder {
        font-size: 1em;
        line-height: 1.08;
      }
    }

    @include tablet {
      padding: 18px 70px 18px 40px;
      font-size: 25px;
      line-height: 27px;

      &::placeholder {
        font-size: 25px;
        line-height: 27px;
      }
    }
  }

  .header__search-button {
    display: block;
    // top: 50%;
    // transform: translateY(-50%);
    transition: 0.3s all ease;
    outline: none;
  }

  @include mobile-wide {
    display: none;
  }

  .header__search-input:hover,
  .header__search-input:hover + button {
    opacity: 0.7;
  }
}

//    
.menu-opened {
  .header__logo-link {
    display: none;
  }

  .header__search-link {
    @include mobile-wide {
      width: 25px;
    }
  }

  @include mobile-wide {
    .header__search-input {
      background-color: $color-gray-blue-input;
    }
  }
}
