.backAndSort {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include min-width(320px) {
    flex-direction: column;
    align-items: flex-start;
    padding: 0;
    padding-bottom: 36px;
  }
  @include min-width(992px) {
    flex-direction: row;
    padding: 40px 20px 40px;
  }
  .backAndSort__link {
    color: white;
    text-decoration: none;

    .backLink {
      display: flex;
      align-items: center;

      @include min-width(320px) {
        padding-bottom: 20px;
      }

      .backLink__icon {
        @include min-width(320px) {
          width: 7px;
          height: 12px;
          position: relative;
          top: 3px;
          margin-bottom: 3px;
        }
        @include min-width(992px) {
          width: 13px;
          height: 30px;
          margin-bottom: 0px;
        }
      }

      .backLink__title {
        font-family: $font-merel;

        @include min-width(320px) {
          font-size: 18px;
          padding-left: 13px;
          text-transform: none;
        }

        @include min-width(992px) {
          text-transform: uppercase;
          font-size: 25px;
          padding-bottom: 2px;
          padding-left: 20px;
          width: 313px;
        }
      }
    }
  }
}

.sort {
  display: flex;
  align-items: center;
  margin: 0 auto;

  @include min-width(620px) {
    margin: 0;
  }

  .sort__title {
    font-family: $font-merel;
    font-size: 20px;
    line-height: 23px;

    @include min-width(320px) {
      display: none;
    }
  }
}

.sort__btns {
  display: flex;

  .sort__table {
    display: flex;
    border: 1px solid #0075d1;
    box-sizing: border-box;
    border: 1px solid #0075d1;
    box-sizing: border-box;
    border-radius: 100px 0px 0px 100px;
    background: transparent;
    color: #0075d1;
    fill: #0075d1;
    padding: 13px 20px 8px 30px;
    background: #31313b;

    font-size: 20px;
    font-family: $font-bebasneue;
    text-transform: uppercase;

    position: relative;
    left: 15px;

    @include min-width(320px) {
      font-size: 18px;
      padding: 13px 19px 10px 20px;
      position: static;
    }

    .sort__table_title {
      @include min-width(320px) {
        padding-bottom: 3px;
        padding-top: 2px;
        padding-left: 9px;
        display: block;
      }
      @include min-width(992px) {
        // padding-top: 2px;
      }

    }
    .sort__table_icon {
      @include min-width(320px) {
        width: 38px;
        height: 21px;
      }
      @include min-width(992px) {
        width: 43px;
        height: 24px;
      }
    }
  }
  .sort__list {
    display: flex;
    box-sizing: border-box;
    border: 1px solid #c4c4c4;
    box-sizing: border-box;
    border-radius: 0px 100px 100px 0px;
    background: transparent;
    color: #e5e5e5;
    fill: #e5e5e5;
    padding: 13px 20px 10px 30px;

    font-size: 20px;
    line-height: 20px;
    text-align: center;
    font-family: $font-bebasneue;
    text-transform: uppercase;

    @include min-width(320px) {
      font-size: 18px;
      padding: 13px 19px 10px 20px;
    }
    .sort__list_title {
      @include min-width(320px) {
        padding-bottom: 3px;
        padding-left: 9px;
        display: flex;
        padding-top: 2px;
      }
      @include min-width(992px) {
        // padding-top: 0px;
      }

    }
    .sort__list_icon {
      @include min-width(320px) {
        width: 38px;
        height: 21px;
      }
      @include min-width(992px) {
        width: 43px;
        height: 24px;
      }
    }
  }
  .sort__list_active {
    border: 1px solid #0075d1;
    color: #0075d1;
    fill: #0075d1;
  }
  .sort__tb_un-active {
    border: 1px solid #e5e5e5;
    color: #e5e5e5;
    fill: #e5e5e5;
  }
}
