html {
  min-height: 100vh;
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Arial', 'Helvetica', sans-serif;
  position: relative;
  color: $color-white;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

// scroll-bar
::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  background: #464654;
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(270deg, #4694d2 0%, #0075d1 100%);
  border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover {
  background: #0075d1;
}
// scroll-bar

*,
*::after,
*::before {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

.hidden-desktop {
  display: none;

  @include mobile-wide {
    display: block;
  }
}

.hidden-mobile {
  display: block;

  @include mobile-wide {
    display: none;
  }
}

button {
  cursor: pointer;
  outline: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  &--advantages {
    overflow: hidden;
  }

  @include mobile-wide {
    min-height: calc(100vh - 2px);
  }
}

.main {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;

  .about-us-page & {
    position: static;
  }
}

.main--safari {
  width: 100%;
  height: calc(100vh - 100px);

  @include desktop-medium {
    height: calc(100vh - 5.2vw);
  }

  @include tablet {
    height: calc(100vh - 68px);
  }

  @include mobile-wide {
    height: auto;
  }
}

.main--not-safari {
  flex-grow: 1;
  height: auto;
}

//    
.popup-open {
  overflow: hidden;
  position: fixed !important;
  left: 0;
  right: 0;
  height: 100vh;
  padding-right: 20px;
}

.menu-opened {
  @include mobile-wide {
    overflow: hidden;
    position: fixed !important;
    left: 0;
    right: 0;
    height: 100vh;
  }
}
a {
  text-decoration: none;
}

.desk-norm {
  h1,
  h2,
  h3,
  h4,
  p {
    margin: 0;
  }
  ul {
    padding: 0;
  }
  ol {
    list-style-type: none;
  }
  .container {
    @include tablet {
      margin-top: 16px;
    }

    @include mobile {
      @include container($mobile-width, 0px 15px);
    }
  }
}
