.dealer-card {
  position: absolute;
  width: 18px;
  height: 18px;
  border-radius: 50%;

  @include desktop-medium {
    width: 0.9vw;
    height: 0.9vw;
  }
}

.dealer-card--tve {
  left: 30%;
  top: 29%;
}

.dealer-card--yan {
  left: 25%;
  bottom: 22%;
}

.dealer-card--sa {
  left: 5%;
  bottom: 37%;
}

.dealer-card--mag {
  left: 53%;
  top: 12%;
}

.dealer-card--kha {
  left: 10%;
  bottom: 10%;
}

.dealer-card .dealer-card__pin {
  z-index: 30000000;
}

.dealer-card--closed {
  .dealer-card__pin {
    background: radial-gradient(22.92% 22.92% at 50% 50%, rgba(255, 255, 255, 0.5) 0%, $color-blue 100%), linear-gradient(180deg, rgba(255, 255, 255, 0.6) -131.25%, $color-blue 100%), $color-blue;
    z-index: 200;
    top: 0;
    left: 0;
    width: 18px;
    height: 18px;
  }

  .dealer-card__wrapper {
    display: none;
    opacity: 0;
  }

  .dealer-card__pin svg {
    display: none;
  }

  @include desktop-medium {
    .dealer-card__pin {
      width: 0.9vw;
      height: 0.9vw;
    }
  }
}

.dealer-card__pin {
  position: absolute;
  width: 26px;
  height: 26px;
  left: -4px;
  top: -4px;
  background: $color-blue;
  cursor: pointer;
  box-shadow: 0 2px 1px rgba(0, 17, 31, 0.5);
  border-radius: 50%;
  transition: 0.3s all ease;

  svg {
    fill: $color-white;
    position: absolute;
    left: 8px;
    top: 8px;
    width: 10px;
    height: 10px;
  }

  &:hover {
    width: 26px;
    height: 26px;
    left: -4px;
    top: -4px;
  }

  @include desktop-medium {
    position: absolute;
    width: 1.3vw;
    height: 1.3vw;
    left: -0.2vw;
    top: -0.2vw;

    svg {
      fill: $color-white;
      position: absolute;
      left: 0.4vw;
      top: 0.4vw;
      width: 0.5vw;
      height: 0.5vw;
    }

    &:hover {
      width: 1.3vw;
      height: 1.3vw;
      left: -0.2vw;
      top: -0.2vw;
    }
  }
}

.dealer-card__wrapper {
  display: none;
  position: absolute;
  right: 8px;
  top: 8px;
  z-index: 999999;
  width: 243px;
  // height: 230px;
  background: #3b3b47;
  padding: 36px 18px;
  padding-bottom: 13px;
  animation: opacityChange 0.3s ease;

  transition: 0.3s opacity ease;
  box-shadow: 0 2px 1px rgba(0, 17, 31, 0.5);

  .dealer-card--display-block & {
    display: block;
  }

  @include desktop-medium {
    right: 0.4vw;
    top: 0.4vw;
    width: 12.7vw;
    min-width: 180px;
    // height: 12.1vw;
    min-height: 175px;
    padding: 1.8vw 0.9vw;
    padding-bottom: 0.6vw;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .dealer-card--display-block & {
      display: flex;
    }
  }
}

.dealer-card__heading {
  margin: 0;
  margin-bottom: 17px;
  font-size: 25px;
  font-weight: normal;
  line-height: 25px;
  text-transform: uppercase;
  color: $color-white;
  font-family: $font-bebasneue;

  @include desktop-medium {
    margin-bottom: 0.8vw;
    font-size: 18px;
    line-height: 1;
  }
}

.dealer-card__deskription {
  font-family: $font-merel;
  font-size: 12px;
  line-height: 14px;
  color: $color-gray;
  margin-top: 10px;
  margin-bottom: 24px;
  padding-left: 2px;

  @include desktop-medium {
    line-height: 1.16;
    margin-top: 0.5vw;
    margin-bottom: 1.2vw;
  }
}

.dealer-card__site {
  font-family: $font-merel;
  font-size: 17px;
  line-height: 20px;
  color: $color-blue-map-letters;
  text-decoration: none;
  display: inline-block;
  margin-bottom: 10px;
  padding-left: 2px;
  padding-right: 30px;
  position: relative;
  transition: 0.3s color ease;

  svg {
    position: absolute;
    right: 0;
    top: 4px;
    width: 19px;
    height: 15px;
    transition: 0.3s all ease;
    fill: $color-light-gray;
  }

  &:hover {
    color: $color-orange;

    svg {
      fill: $color-orange;
    }
  }

  @include desktop-medium {
    font-size: 15px;
    line-height: 1.176;
    margin-bottom: 0.5vw;
    padding-right: 15px;

    svg {
      left: auto;
      right: 0;
      width: 0.1vw;
      height: 0.7vw;
      min-width: 10px;
      min-height: 8px;
    }
  }
}

.dealer-card__phone {
  font-family: $font-merel;
  font-size: 20px;
  line-height: 23px;
  color: $color-white;
  text-decoration: none;
  display: inline-block;
  padding-left: 2px;
  transition: 0.3s color ease;

  &:hover {
    color: $color-orange;
  }

  @include desktop-medium {
    font-size: 15px;
    line-height: 1.2;
  }
}
