.webstudio {
  display: flex;
  justify-content: space-between;
}

.webstudio__deskription {
  display: flex;
  flex-direction: column;
  padding-top: 5px;
  font-family: $font-opensans;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;

  @include desktop-medium {
    padding-top: 0.2vw;
    line-height: 1.33;
  }

  @include tablet {
    padding-top: 5px;
    font-size: 12px;
    line-height: 16px;
  }
}

.webstudio__p {
  margin: 0;
  color: $color-gray-webstudio;
}

.webstudio__text-link {
  margin: 0;
  cursor: pointer;
  text-decoration: none;
  color: $color-white;
  transition: 0.3s all ease;

  &:hover {
    color: $color-orange;
  }
}

.webstudio__link {
  cursor: pointer;

  svg {
    transition: 0.3s all ease;
    opacity: 1;
    width: 30px;
    height: 39px;
  }

  &:hover {
    svg {
      opacity: 0.7;
    }
  }

  @include desktop-medium {
    svg {
      width: 1.5vw;
      height: 2vw;
    }
  }

  @include tablet {
    svg {
      width: 30px;
      height: 39px;
    }
  }
}
