            padding-left: 0px;
            padding-top: 20px;
            padding-bottom: 30px;
          }
          @include min-width(992px) {
            padding-left: 50px;
            padding-top: 0px;
            padding-bottom: 0px;
          }
        }
      }
      .partners__title {
        @include min-width(320px) {
          font-size: 24px;
        }
        @include min-width(992px) {
          font-size: get-vw(40px);
        }
      }
    }

    .switch {
