      margin: 0 auto;
      position: relative;
      // max-width: 220px;
      padding: 15px 44px 14px 42px;
      margin: 0 auto;
      margin-top: 30px;
      font-size: 1.25em;
      line-height: 20px;
      text-align: center;
      border: 1px solid $color-blue;
      background: transparent;
      border-radius: 100px;
      color: $color-white;
      transition: all $style-change-duration $style-change-function;
      outline: none;
      font-family: $font-bebasneue;

      a {
        color: $color-white;
      }

      @include desktop-medium {
        // padding: 0.7vw 2.3vw 0.7vw 2.2vw;
