      font-size: 2.5em;
      line-height: 43px;
      text-decoration: none;
      text-transform: uppercase;
      color: $color-blue;
      border-bottom: 1px dashed $color-blue;
      transition: all $style-change-duration $style-change-function;

      @include desktop-medium {
        font-size: 1.004em;
      }

      @include tablet {
        // font-size: 24px;
        line-height: 24px;
      }
      @include min-width(992px) {
        font-size: 50px;
      }
    }

    & .catalog__link--active {
      color: $color-white;
      border: none;
    }

