  .catalog__pagination {
    display: flex;
    // justify-content: center;
    padding-left: 30px;

    &.swiper-pagination.swiper-pagination-bullets {
      position: relative;
      left: 12px;
      bottom: 0;
    }

    & span {
      margin: 0;
      width: 81px;
      height: 7px;
      border-radius: 25px;
      background-color: $color-gray;
      opacity: 1;
      outline: none;
    }

    &.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
      margin-right: 9px;

      &:last-of-type {
        margin-right: 0;
      }
    }

    & .swiper-pagination-bullet-active {
      background: $color-gradient-blue;
    }

    @include tablet {
      display: none;
    }
  }

