.slider-button {
  @include button-reset;
  display: none;
  position: absolute;
  right: -43px;
  top: 123px;
  width: 23px;
  height: 48px;
  cursor: pointer;
  outline: none;

  transition: opacity $style-change-duration $style-change-function;

  &:hover {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.8;
  }

  &--prev {
    top: 183px;
  }

  @include tablet {
    top: 10px;
    width: 11px;
    height: 24px;

    &--prev {
      top: 35px;
    }
  }

  @include mobile-wide {
    display: none;
  }
}

.slider-button--visible {
  display: block;

  @include mobile-wide {
    display: none;
  }
}

.slider-button--disabled {
  cursor: default;

  &:hover {
    opacity: 1;
  }
}

.slider-button--disabled .slider-button__icon {
  stroke: #666666;
}

.slider-button__icon {
  width: 100%;
  height: 100%;
  fill: none;
  stroke-width: 2px;
  stroke: $color-blue;
}
