.privat-policy {
  .privat-policy__container {
    padding: 0;

    .privat-policy__title {
      @include min-width(320px) {
        padding-top: 54px;
        font-family: $font-merel;
        font-family: $font-bebasneue;
        font-style: normal;
        font-weight: 100;
        font-size: 24px;
        line-height: 107.1%;
        text-transform: uppercase;
        padding-left: 15px;
      }
      @include min-width(768px) {
        padding-left: 40px;
      }
      @include min-width(992px) {
        padding-left: 80px;
        font-size: 50px;
      }
    }

    .privat-policy__container_content {
      @include min-width(320px) {
        margin: 0px 15px;
      }
      @include min-width(768px) {
        margin: 0px 100px;
      }
      @include min-width(992px) {
        margin: 0px 148px;
      }

      .privat-policy__content {
        @include min-width(320px) {
          max-width: 1194px;
          margin: 0 auto;
          padding-top: 10px;
          padding-bottom: 30px;
        }
        @include min-width(992px) {
          padding-top: 60px;
          padding-bottom: 60px;
        }

        .privat-policy__terms,
        .privat-policy__provisions {
          & > h2 {
            @include min-width(320px) {
              font-family: $font-merel;
              font-weight: 900;
              font-size: 17px;
              line-height: 35px;
              color: #ed8528;
            }
            @include min-width(992px) {
              font-size: 30px;
            }
          }

          & > p {
            @include min-width(320px) {
              font-family: $font-merel;
              font-size: 14px;
              color: #e6e6e6;
              padding-top: 16px;
              line-height: 16px;
            }
            @include min-width(992px) {
              font-size: 20px;
              padding-top: 35px;
              line-height: 23px;
            }
          }
          :nth-child(2) {
            @include min-width(320px) {
              padding-top: 3px;
            }
            @include min-width(992px) {
              padding-top: 13px;
            }
          }
        }
        .privat-policy__provisions {
          @include min-width(320px) {
            padding-top: 17px;
          }
          @include min-width(992px) {
            padding-top: 25px;
          }
        }
      }
    }
  }
}
