.tabs {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  max-width: 100%;

  @include mobile-wide {
    padding-top: 48px;
  }
}

.tabs__list {
  flex-grow: 1;
  display: flex;
  min-height: 37px;
  justify-content: flex-start;
  width: 50%;
  font-family: $font-bebasneue;
  text-transform: uppercase;

  @include tablet {
    padding: 0;
  }

  @include mobile-wide {
    min-height: 0;
    padding: 0 15px;
    overflow-x: auto;
  }
}

.tabs__tab {
  display: flex;
  margin-right: 30px;
  text-decoration: none;

  @include desktop-medium {
    margin-right: 1.5vw;
  }

  @include tablet {
    margin-right: 25px;
  }

  @include mobile-wide {
    flex-shrink: 0;
  }

  &--active {
    cursor: default;
  }

  &--active .tabs__tab-title {
    color: $color-white;
    border-bottom: none;
  }
}

.tabs__tab-title {
  margin: 0;
  font-size: 40px;
  line-height: 1.1;
  font-weight: 400;
  color: $color-blue;
  border-bottom: 1px dashed $color-blue;
  margin-bottom: 2px;
  pointer-events: none;
  transition: all $style-change-duration $style-change-function;
  align-self: center;

  @include desktop-medium {
    font-size: 1em;
    line-height: 1.1;
  }

  @include tablet {
    font-size: 24px;
    line-height: 1.1;
    padding-bottom: 2px;
  }

  @include mobile-wide {
    font-size: 24px;
    line-height: 1.1;
    padding-bottom: 2px;
  }
}

.tabs__link-more {
  align-self: flex-start;
  flex-shrink: 0;
  width: 220px;
  text-align: center;
  margin-left: auto;
  margin-top: 6px;
  cursor: pointer;

  &--display-none {
    display: none;
  }

  @media (min-height: 1001px) {
    align-self: flex-start;
  }

  @include desktop-medium {
    width: 11.5vw;
    margin-top: 0.3vw;
  }

  @include tablet {
    margin-top: 3px;
    margin-right: 0;
  }

  @include mobile-wide {
    order: 3;
    width: 200px;
    margin: 0 auto;
    margin-top: 3px;
  }
}


.tabs__panel {
  display: none;
  width: 100%;

  &--active {
    display: block;
  }
}
