.footer {
  width: 100%;
  background-color: $color-mobile-background;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 100%;
    height: 1px;
    background-color: $color-darknes-gray;
  }
}

.footer__wrapper {
  max-width: 1900px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-content: center;
  padding: 33px 88px 26px 90px;

  @include desktop-medium {
    max-width: 100%;
    padding: 1.7vw 4.6vw 1.3vw 4.7vw;
  }

  @include desktop-small {
    padding: 1.7vw 2.6vw 1.3vw 2.7vw;
  }

  @include tablet {
    padding: 14px 20px;
  }

  @include mobile-wide {
    padding: 19px 15px 24px 15px;
    flex-direction: column;
    align-content: center;
  }
}

.footer__center {
  width: 779px;
  display: flex;
  margin-left: 0;
  justify-content: space-between;

  @include desktop-medium {
    width: 41vw;
    margin-left: 0;
  }

  @include desktop-small {
    width: calc(100% - 150px - 3%);
    max-width: 46%;
  }

  @include max-width(1400px) {
    max-width: 50%;
  }

  @media (max-width: 1200px) {
    width: calc(100% - 150px - 3%);
    max-width: 79.3%;
  }

  @include tablet {
    width: calc(100% - 150px - 3%);
    max-width: 79.3%;
  }

  @include mobile-wide {
    width: 100%;
    max-width: 100%;
    flex-direction: column;
  }
}

.footer__right {
  padding-top: 38px;
  margin-right: 3px;
  width: 150px;

  @include desktop-medium {
    padding-top: 2vw;
    margin-right: 0.1vw;
  }

  @include tablet {
    padding-top: 10px;
    width: 150px;
  }

  @include mobile-wide {
    align-self: flex-start;
    padding-top: 36px;
  }

  @include max-width(1380px) {
    width: 170px;
  }
  @include max-width(1200px) {
    width: 150px;
  }
}

.footer__logo-link {
  display: none;

  @include mobile-wide {
    width: 145px;
    height: 55px;
    left: calc(50% - 72px);
    display: block;
    position: relative;
  }
}

.footer__logo {
  position: absolute;
  bottom: 5px;
  left: 0;
  width: 100%;
}

.footer__copyright {
  display: none;

  @include mobile-wide {
    padding-top: 28px;
    font-family: $font-merel;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    display: block;
    color: $color-gray;
    margin-left: 4px;
  }
}
