.desktop-contacts {
  .desktop-contacts__container {
    font-family: $font-merel;

    @include min-width(320px) {
      width: 100%;
      margin: 0;
      padding: 0;
      margin: 30px 0px;
    }
    @include min-width(500px) {
      padding: 0px 15px;
    }
    @include min-width(992px) {
      margin: get-vw(56px) 50px 0 auto;
      padding-top: get-vw(56px);
      max-width: get-vw(1648px);
      padding: 0px 0px;
    }
    .contacts__toggle {
      @include min-width(320px) {
        max-width: 267px;
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding-left: 15px;
      }
      @include min-width(992px) {
        max-width: get-vw(560px);
        padding-left: 0px;
      }
      .contacts__toggle_contacts,
      .contacts__toggle_representations {
        @include min-width(320px) {
          font-family: $font-bebasneue;
          font-style: normal;
          font-weight: 100;
          font-size: 24px;
          line-height: 107.1%;
          text-transform: uppercase;
          background: transparent;
          color: white;
          border: none;
          padding: 0;
        }
        @include min-width(992px) {
          font-size: get-vw(50px);
        }
      }
      .contacts__toggle-active-btn {
        border-bottom: 1px dashed #0075d1;
        color: #0075d1;
      }
    }
  }
}

