.hidden-992 {
  @include max-width(992px) {
    display: none !important;
  }
}
.catalog__filter {
  display: flex;
  background-color: #474754;
  padding: get-vw(15px) get-vw(40px);
  font-size: get-vw(20px);

  // @include max-width(1735px) {
  //   font-size: 15px;
  // }
  // @include max-width(1900px) {
  //   font-size: 20px;
  // }
  // @include max-width(1333px) {
  //   font-size: 12px;
  // }
  @include max-width(1084px) {
    padding: get-vw(15px) get-vw(20px);
  }

  #count {
    padding: 0px get-vw(40px);

    // @include max-width(1333px) {
    //   padding: 0px 10px;
    // }
  }

  .catalog__filter-confBlock {
    line-height: 100%;
    display: flex;
    align-items: center;

    .catalog__filter-title {
      @include reset;
      padding-right: get-vw(6px);
      font-family: $font-merel;
    }
    .customselect {
      position: relative;
      font-size: get-vw(14px);
      display: inline-block;
      font-family: $font-merel;
      font-size: get-vw(20px);
    }

    .dropdown-type,
    .dropdown-quantity,
    .dropdown-diametr,
    .dropdown-stretch {
      color: white;
      text-align: left;
      border: none;
      font-family: $font-merel;
      background-color: #494957;
      padding: get-vw(14px) get-vw(40px) get-vw(16px) get-vw(10px);
      box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
      min-width: 116px;
      font-size: get-vw(20px);

      // @include max-width(1735px) {
      //   font-size: 15px;
      // }
      // @include max-width(1900px) {
      //   font-size: 20px;
      // }
      // @include max-width(1333px) {
      //   font-size: 12px;
      // }
    }
    .arrow__down--type,
    .arrow__up--type,
    .reset--type,
    .arrow__down--quantity,
    .arrow__up--quantity,
    .reset--quantity,
    .arrow__down--diametr,
    .arrow__up--diametr,
    .reset--diametr,
    .arrow__down--stretch,
    .arrow__up--stretch,
    .reset--stretch {
      position: absolute;
      right: 1px;
      border: none;
      background-color: #494957;
      height: get-vw(17px);
      width: get-vw(17px);
      height: 17px;
      width: 17px;

      @include min-width(1280px) {
        width: get-vw(17px);
        height: get-vw(17px);
      }
      // margin: get-vw(19px) get-vw(10px);
      padding: 0;
      // @include min-width(1200px) {
        bottom: get-vw(17px);
        right: get-vw(17px);
        margin: 0;
      // }
      @include min-width(1600px) {
        // right: get-vw(10px);
      }
      svg {
        width: get-vw(17px);
        height: get-vw(17px);

        @include max-width(992px) {
          width: 17px;
          height: 17px;
        }
      }
    }
    .hidden {
      display: none;
    }
    .hidden1 {
      display: none;
    }
    .customselect li {
      padding: 2px;
      cursor: pointer;
    }
    .customselect ul {
      list-style: none;
      margin: 0;
      padding: 0;
      display: none;
      position: absolute;
      z-index: 1000;
      background: #494957;
      width: 100%;
      border-top: 1px solid #959595;
      box-sizing: border-box;
      box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
    }
    .customselect.open ul {
      display: block;
    }
    .customselect.open ul li {
      padding-top: 10px;
      padding-left: 10px;

      @include max-width(1735px) {
        font-size: 15px;
      }
      @include max-width(1735px) {
        font-size: 16px;
      }
    }
    .customselect.open ul li:last-child {
      padding-bottom: 10px;
    }
    .catalog__filter_btn-apply {
      @include min-width(320px) {
        font-family: $font-bebasneue;
        text-transform: uppercase;
        background: #0075d1;
        border-radius: 100px;
        padding: get-vw(17px) get-vw(58px);
        margin: 0 get-vw(62px);
        cursor: pointer;
      }
      @include min-width(992px) {
        margin: 0 10px;
        padding: get-vw(18px) get-vw(30px) get-vw(16px);
      }
      @include min-width(1225px) {
        margin: 0 get-vw(35px);
        font-size: get-vw(22px);
        padding: get-vw(18px) get-vw(50px) get-vw(16px);
      }
      @include min-width(1600px) {
        margin: 0 get-vw(62px);
        padding: get-vw(18px) get-vw(58px) get-vw(16px);
        font-size: get-vw(20px);
      }
    }
    .catalog__filter_btn-reset {
      text-transform: none;
      font-family: $font-merel;
      border-bottom: 1px dashed;
      height: get-vw(27px);
      cursor: pointer;
    }
    .diametr__title {
      width: get-vw(115px);
    }
    .stretch__title {
      width: get-vw(184px);
    }
  }
}

.catalog__mobile-filter {
  display: flex;

  @include min-width(990px) {
    display: none;
    padding-bottom: 30px;
  }
  @include min-width(320px) {
    padding-bottom: 5px;
  }

  .mobile-filter__btn {
    margin-left: 15px;
    background-color: #474754;
    border: none;
    height: 46px;
    width: 46px;
  }
}

.dropdown-type {
  width: get-vw(315px);

  // @include max-width(1735px) {
  //   width: 260px;
  // }
  // @include max-width(1900px) {
  //   width: 315px;
  // }
  // @include max-width(1333px) {
  //   width: 210px;
  // }
}
.dropdown-quantity {
  width: get-vw(100px);
}
.dropdown-diametr {
  width: get-vw(142px);

  @include max-width(1735px) {
    width: get-vw(120px);
  }
}
.dropdown-stretch {
  width: get-vw(120px);

  // @include max-width(1735px) {
  //   width: 100px;
  // }
}

#quantity {
  margin-right: get-vw(30px);
}
