.desktop-selected-news {
  .news__container {
    max-width: 1680px;
    padding: 0;

    .news__title {
      @include min-width(320px) {
        padding-top: 30px;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 35px;
        font-family: $font-merel;
        text-transform: none;

        padding-left: 15px;
        padding-right: 15px;

        line-height: 21px;
      }
      @include min-width(768px) {
        padding-top: 54px;
        padding-left: 80px;
        font-size: 30px;
        padding-right: 80px;
        line-height: 35px;
      }
    }
    .news__title_date {
      @include min-width(320px) {
        font-family: $font-merel;
        line-height: 23px;
        color: #ed8528;
        padding-left: 15px;
        padding-top: 10px;
        font-size: 12px;
      }
      @include min-width(768px) {
        padding-left: 80px;
        font-size: 20px;
      }
    }
    .main-news {
      // section 1
      @include min-width(320px) {
        padding: 0px 15px;
        font-family: $font-merel;
        font-size: 14px;
        line-height: 23px;
      }
      @include min-width(768px) {
        padding: 0px 50px 0px 50px;
        font-size: 20px;
      }
      @include min-width(992px) {
        padding: 0px 150px 0px 150px;
        font-size: 20px;
      }
      @include min-width(1400px) {
        padding: 0px 242px 0px 242px;
        font-size: 20px;
      }

      & > p {
        @include min-width(320px) {
          padding-top: 20px;
        }
      }
      .main-news__img_block {
        @include min-width(1200px) {
          position: absolute;
          left: get-vw(80px);
          // width: 90vw;
        }
        @include min-width(1600px) {
          position: static;
        }
        & > img {
          @include min-width(320px) {
            padding-top: 40px;
          }
          @include min-width(1200px) {
            width: 90vw;
          }
          @include min-width(1600px) {
          }
        }
        .main-news__img-desc {
          @include min-width(320px) {
            padding-top: 7px;
            color: #c4c4c4;
            font-size: 14px;
            bottom: 0;
          }
          @include min-width(768px) {
            font-size: 20px;
          }
          @include min-width(1200px) {
            margin-left: get-vw(50px);
          }
          @include min-width(1600px) {
            margin-left: 0;
          }
        }
      }
      .desktop-selected-news__space {
        height: 48vw;
        @include max-width(1200px) {
          display: none;
        }
        @include min-width(1600px) {
          display: none;
        }
      }
      .links__network {
        @include min-width(320px) {
          padding-top: 30px;
          color: #c4c4c4;
          font-size: 16px;
          line-height: 23px;
          display: flex;
        }
        @include min-width(768px) {
          font-size: 20px;
          padding-top: 62px;
        }

        & > a {
          margin-left: 20px;
        }
      }
    }
    .other-news {
      // section 2
      @include min-width(320px) {
        padding: 30px 15px 40px;
      }
      @include min-width(768px) {
        padding: 60px 91px 61px;
      }
      // @include min-width(1200px) {
      //   padding: 60px 91px 81px;
      // }
      // @include min-width(1600px) {
      //   padding: 60px 91px 81px;
      // }
      .other-news__title {
        @include min-width(320px) {
          font-family: $font-bebasneue;
          font-weight: normal;
          font-size: 24px;
          line-height: 107.1%;
        }
        @include min-width(768px) {
          font-size: 40px;
        }
      }
      .other-news__news-block {
        @include min-width(320px) {
          flex-direction: column;
          padding-top: 15px;
          display: flex;
          font-family: $font-merel;
        }
        @include min-width(768px) {
          justify-content: space-between;
          flex-direction: row;
          padding-top: 32px;
        }

        .other-news__item {
          @include min-width(320px) {
            max-width: 450px;
            padding-bottom: 15px;
          }
          @include min-width(768px) {
            margin-right: 10px;
          }
          @include min-width(1600px) {
            margin-right: 0px;
          }
          .other-news__item_title {
            @include min-width(320px) {
              padding: 23px 10px;
              text-transform: none;
              font-weight: 100;
              font-size: 14px;
              line-height: 23px;
              align-items: center;
              text-align: center;
              background: #2d2d36;
              box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
              border-radius: 10px 9px 9px 9px;
            }
            @include min-width(768px) {
              font-size: 18px;
              height: 160px;
            }
            @include min-width(992px) {
              height: 130px;
            }
            @include min-width(1600px) {
              font-size: 20px;
              height: auto;
            }
          }
          .other-news__item__footer {
            padding: 12px 20px 12px 20px;
            font-size: 17px;
            background: #24242b;
            border-radius: 0px 0px 9px 9px;
            display: flex;
            justify-content: space-between;

            .footer__link-more {
              color: #ed8528;
            }
            .footer__date {
              color: #c4c4c4;
            }
          }
        }
      }
    }
  }
}
