.about-company_drum {
  @include min-width(320px) {
    display: none;
    position: absolute;
    top: -12vw;
    left: 50%;
    right: 50%;
    transform: translate(-50%);
    z-index: 1;
    width: 50.84vw;
    height: 50.84vw;
  }
  @include min-width(992px) {
    display: block;
  }
}
.about-company_scope {
  @include min-width(320px) {
    width: 52.84vw;
    height: 52.84vw;
    background: rgba(81, 82, 92, 0.3);
    filter: blur(250px);
    position: absolute;
    top: -130px;
    right: 150px;
    z-index: 2;
    display: none;
  }
  @include min-width(992px) {
    display: block;
  }
}

// Mobile ==========================
.about-company_background_triangle {
  @include min-width(320px) {
    position: absolute;
    top: 1px;
    z-index: 3;
    width: 100vw;
    height: 487px; 
  }
  @include min-width(992px) {
    width: 59.7vw;
    height: 41.3vw;
  }
}
.about-company_content {
  @include min-width(320px) {
    position: absolute;
    top: 13px;
    left: 15px;
    z-index: 4;
    width: 90.63vw;
    height: 457px;
  }
  @include min-width(992px) {
    top: 120px; // change for parallax
    left: 60px;
    width: 0px;
    height: 0px;
  }

  .about-company_background_simple {
    background: linear-gradient(180.07deg, #3b3b47 41.16%, #31313b 99.91%);
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
  }
  .about-company_desc {
    @include min-width(320px) {
      position: absolute;
      top: 12px;
      bottom: 0vw;
      left: 10px;
      max-width: 84.38vw;
      font-family: $font-merel;
      font-size: 14px;
      line-height: 19px;
      padding-right: 0px;
    }
    @include min-width(391px) {
      font-size: 14px;
      line-height: 23px;
    }
    @include min-width(448px) {
      font-size: 15px;
      line-height: 24px;
    }
    @include min-width(472px) {
      font-size: 15px;
      line-height: 24px;
    }
    @include min-width(533px) {
      font-size: 16px;
      line-height: 24px;
    }
    @include min-width(613px) {
      font-size: 18px;
      line-height: 27px;
    }
    @include min-width(768px) {
      font-size: 20px;
      line-height: 30px;
    }

    @include min-width(992px) {
      max-width: 51.84vw;
      left: 55px;
      bottom: 4.1vw;
      top: auto;
      padding-right: 20px;
      font-size: 1.3vw;
      line-height: 1.84vw;
      width: get-vw(985px);
    }
    & > p {
      @include min-width(320px) {
        padding-top: 16px;
      }
      @include min-width(992px) {
        padding-top: 1.82vw;
      }
    }
    p:first-child {
      padding-top: 0px;
    }
  }
}
.about-company_title {
  @include min-width(320px) {
    font-family: $font-bebasneue;
    -webkit-text-stroke: 0.35px #c4c4c4;
    -webkit-text-fill-color: transparent;
    font-weight: normal;
    font-size: 48px;
    line-height: 107.1%;
    letter-spacing: 0.03em;
    display: block;
    max-width: 1376px;
    position: absolute;
    top: -60px;
    z-index: 5;
    padding-left: 15px;
  }
  @include min-width(992px) {
    -webkit-text-stroke: 2px white;
    font-size: 14.5vw;
    top: -4.36vw;
    right: get-vw(30px);
    padding-left: 0px;

    right: get-vw(-1480px);
  }
}
.about-company_strip {
  @include min-width(320px) {
    display: none;
  }
  @include min-width(768px) {
    display: block;
    width: 0px;
    height: 1px;
    background: white;
    position: absolute;
    z-index: 6;
    top: get-vw(210px);
    left: get-vw(290px);

    margin-left: 130px;
  }
  @include min-width(1700px) {
    margin-left: 0px;
  }
}
