.popup-call {
  display: none;

  &--open {
    position: fixed;
    z-index: 400;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    min-height: 100%;
    padding-bottom: 34px;
    background-color: rgba($color-dark-gray-background, 0.92);
  }

  @include laptop {
    padding-bottom: 0;
  }
}

.popup-call__container {
  position: relative;
  width: 598px;
  height: 449px;
  padding-top: 70px;
  padding-right: 100px;
  padding-left: 98px;
  padding-bottom: 58px;
  margin: auto;
  transform: translateY(-200%);
  transition: transform 0.5s ease-in-out;
  background-image: linear-gradient(180.11deg, #3b3b47 41.16%, #31313b 99.91%);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);

  .popup-call--success & {
    display: none;
  }

  @include desktop-medium {
    width: 31.47vw;
    height: 31.47vw;
    padding-top: 3.7vw;
    padding-right: 5.26vw;
    padding-left: 5.16vw;
    padding-bottom: 3.05vw;
  }

  @include tablet {
    width: 598px;
    height: 598px;
    padding-top: 70px;
    padding-right: 100px;
    padding-left: 98px;
    padding-bottom: 58px;
  }

  @include mobile-wide {
    height: auto;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: auto;
  }
}

.popup-call__title {
  @include reset;
  font-family: $font-bebasneue;
  font-weight: normal;
  font-size: 40px;
  line-height: 1;
  text-transform: uppercase;
  text-align: center;

  @include desktop-medium {
    font-size: 2.1vw;
  }

  @include tablet {
    font-size: 40px;
  }
}

.popup-call__form {
  padding-top: 44px;
  font-family: $font-merel;

  @include desktop-medium {
    padding-top: 2.3vw;
  }

  @include tablet {
    padding-top: 44px;
  }
}

.popup-call__input-wrap {
  position: relative;
  margin-bottom: 30px;

  @include desktop-medium {
    margin-bottom: 1.57vw;
  }

  @include tablet {
    margin-bottom: 30px;
  }
}

.popup-call__input-comments {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 1px;
  font-size: 14px;
  line-height: 16px;
  color: $color-orange;

  .popup-call__checkbox-label & {
    overflow: hidden;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 1px;
    background-color: $color-gray;
  }

  @include desktop-medium {
    font-size: 0.73vw;
    line-height: 1.142;
  }

  @include tablet {
    font-size: 14px;
    line-height: 16px;
  }
}

.popup-call__input-comment {
  height: 0;
  overflow: hidden;
}

.popup-call__input {
  @include button-reset;
  display: block;
  width: 100%;
  padding-bottom: 5px;
  font-family: $font-merel;
  font-size: 22px;
  line-height: 26px;
  color: #ffffff;
  outline: none;

  &::placeholder {
    color: $color-gray;
  }

  &.invalid ~ .popup-call__input-comments .popup-call__input-comment {
    height: auto;
  }

  &.invalid ~ .popup-call__input-comments::before {
    background-color: $color-orange;
    box-shadow: 0 0 3px 1px rgba($color-orange, 0.3);
  }

  &:hover + .popup-call__input-comments::before,
  &:focus + .popup-call__input-comments::before {
    background-color: $color-blue;
    box-shadow: 0 0 3px 1px rgba($color-blue, 0.3);
  }

  @include desktop-medium {
    padding-bottom: 0.26vw;
    font-size: 1.157vw;
    line-height: 1.18;
  }

  @include tablet {
    padding-bottom: 5px;
    font-size: 22px;
    line-height: 26px;
  }
}

.popup-call__time {
  @include reset;
  border: none;
  padding-left: 82px;
  padding-right: 82px;

  @include desktop-medium {
    padding-left: 4.31vw;
    padding-right: 4.31vw;
  }

  @include tablet {
    padding-left: 82px;
    padding-right: 82px;
  }

  @include mobile-wide {
    padding-left: 6vw;
    padding-right: 6vw;
  }
}

.popup-call__time-title {
  @include reset;
  padding-top: 10px;
  font-family: $font-merel;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  font-weight: normal;
  color: #ffffff;

  @include desktop-medium {
    padding-top: 0.52vw;
    font-size: 1.05vw;
    line-height: 1.15;
  }

  @include tablet {
    padding-top: 10px;
    font-size: 20px;
    line-height: 23px;
  }
}

.popup-call__timeStart,
.popup-call__timeEnd {
  position: relative;
  display: flex;
  align-items: flex-start;

  &::before {
    content: ":";
    display: block;
    position: absolute;
    left: 27px;
    top: -3px;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: 8px;
    right: 0;
    bottom: 0;
    border-bottom: 1px solid #959595;
  }

  @include desktop-medium {
    &::before {
      left: 1.42vw;
      top: -0.15vw;
    }

    &::after {
      left: 0.42vw;
    }
  }

  @include tablet {
    &::before {
      left: 27px;
      top: -3px;
    }

    &::after {
      left: 8px;
    }
  }

  @include mobile-wide {
    &::before {
      top: -5px;
    }
  }
}

.popup-call__timeStart {
  margin-right: 27px;

  @include desktop-medium {
    margin-right: 1.42vw;
  }

  @include tablet {
    margin-right: 27px;
  }
}

.popup-call__time-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 11px;
  padding-right: 14px;
  font-family: $font-merel;
  font-size: 20px;
  line-height: 23px;

  input::-webkit-inner-spin-button,
  input::-webkit-outer-spin-button {
    appearance: none;
    margin: 0;
  }

  input {
    -moz-appearance: textfield;
    width: 27px;
    height: 32px;
    padding: 0;
    padding-bottom: 10px;
    border: none;
    font-family: $font-merel;
    font-size: 20px;
    line-height: 23px;
    text-align: right;
    letter-spacing: 0.2px;
    color: #ffffff;
    background-color: transparent;

    &:focus {
      outline: none;
    }
  }

  input:nth-child(2) {
    margin-left: 4px;
    text-align: left;
  }

  @include desktop-medium {
    padding-top: 0.57vw;
    padding-right: 0.74vw;
    font-size: 1.05vw;
    line-height: 1.15;

    input {
      width: 1.42vw;
      height: 1.68vw;
      padding-bottom: 0.52vw;
      font-size: 1.05vw;
      line-height: 1.15;
    }

    input:nth-child(2) {
      margin-left: 0.21vw;
    }
  }

  @include tablet {
    padding-top: 11px;
    padding-right: 14px;
    font-size: 20px;
    line-height: 23px;

    input {
      width: 27px;
      height: 32px;
      padding-bottom: 10px;
      font-size: 20px;
      line-height: 23px;
    }

    input:nth-child(2) {
      margin-left: 4px;
    }
  }
}

.popup-call__checkbox {
  @include visually-hidden;
}

.popup-call__checkbox-label {
  position: relative;
  display: block;
  padding-left: 29px;
  margin-top: 40px;
  font-family: $font-merel;
  font-size: 16px;
  line-height: 19px;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 3px;
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 3px;
    border: 1px solid $color-blue;
    transition: opacity $style-change-duration $style-change-function;
  }

  &::after {
    content: none;
    position: absolute;
    display: block;
    left: 3px;
    top: 7px;
    width: 13px;
    height: 10px;
    background-image: url("../img/icon-tick.svg");
    background-size: contain;
  }

  &:hover::before {
    opacity: 0.6;
  }

  .popup-call__input-comments {
    &::before {
      content: none;
    }
  }

  @include desktop-medium {
    padding-left: 1.52vw;
    margin-top: 0.42vw;
    font-size: 0.84vw;
    line-height: 1.185;

    &::before {
      top: 0.15vw;
      width: 0.94vw;
      height: 0.94vw;
      border-radius: 3px;
    }

    &::after {
      left: 0.15vw;
      top: 0.36vw;
      width: 0.68vw;
      height: 0.52vw;
    }
  }

  @include tablet {
    padding-left: 29px;
    margin-top: 8px;
    font-size: 16px;
    line-height: 19px;

    &::before {
      top: 3px;
      width: 18px;
      height: 18px;
      border-radius: 3px;
    }

    &::after {
      left: 3px;
      top: 7px;
      width: 13px;
      height: 10px;
    }
  }
}

.popup-call__checkbox.invalid + .popup-call__checkbox-label .popup-call__input-comments {
  top: calc(100% + 7px);
  height: auto;

  @include desktop-medium {
    top: calc(100% + 0.36vw);
  }

  @include tablet {
    top: calc(100% + 7px);
  }
}

.popup-call__checkbox.invalid + .popup-call__checkbox-label::before {
  border-color: $color-orange;
}

.popup-call__checkbox:checked + .popup-call__checkbox-label::after {
  content: "";
}

.popup-call__button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 44px;
  width: 220px;
  border: 1px solid $color-blue;
  font-family: $font-bebasneue;
  font-size: 20px;
  line-height: 21px;
  text-align: center;

  @include hover-background-color($color-blue);

  @include desktop-medium {
    margin-top: 2.31vw;
    width: 11.57vw;
    font-size: 1.05vw;
    line-height: 1.05;
  }

  @include tablet {
    margin-top: 44px;
    width: 220px;
    font-size: 20px;
    line-height: 21px;
  }
}

.popup-call .menu-btn {
  display: none;
  position: absolute;
  top: 55px;
  right: 34px;
  padding-top: 8px;
  padding-bottom: 8px;
  transform: none;

  @include hover-opacity;

  @include desktop-medium {
    top: 2.8vw;
    right: 1.78vw;
    padding-top: 0.42vw;
    padding-bottom: 0.42vw;
  }

  @include tablet {
    top: 34px;
    right: 34px;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  @include mobile-wide {
    top: 27px;
    right: 10px;
  }
}

.popup-call--open .menu-btn {
  display: block;
  width: 39px;
  height: 36px;

  @include desktop-medium {
    width: 2vw;
    height: 2vw;
  }

  @include tablet {
    width: 39px;
    height: 36px;
  }
}

.popup-call .menu-btn::before,
.popup-call .menu-btn::after {
  width: 100%;
  height: 3px;
  border-radius: 5px;
  background-color: #ffffff;
}

.popup-call__success {
  position: relative;
  display: none;
  width: 792px;
  min-height: 457px;
  margin: auto;
  margin-top: 257px;
  overflow: hidden;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background-position: 190% -22px, 0 0;
  background-image: url("../img/background-popup@1x.png"), linear-gradient(180.06deg, #3b3b47 41.16%, #31313b 99.91%);

  &::before {
    content: "";
    display: block;
    position: absolute;
    z-index: 0;
    left: -23%;
    top: 0;
    right: 11.4%;
    bottom: 0;
    border-bottom-right-radius: 5px;
    transform: skew(-21deg, 0deg);
    box-shadow: 5px 0 10px rgba(0, 17, 31, 0.5);
    background-image: linear-gradient(180.06deg, #3b3b47 41.16%, #31313b 99.91%);
  }

  .popup-call--success & {
    display: flex;
    flex-direction: column;
  }

  @include desktop-medium {
    width: 41.68vw;
    min-height: 24.05vw;
    margin-top: 13.52vw;
  }

  @include tablet {
    width: 792px;
    min-height: 457px;
    margin-top: 257px;
  }

  @include mobile-wide {
    margin-top: auto;

    &::before {
      content: "";
      left: 0;
      right: 0;
      transform: none;
    }
  }
}

.popup-call__success-container {
  position: relative;
  margin: auto;
  margin-top: 138px;

  @include desktop-medium {
    margin-top: 7.26vw;
  }

  @include tablet {
    margin-top: 138px;
  }
}

.popup-call__success-title {
  padding-bottom: 20px;
  font-family: $font-bebasneue;
  font-size: 40px;
  line-height: 1;
  font-weight: normal;
  text-align: center;

  span {
    display: block;
    padding-bottom: 22px;
  }

  @include desktop-medium {
    padding-bottom: 1.05vw;
    font-size: 2.1vw;

    span {
      padding-bottom: 1.15vw;
    }
  }

  @include tablet {
    padding-bottom: 20px;
    font-size: 40px;

    span {
      padding-bottom: 22px;
    }
  }

  @include mobile-wide {
    font-size: 32px;
  }
}

.popup-call__slider-container {
  position: relative;
  width: 100%;
  margin-top: 30px;
  margin-bottom: 34px;
  padding-right: 10px;
  margin-left: 3px;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 6px;
    bottom: 0;
    height: 1px;
    background-color: #959595;
  }

  .noUi-horizontal {
    height: 1px;
    border: none;
    box-shadow: none;
    background-color: transparent;
  }

  .noUi-connects {
    background-color: transparent;
  }

  .noUi-connect {
    background: linear-gradient(270deg, #4694d2 0%, #0075d1 100%);
  }

  .noUi-horizontal .noUi-handle {
    top: -11px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    box-shadow: none;
    border: none;
    background-color: #0075d1;
    cursor: pointer;
  }

  .noUi-horizontal .noUi-handle:focus {
    outline: none;
  }

  .noUi-handle::before,
  .noUi-handle::after {
    content: none;
  }

  @include desktop-medium {
    margin-top: 1.57vw;
    margin-bottom: 1.78vw;
    padding-right: 0.52vw;
    margin-left: 0.15vw;

    &::before {
      right: 0.31vw;
    }

    .noUi-horizontal .noUi-handle {
      top: -0.57vw;
      width: 1.26vw;
      height: 1.26vw;
    }
  }

  @include tablet {
    margin-top: 30px;
    margin-bottom: 34px;
    padding-right: 10px;
    margin-left: 3px;

    &::before {
      right: 6px;
    }

    .noUi-horizontal .noUi-handle {
      top: -11px;
      width: 24px;
      height: 24px;
    }
  }
}

.slideInDown {
  transform: translateY(0);
}
