@keyframes animation_mainlinks {
  from {
    transform: translateY(-50%);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes animation_mainlinks-reverse {
  from {
    transform: translateY(0);
    opacity: 1;
  }

  to {
    transform: translateY(-50%);
    opacity: 0;
  }
}

@keyframes animation_mainbuttons {
  from {
    transform: translateX(50px);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes animation_mainbuttons-reverse {
  from {
    transform: translateX(0);
    opacity: 1;
  }

  to {
    transform: translateX(50px);
    opacity: 0;
  }
}

.news {
  position: static;
  color: $color-white;
}

.news__tabs {
  display: flex;
  justify-content: flex-start;
  width: 50%;
  font-family: $font-bebasneue;
  text-transform: uppercase;

  @include tablet {
    padding: 0 30px;
  }

  @include mobile-wide {
    padding: 0 15px;
  }
}

.news__header-link {
  margin-right: 30px;
  display: flex;

  @include desktop-medium {
    margin-right: 1.5vw;
  }

  @include mobile-wide {
    margin-right: 25px;
  }
}

.news__read-news {
  align-self: center;
  margin-top: 6px;

  @include desktop-medium {
    margin-top: 6px;
  }

  @include tablet {
    margin-right: 15px;
  }

  @include mobile-wide {
    order: 3;
    margin: 0 auto;
  }
}

.news__header-link h2 {
  margin: 0;
  font-size: 40px;
  line-height: 1.1;
  font-weight: 400;
  color: $color-blue;
  border-bottom: 1px dashed $color-blue;
  margin-bottom: 2px;
  transition: 0.3s all ease;
  align-self: center;

  @include desktop-medium {
    font-size: 1em;
    line-height: 1.1;
    margin-bottom: 2px;
  }

  @include tablet {
    font-size: 24px;
    line-height: 1.1;
    padding-top: 5px;
    margin-bottom: 2px;
  }
}

.news__header-link--current h2 {
  color: $color-white;
  border-bottom: none;
}

.news__header-link--active {
  cursor: pointer;
}

.news__container {
  width: 100%;
  margin-bottom: 34px;
  display: flex;
  flex-direction: column;

  @include desktop-medium {
    margin-bottom: 1.5vw;
  }

  @include mobile-wide {
    margin-bottom: 20px;
  }
}

.news__news-news,
.news__news-papers {
  width: 100%;
}


.news--animate .news__header-link {
  animation-name: animation_mainlinks;
  animation-fill-mode: both;
  animation-duration: 1s;
}

.news--reverse .news__header-link {
  animation-name: animation_mainlinks-reverse;
  animation-fill-mode: both;
  animation-duration: 1s;
}

.news--animate .news__read-news {
  animation-name: animation_mainbuttons;
  animation-fill-mode: both;
  animation-duration: 1s;
}

.news--reverse .news__read-news {
  animation-name: animation_mainbuttons-reverse;
  animation-fill-mode: both;
  animation-duration: 1s;
}
