@keyframes animation_wave {
  from {
    background-position: 1900px 0;
  }

  to {
    background-position: 0 0;
  }
}

@keyframes animation_wave-first {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes animation_wave-last {
  from {
    background-position: 1900px 0;
    background-size: 200% 100%;
  }

  to {
    background-position: 0 0;
    background-size: 200% 100%;
  }
}

@keyframes animation_wave-wrapper {
  from {
    width: 100%;
    right: 0;
    margin-right: 0;
  }

  30% {
    width: 90%;
    height: 120%;
    right: 0;
    margin-right: 0;
  }

  50% {
    width: 75%;
    height: 140%;
    right: 0;
    margin-right: 0;
  }

  70% {
    width: 60%;
    height: 120%;
    right: 0;
    margin-right: 0;
  }

  to {
    width: 50%;
    height: 100%;
    right: 0;
    margin-right: auto;
  }
}

@keyframes animation_buttons {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.switch {
  background: transparent;
  margin-bottom: 65px;
  z-index: 200;
  min-height: 100px;
  font-family: $font-merel;

  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;


  @include desktop-medium {
    position: relative;
    font-size: 0.7vw;
    min-height: 5.2vw;
    margin-bottom: 4vw;

    .last-page & {
      position: absolute;
    }
  }

  @include tablet {
    min-height: 60px;
    margin-bottom: 40px;
  }

  @include mobile-wide {
    display: none;
    padding: 0;
    margin: 0;
  }
}

.switch--dealers {
  @include mobile-wide {
    margin-bottom: 0;
    display: none;
  }
}

.switch__line-wrapper {
  position: relative;
  width: 100%;
  height: 100px;

  @include desktop-medium {
    height: 5.2vw;
  }

  @include tablet {
    height: 60px;
  }
}

.switch__line {
  width: 100%;
  height: 100px;
  min-height: 100px;
  background-image: url("../img/line.svg");
  background-position: 0 0;
  background-size: 100% 100%;
  background-repeat: repeat-x;
  position: absolute;
  padding-bottom: 31px;
  top: 0;
  right: 0;

  @include desktop-medium {
    height: 5.2vw;
    min-height: 5.2vw;
    padding-bottom: 15px;
  }

  @include tablet {
    height: 60px;
    min-height: 60px;
    padding-bottom: 15px;
  }


  @include mobile-wide {
    display: none;
  }
}

.switch--animation .switch__line {
  animation-name: animation_wave;
  animation-duration: 1s;
  animation-iteration-count: 1;
  animation-fill-mode: both;
  animation-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.switch--first .switch__line {
  width: 200%;
}

.switch--first .switch__line-wrapper {
  width: 50%;
  overflow-x: hidden;
  margin-left: auto;
}

.switch--last .switch__line {
  background-image: url("../img/line.svg");
  background-position: 0 0;
  background-size: 200% 100%;
}

.switch--last .switch__line-wrapper {
  width: 100%;
  margin-right: auto;
}

.switch--first.switch--animation .switch__line {
  animation-name: animation_wave-first;
  animation-duration: 1s;
  animation-iteration-count: 1;
  animation-fill-mode: both;
  animation-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.switch--last.switch--animation .switch__line {
  animation-name: animation_wave-last;
  animation-duration: 1s;
  animation-iteration-count: 1;
  animation-fill-mode: both;
  animation-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.switch--last.switch--animation .switch__line-wrapper {
  animation-name: animation_wave-wrapper;
  animation-duration: 1s;
  animation-iteration-count: 1;
  animation-fill-mode: both;
  animation-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.switch__center {
  position: absolute;
  left: calc(50% - 38px);
  top: calc(50% - 48px + 40px);

  @include desktop-medium {
    left: calc(50% - 2vw);
    top: calc(50% - 0.4vw);
  }


  button {
    width: 97px;
    height: 97px;
    border-radius: 50%;
    border: none;
    background-image: url("../img/menu@1x.png");
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    transition: 0.3s all ease;
    top: 0;
    left: 0;
    outline: none;

    &::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 2px solid $color-blue-gradient-start;
      transition: 0.3s all ease;
    }

    &::after {
      position: absolute;
      bottom: 20%;
      left: 30%;
      width: 40%;
      height: 50%;
      border-radius: 50%;
      content: "";
      z-index: -1;
      box-shadow: 0 0 20px 25px rgba(0, 117, 209, 0.4);
    }

    &:hover {
      left: 8px;
      top: 8px;
      width: 80px;
      height: 80px;

      &::before {
        top: -15%;
        left: -15%;
        content: "";
        width: 130%;
        height: 130%;
        position: absolute;
        border: 2px solid $color-blue-gradient-start;
      }
    }
  }

  p {
    min-width: 97px;
    margin-top: 97px;
    margin-bottom: 0;
    text-align: center;
    font-size: 15px;

    transform: translateX(calc(48px - 50% + 1px));
    transition: 0.3s all ease;
  }

  &:hover p {
    transform: translateY(5px) translateX(calc(48px - 50% + 1px));
  }

  @include desktop-medium {
    button {
      width: 5.1vw;
      height: 5.1vw;
      top: 0;
      left: 0;

      &:hover {
        left: 0.4vw;
        top: 0.4vw;
        width: 4.2vw;
        height: 4.2vw;

        &::before {
          top: -0.7vw;
          left: -0.7vw;
          width: 130%;
          height: 130%;
        }
      }
    }

    p {
      min-width: 5.1vw;
      margin-top: 5.1vw;
      transform: translateX(calc(2.5vw - 50%));
    }

    &:hover p {
      transform: translateY(0.2vw) translateX(calc(2.5vw - 50%));
    }
  }
}

.switch--animation .switch__center {
  animation-name: animation_buttons;
  animation-fill-mode: both;
  animation-duration: 1s;
  animation-delay: 1s;
}

.switch__button {
  position: absolute;
  display: flex;
  flex-direction: column;
  padding-top: 24px;

  button {
    width: 24px;
    height: 24px;
    border: none;
    padding: 0;
    position: absolute;
    left: -2px;
    top: -2px;
    background: transparent;
    outline: none;

    &::before {
      content: "";
      position: absolute;
      width: 12px;
      height: 12px;
      background-color: $color-white;
      border-radius: 50%;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      transition: all 0.3s ease;
    }

    &::after {
      content: "";
      position: absolute;
      width: 24px;
      height: 24px;
      background-color: transparent;
      border: 2px solid $color-blue;
      border-radius: 50%;
      left: 0;
      top: 0;
      transition: all 0.3s ease;
    }
  }

  &:hover button {
    left: -2px;
    top: -2px;

    &::before {
      transform: translate(-50%, -50%) scale(2);
      transform-origin: center;
      background-color: $color-blue;
    }

    &::after {
      transform: scale(1.5);
      transform-origin: center;
    }

    span {
      transform: translateY(20px);
    }
  }

  span {
    transform: translateX(calc(12px - 50% - 2px));
    font-size: 15px;
    transition: 0.3s all ease;
  }

  &:hover span {
    transform: translateY(5px) translateX(calc(12px - 50% - 2px));
  }
}

.switch--animation .switch__button {
  animation-name: animation_buttons;
  animation-fill-mode: both;
  animation-duration: 1s;
  animation-delay: 1s;
}

.switch__button--right-first {
  top: calc(34% - 12px);
  left: calc(65.3% - 12px);
}

.switch__button--right-second {
  top: calc(3% - 12px);
  left: calc(78% - 12px);
}

.switch__button--left-first {
  top: calc(60% - 12px);
  left: calc(12.3% - 12px);

  button {
    &::before {
      background-color: $color-blue;
    }
  }
}

.switch__button--left-second {
  top: calc(67.5% - 12px);
  left: calc(29.8% - 12px);

  button {
    &::before {
      background-color: $color-blue;
    }
  }
}
