.about-company-theme {
  overflow-x: hidden;
  background: #363640;
  .header {
    background: #363640;
  }
}

.about-company {
  .about-company__container {
    @include min-width(320px) {
      max-width: 1708px;
      width: 100%;
      margin: 0;
      padding: 0;
      margin: 0 auto;
    }
    @include min-width(992px) {
      padding-left: 36px;
    }
    .about-company__title {
      @include min-width(320px) {
        font-family: $font-bebasneue;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        line-height: 107.1%;
        text-transform: uppercase;
        padding-top: 30px;
        padding-left: 15px;
      }
      @include min-width(992px) {
        padding-left: 60px;
        font-size: 50px;
        padding-top: 56px;
      }
    }

    .about-company__project-goals {
      @include min-width(320px) {
        position: relative;
        margin-top: 80px;
        height: 487px;
      }
      @include min-width(992px) {
        height: 41.36vw;
        margin-top: 19vw;
      }
      @include min-width(1600px) {
        margin-top: 18vw;
      }

      .about-company_scope {
        background: rgba(81, 82, 92, 0.3);
        filter: blur(250px);
      }
    }

    .about-company__production {
      @include min-width(320px) {
        position: relative;
        margin-top: 80px;
        height: 520px;
      }
      @include min-width(992px) {
        height: 41.36vw;
        margin-top: 25.52vw;
      }
      @include min-width(1600px) {
        margin-top: 20.52vw;
      }
      .about-company_background_triangle {
        @include min-width(320px) {
          height: 100%;
        }
        @include min-width(992px) {
          height: 41.3vw;
        }
      }
      .about-company_content {
        @include min-width(320px) {
          height: 490px;
        }
        @include min-width(992px) {
          height: 0;
        }
      }
      .about-company_scope {
        background: rgba(0, 117, 209, 0.2);
        filter: blur(250px);
      }
    }

    .about-company__products {
      @include min-width(320px) {
        position: relative;
        margin-top: 80px;
        height: 715px;
      }
      @include min-width(992px) {
        margin-top: 25.52vw;
        height: 52.84vw;
      }
      @include min-width(1600px) {
        margin-top: 20.52vw;
      }
      .about-company_background_triangle {
        @include min-width(320px) {
          height: 705px;
        }
        @include min-width(992px) {
          height: 48.78vw;
        }
      }
      .about-company_scope {
        background: rgba(237, 133, 40, 0.2);
        filter: blur(250px);
      }
      .about-company_content {
        @include min-width(320px) {
          height: 680px;
        }
        @include min-width(992px) {
          height: 0;
        }
      }
      .about-company_strip {
        left: get-vw(550px);
      }
    }

    .about-company__company {
      @include min-width(320px) {
        position: relative;
        margin-top: 80px;
        height: 460px;
      }
      @include min-width(992px) {
        height: 41.36vw;
        margin-top: 25.52vw;
      }
      @include min-width(1600px) {
        margin-top: 20.52vw;
      }
      .about-company_scope {
        background: rgba(229, 229, 229, 0.1);
        filter: blur(250px);
      }
      .about-company_background_triangle {
        @include min-width(320px) {
          height: 430px;
        }
        @include min-width(992px) {
          height: 35.89vw;
        }
      }
      .about-company_content {
        @include min-width(320px) {
          height: 393px;
        }
        @include min-width(992px) {
          height: 0;
        }
        .about-company_desc {
          & > p {
            @include min-width(392px) {
              padding-top: 10px;
              line-height: 21px;
            }
            @include min-width(410px) {
              padding-top: 16px;
              line-height: 23px;
            }
            @include min-width(992px) {
              padding-top: 1.82vw;
              line-height: 1.84vw;
            }
          }
        }
      }
      .about-company_strip {
        left: get-vw(677px);
      }
    }
  }

  .about-company__links__container {
    z-index: 5;
    position: relative;

    // @include min-width(320px) {
    //   padding: 0px 0px;
    // }
    // @include min-width(768px) {
    //   padding: 3vw 20px;
    // }
    // @include min-width(1300px) {
    //   padding: 3vw 272px;
    // }
  }
}
