.desktop-library {
  .desktop-library__container {
    @include min-width(320px) {
      padding: 30px 15px;
      width: 100%;
      margin: 0 auto;
    }
    @include min-width(500px) {
      padding: 30px 30px;
    }
    @include min-width(992px) {
      padding: 56px 0px 0px;
      max-width: 896px;
    }
    @include min-width(1350px) {
      max-width: 1150px;
    }
    @include min-width(1550px) {
      max-width: 1496px;
    }
    & > h1 {
      @include min-width(320px) {
        font-size: 24px;
        font-family: $font-bebasneue;
        font-weight: 100;
        line-height: 107.1%;
      }
      @include min-width(992px) {
        margin-bottom: -20px;
        font-size: 50px;
      }
    }
  }
  // pagination
  .library-paginator {
    @include min-width(320px) {
      flex-wrap: wrap;
      padding-bottom: 0px;
      display: flex;
      padding-top: 10px;
    }
    @include min-width(992px) {
      padding-bottom: 80px;
      padding-top: 80px;
    }

    .swiper-wrapper {
      .article-slide {
        @include min-width(320px) {
          padding-bottom: 60px;
        }
        @include min-width(768px) {
          padding-bottom: 130px;
        }
        @include min-width(992px) {
          padding-bottom: 60px;
        }
        .article-slide_item {
          display: flex;
          align-items: flex-start;

          @include min-width(320px) {
            font-family: $font-merel;
            cursor: pointer;
            padding-bottom: 15px;
            position: relative;
            max-width: 1194px;
            width: 100%;
            margin: 0 auto;
          }
          @include min-width(992px) {
            padding-bottom: 30px;
          }

          .article-slide_item__icon {
            @include min-width(320px) {
              margin-right: 13px;
              width: 33px;
            }
            @include min-width(768px) {
              width: 43px;
              height: 50px;
            }
          }
          & > div {
            max-width: 1100px;
          }
          h2 {
            @include min-width(320px) {
              font-size: 14px;
              line-height: 111%;
              text-transform: none;
              font-weight: 100;
            }
            @include min-width(768px) {
              font-size: 22px;
              line-height: 26px;
              font-weight: 100;
              font-weight: 900;
            }
          }
          p {
            @include min-width(320px) {
              padding-top: 4px;
              font-size: 14px;
              line-height: 16px;
              color: #c4c4c4;
            }
            @include min-width(768px) {
              font-size: 20px;
            }
          }
          :hover {
            & > h2 {
              color: #0075d1;
              transition: 0.2s ease;
            }
          }
        }
      }
    }

    .desktop-library-slider__pagination {
      & > span {
        width: 100px;
        width: 81px;
        height: 7px;
        border-radius: 25px;
        background-color: #c4c4c4;
        opacity: 1;
        outline: 0;
      }
      .swiper-pagination-bullet-active {
        background-color: #4694d2;
      }
    }
    .swiper-pagination {
      @include min-width(320px) {
        bottom: 40px;
        padding-right: 15px;
      }
      @include min-width(768px) {
        padding-right: 0px;
        bottom: 110px;
      }
    }

    .library__current-page {
      @include min-width(320px) {
        text-align: center;
        font-family: $font-merel;
        font-size: 18px;
        line-height: 28px;
        color: #c4c4c4;

        position: absolute;
        bottom: -15px;
        left: 50%;
        right: 50%;
        margin-right: -50%;

        transform: translate(-50%, -50%);
        display: inline-block;
        width: 100%;
        padding-right: 15px;
      }
      @include min-width(768px) {
        bottom: 50px;
        font-size: 24px;
        padding-right: 0px;
      }
    }
  }
}
