.desktop-requisites {
  .desktop-requisites__container {
    @include min-width(320px) {
      padding: 40px 15px;
      width: 100%;
      margin: 0 auto;
    }
    @include min-width(500px) {
      padding: 40px 30px;
    }
    @include min-width(992px) {
      max-width: 400px;
    }
    @include min-width(992px) {
      max-width: 900px;
    }
    @include min-width(1350px) {
      padding: 56px 0px 0px;
      max-width: 1194px;
    }
    & > h1 {
      @include min-width(320px) {
        font-size: 24px;
        font-family: $font-bebasneue;
        font-weight: 100;
        line-height: 107.1%;
      }
      @include min-width(992px) {
        font-size: 50px;
      }
      @include min-width(1600px) {
        position: relative;
        left: get-vw(-150px);
      }
    }
    .desktop-requisites__content {
      @include min-width(320px) {
        font-family: $font-merel;
        padding-top: 10px;
        max-width: 1194px;
        width: 100%;
        margin: 0 auto;
        // padding-bottom: 40px;
        font-size: 14px;
        line-height: 20px;
      }
      @include min-width(992px) {
        padding-top: 60px;
        font-size: 24px;
        line-height: 30px;
        padding-bottom: 80px;
      }
      :nth-child(-n + 4) {
        padding-bottom: 20px;
      }
      .content__tittle {
        color: rgba(255, 255, 255, 0.37);
      }
    }

    .links__download {
      @include min-width(320px) {
        padding-bottom: 25px;
        display: block;
        align-items: center;
        font-size: 16px;
      }
      @include min-width(768px) {
        display: flex;
        font-size: 20px;
      }
      .links__download_title {
        @include min-width(320px) {
          color: white;
          white-space: nowrap;
          border-bottom: 1px dashed white;
          margin-right: 15px;
          display: block;
          // max-width: 130px;
          margin-bottom: 16px;
        }
        @include min-width(768px) {
          // max-width: 170px;
          display: block;
          margin-bottom: 0px;
          font-size: 15px;
        }
        @include min-width(992px) {
          font-size: 18px;
        }
        @include min-width(1200px) {
          font-size: 20px;
        }
      }
    }
  }
}
