.desktop-delivery-conditions {
  .desktop-delivery-conditions__container {
    @include min-width(320px) {
      padding: 30px 15px 420px;
      width: 100%;
      margin: 0 auto;
    }
    @include min-width(480px) {
      padding: 30px 50px 480px;
    }
    @include min-width(768px) {
      padding: 30px 50px 0px;
      margin-bottom: 200px;
    }
    @include min-width(992px) {
      padding: 56px 0px 140px;
      max-width: 896px;
      margin-bottom: 50px;
    }
    @include min-width(1044px) {
      padding: 56px 0px 160px;
    }
    @include min-width(1350px) {
      max-width: 1296px;
    }
    @include min-width(1550px) {
      max-width: 1496px;
      margin-bottom: 100px;
    }
    & > h1 {
      @include min-width(320px) {
        font-size: 24px;
        font-family: $font-bebasneue;
        font-weight: 100; 
        line-height: 107.1%;
      }
      @include min-width(992px) {
        margin-bottom: -20px;
        font-size: 50px;
      }
    }
    .desktop-delivery-conditions__content {
      counter-reset: myCounter;
    
      @include min-width(320px) {
        padding-top: 10px;
        padding-left: 15px;
        font-family: $font-merel;
        font-size: 14px;
        line-height: 16px;
        max-width: 1268px;
        width: 100%;
        margin: 0 auto;
      }
      @include min-width(768px) {
        font-size: 20px;
        line-height: 23px;
        padding-top: 60px;
        padding-left: 40px;
      }

      & > li {
        margin-top: -7px;

        & > span {
          color: #ed8528;
        }
      }

      li:before {
        @include min-width(320px) {
          position: relative;
          left: -16px;
          top: 16px;
          counter-increment: myCounter;
          content: counter(myCounter) ". ";
          color: #ed8528;
          display: block;
          font-size: 14px;
          line-height: 16px;
        }
        @include min-width(768px) {
          font-size: 20px;
          line-height: 23px;
          left: -22px;
          top: 23px;
        }
      }
    }
  }
}
.desktop-delivery-conditions__footer {
  .footer {
    position: absolute;
    bottom: 0px;
  }
}
