@keyframes animation_rotate-0 {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(60deg);
  }
}

@keyframes animation_rotate-0-reverse {
  from {
    transform: rotate(120deg);
  }

  to {
    transform: rotate(60deg);
  }
}

@keyframes animation_rotate-7-reverse {
  from {
    opacity: 1;
    transform: rotate(60deg);
  }

  to {
    opacity: 0;
    transform: rotate(0deg);
  }
}

@keyframes animation_stroke {
  from {
    stroke: $color-blue;
  }

  to {
    stroke: $color-orange;
  }
}

@keyframes animation_line {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@keyframes animation_color {
  from {
    stroke: $color-blue;
  }

  to {
    stroke: $color-orange;
  }
}

.drum {
  position: relative;
  animation-fill-mode: both;
  animation-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
  animation-duration: 1s;

  span {
    position: absolute;
    width: 23.7%;
    height: 23.7%;

    svg {
      width: 100%;
      height: 100%;
    }
  }
}

.drum__item {
  cursor: pointer;

  .drum__fill {
    fill: url("#paint0_linear");
    transition: 0.3s all ease;
  }

  &::after {
    position: absolute;
    bottom: 25%;
    left: 25%;
    width: 50%;
    height: 50%;
    border-radius: 50%;
    content: "";
    z-index: -1;
    box-shadow: 0 0 20px 55px rgba(0, 117, 209, 0.4);

    @media (max-height: 999px) {
      box-shadow: 0 0 20px 30px rgba(0, 117, 209, 0.4);
    }

    @include desktop-small {
      box-shadow: 0 0 20px 30px rgba(0, 117, 209, 0.4);
    }
  }

  &:hover {
    .drum__fill {
      fill: url("#paint01_linear");
    }
  }
}

.drum__item--active {
  &::after {
    box-shadow: 0 0 20px 55px rgba(237, 133, 40, 0.4);

    @media (max-height: 999px) {
      box-shadow: 0 0 20px 30px rgba(237, 133, 40, 0.4);
    }

    @include desktop-small {
      box-shadow: 0 0 20px 30px rgba(237, 133, 40, 0.4);
    }
  }
}

.drum__item:nth-of-type(3) {
  left: 17.9%;
  top: 26.6%;
}

.drum__item:nth-of-type(2) {
  left: 38.2%;
  top: 14.8%;
}

.drum__item:nth-of-type(1) {
  right: 17.7%;
  top: 26.5%;
}

.drum__item:nth-of-type(6) {
  right: 17.5%;
  bottom: 26.4%;
}

.drum__item:nth-of-type(5) {
  right: 38%;
  bottom: 14.7%;
}

.drum__item:nth-of-type(4) {
  left: 17.8%;
  bottom: 26.2%;
}

.drum__item:nth-of-type(1).drum__item--animate .drum__circle {
  animation-name: animation_color;
  animation-fill-mode: both;
  animation-duration: 1s;
}

.drum__item .drum__circle {
  stroke: $color-blue;
}

.drum__item--active .drum__circle {
  animation-name: animation_stroke;
  animation-fill-mode: both;
  animation-duration: 1s;
}

.drum--animate-0 {
  animation-name: animation_rotate-0;
}

.drum--animate-0-reverse {
  animation-name: animation_rotate-0-reverse;
}

.drum--animate-7-reverse {
  animation-name: animation_rotate-7-reverse;
  animation-duration: 1s;
}
