.desktop-patents {
  .desktop-patents__container {
    @include min-width(320px) {
      padding: 30px 15px;
      width: 100%;
      margin: 0 auto;
    }
    @include min-width(500px) {
      padding: 30px 30px;
    }
    @include min-width(992px) {
      padding: 56px 0px;
      max-width: 896px;
    }
    @include min-width(1350px) {
      max-width: 1196px;
    }
    @include min-width(1550px) {
      max-width: 1396px;
    }
    @include min-width(1700px) {
      max-width: 1496px;
    }
    & > h1 {
      @include min-width(320px) {
        font-size: 24px;
        font-family: $font-bebasneue;
        font-weight: 100;
        line-height: 107.1%;
      }
      @include min-width(992px) {
        margin-bottom: -20px;
        font-size: 50px;
        padding-bottom: 45px;
      }
    }
    .desktop-patents__content {
      @include min-width(320px) {
        padding-top: 10px;
        font-family: $font-merel;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        margin: 0 -25px;
      }
      @include min-width(992px) {
        flex-wrap: wrap;
        flex-direction: row;
        margin: 0 -50px;
      }

      .patent__item {
        @include min-width(320px) {
          cursor: pointer;
          max-width: 465px;
          flex: 1 1 auto;
          padding: 20px 25px 0px 25px;
          margin: 0 auto;
        }
        @include min-width(992px) {
          width: calc(1 / 3 * 100% - 50px);
          max-width: 282px;
          padding-top: 10px;
          margin: 25px;
        }
        @include min-width(1350px) {
          // padding-top: 50px;
          max-width: 382px;
        }
        @include min-width(1550px) {
          max-width: 465px;
        }

        & > h2 {
          @include min-width(320px) {
            font-size: 16px;
            line-height: 19px;
            text-transform: none;
            padding: 3px 0px 3px;
            font-weight: 100;
          }
          @include min-width(992px) {
            font-size: 18px;
            line-height: 18px;
            padding: 10px 0px 10px;
            font-weight: 500;
          }
          @include min-width(1350px) {
            font-size: 20px;
            line-height: 23px;
            padding: 15px 0px 15px;
            font-weight: 500;
          }
        }
        & > P {
          @include min-width(320px) {
            color: #e5e5e5;
            font-size: 14px;
            line-height: 16px;
          }
          @include min-width(992px) {
            font-size: 14px;
            line-height: 14px;
          }
          @include min-width(1350px) {
            font-size: 20px;
            line-height: 23px;
          }
        }
      }
    }
  }
}
