.round-button {
  border-radius: 100px;
  background-color: transparent;
  color: $color-white;
  display: block;
  text-transform: uppercase;
  padding: 15px 36px 15px 38px;
  transition: all 0.3s ease;
  font-family: $font-bebasneue;
  position: relative;
  cursor: pointer;
  outline: none;

  a {
    color: white;
  }

  @include desktop-medium {
    border-radius: 5.2vw;
    padding: 0.95vw 1.8vw 0.85vw 2vw;
    align-self: center;
  }

  @include mobile-wide {
    align-self: stretch;
    border-radius: 100px;
    padding: 7px 15px 6px 15px;
  }

  svg {
    position: absolute;
  }
}

.round-button--blue {
  border: 1px solid $color-blue;
  color: $color-white;
  font-size: 20px;
  line-height: 20px;
  font-weight: 400;
  transition: all 0.3s ease;
  padding: 16px 44px 13px 42px;

  &:hover {
    background-color: $color-blue;
  }

  @include desktop-medium {
    font-size: 0.5em;
    line-height: 1.2;
    padding: 1vw 2.3vw 0.8vw 2.2vw;
  }

  @include tablet {
    font-size: 16px;
    line-height: 16px;
    padding: 1vw 10px 0.8vw 8px;
  }

  @include mobile-wide {
    font-size: 16px;
    line-height: 16px;
    padding: 14px 46px 13px 44px;
  }
}

.round-button--news {
  padding: 15px 44px 14px 42px;
  transition: all 0.3s ease;

  @include desktop-medium {
    padding: 0.7vw 2.3vw 0.7vw 2.2vw;
  }

  @include mobile-wide {
    padding: 14px 46px 13px 48px;
  }
}

.round-button--orange {
  border: 1px solid $color-orange;
  color: $color-white;
  font-size: 20px;
  line-height: 20px;
  transition: all 0.3s ease;

  svg {
    stroke: $color-white;
  }

  &:hover {
    color: $color-orange;

    svg {
      stroke: $color-orange;
    }
  }

  @include desktop-medium {
    font-size: 0.8em;
    line-height: 1;
  }

  @include tablet {
    font-size: 11px;
  }

  @include mobile-wide {
    font-size: 12px;
    line-height: 12px;
  }
}

.round-button--phone {
  padding: 0;
  border-radius: 50%;
  width: 52px;
  min-width: 52px;
  height: 52px;
  transition: all 0.3s ease;
  outline: none;

  svg {
    top: 10px;
    left: 10px;
    stroke: $color-white;
    fill: transparent;
    width: 32px;
    height: 32px;
    transition: all 0.3s ease;
  }

  @include desktop-medium {
    width: 2.7vw;
    min-width: 2.7vw;
    height: 2.7vw;
    align-self: center;

    svg {
      top: 0.5vw;
      left: 0.5vw;
      width: 1.6vw;
      height: 1.6vw;
    }
  }

  @include mobile-wide {
    width: 35px;
    min-width: 35px;
    height: 35px;

    svg {
      top: 19.2%;
      left: 19.2%;
      width: 61.5%;
      height: 61.5%;
    }
  }
}

.round-button--blue-arrow {
  font-size: 25px;
  line-height: 25px;
  border: 2px solid $color-blue-gradient-start;
  border-radius: 100px;
  padding: 24px 42px 21px 42px;
  text-align: center;
  min-width: 315px;
  max-width: 400px;
  transition: all 0.3s ease;

  svg {
    fill: $color-blue-gradient-start;
    width: 40px;
    height: 17px;
    right: 12px;
    top: calc(50% - 8px);
    transition: all 0.3s ease;
  }

  &::after {
    content: "";
    position: absolute;
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    border-radius: 100px;
    left: -2px;
    top: -2px;
    z-index: -1;
    transition: all 0.3s ease;
    background-image: $color-gradient-blue;
    background-size: cover;
    opacity: 0;
  }

  &:hover {
    border: 2px solid transparent;

    &::after {
      opacity: 1;
    }

    svg {
      fill: $color-white;
    }
  }

  @include desktop-medium {
    font-size: 0.625em;
    line-height: 1;
    padding: 1.2vw 2.2vw 1.1vw 2.2vw;
    min-width: 16.5vw;
    max-width: 21vw;

    svg {
      width: 2.1vw;
      height: 0.8vw;
      right: 0.6vw;
      top: calc(50% - 0.4vw);
    }

    &::after {
      width: calc(100% + 0.2vw);
      height: calc(100% + 0.2vw);
      border-radius: 5.2vw;
      left: -2px;
      top: -2px;
    }
  }

  @include mobile-wide {
    min-width: 200px;
    margin: 0 auto;
    font-size: 16px;
    line-height: 16px;
    padding: 15px 25px 14px 23px;

    svg {
      right: 17px;
      top: calc(50% - 7px);
      width: 7px;
      height: 12px;
    }
  }
}
