.desktop-certificate__hide-empty-space {
  height: 1500px;
  overflow: hidden;
}

.desktop-certificate {
  .desktop-certificate__container {
    @include min-width(320px) {
      padding: 30px 15px;
      width: 100%;
      margin: 0 auto;
    }
    @include min-width(500px) {
      padding: 30px 30px;
    }
    @include min-width(992px) {
      padding: 56px 0px;
      max-width: 896px;
    }
    @include min-width(1350px) {
      max-width: 1196px;
    }
    @include min-width(1550px) {
      max-width: 1396px;
    }
    @include min-width(1700px) {
      max-width: 1496px;
    }
    & > h1 {
      @include min-width(320px) {
        font-size: 24px;
        font-family: $font-bebasneue;
        font-weight: 100;
        line-height: 107.1%;
      }
      @include min-width(992px) {
        margin-bottom: -20px;
        font-size: 50px;
        padding-bottom: 45px;
      }
    }
    .desktop-certificate__content {
      @include min-width(320px) {
        padding-top: 10px;
        font-family: $font-merel;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
      }
      @include min-width(992px) {
        flex-wrap: wrap;
        flex-direction: row;
        margin: 0 -50px;
      }

      .certificate__item {
        @include min-width(320px) {
          max-width: 465px;
          flex: 1 1 auto;
          padding: 20px 0px 0px 0px;
          margin: 0 auto;
        }
        @include min-width(992px) {
          width: calc(1 / 3 * 100% - 50px);
          max-width: 282px;
          padding-top: 10px;
          margin: 25px;
        }
        @include min-width(1350px) {
          // padding-top: 50px;
          max-width: 382px;
        }
        @include min-width(1550px) {
          max-width: 465px;
        }
        & > img {
          cursor: pointer;
        }
        & > P {
          @include min-width(320px) {
            padding-top: 7px;
            color: #e5e5e5;
            font-size: 16px;
            line-height: 20px;
          }
          @include min-width(992px) {
            font-size: 16px;
            line-height: 16px;
          }
          @include min-width(1350px) {
            font-size: 20px;
            line-height: 23px;
          }
        }
      }
    }
    .certificate__trails {
      @include min-width(320px) {
        padding-top: 30px;
      }
      @include min-width(992px) {
        padding-top: 60px;
        margin-bottom: 30px;
      }

      & > h2 {
        @include min-width(320px) {
          font-size: 24px;
          line-height: 24px;
          font-family: $font-bebasneue;
          font-weight: 100;
        }
        @include min-width(992px) {
          font-size: 50px;
          line-height: 107.1%;
        }
      }
      .trails-block {
        @include min-width(320px) {
          font-family: $font-merel;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
        }
        @include min-width(992px) {
          padding-top: 40px;
          flex-wrap: wrap;
          flex-direction: row;
          margin: 0 -50px;
        }
        .trails-block__item {
          @include min-width(320px) {
            position: relative;
            max-width: 465px;
            max-height: 655px;
            padding-top: 20px;
            margin: 0 auto;
            flex: 1 1 auto;
          }
          @include min-width(992px) {
            width: calc(1 / 3 * 100% - 50px);
            max-width: 282px;
          }
          @include min-width(1350px) {
            max-width: 382px;
          }
          @include min-width(1550px) {
            max-width: 465px;
          }

          & > img {
            box-shadow: 5px 0px 10px rgba(0, 17, 31, 0.5);

            &:hover {
              color: #c4c4c4;
              background: linear-gradient(180.07deg, #3b3b47 41.16%, #31313b 99.91%);
              box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
              cursor: pointer;
            }
          }

          & > p {
            @include min-width(320px) {
              font-size: 25px;
              line-height: 107.1%;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              font-family: $font-bebasneue;
              font-style: normal;
              font-weight: normal;
              text-align: center;
              text-transform: uppercase;
              font-weight: 100;
              width: 100%;
              padding: 0px 20px;
              cursor: pointer;
            }
            @include min-width(768px) {
              font-size: 20px;
            }
            @include min-width(1044px) {
              font-size: 24px;
            }
            @include min-width(1668px) {
              font-size: 34px;
            }
          }
        }
      }
    }
  }
}
