@keyframes animation__orange-line-start {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@keyframes animation__orange-line-end {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@keyframes animation__orange-stroke-link {
  from {
    border-color: $color-blue;
    box-shadow: 0 0 2px 2px rgba(0, 117, 209, 0.4);
  }

  to {
    border-color: $color-orange;
    box-shadow: 0 0 2px 2px rgba(237, 133, 40, 0.4);
  }
}

.switch-advantages__list {
  @include list-reset;
  padding-top: 25px;

  @include desktop-medium {
    padding-top: 1.3vw;
  }

  @include mobile-wide {
    padding-top: 0;
  }
}

.integrate .switch-advantages__list-item {
  opacity: 0;

  @include mobile-wide {
    opacity: 1;
  }
}

.switch-advantages__link {
  position: relative;
  padding: 11px 10px 5px 8px;
  display: flex;
  flex-direction: column;
  border: 1px solid $color-blue;
  width: 100%;
  border-radius: 10px;
  text-align: center;
  margin-bottom: 16px;
  min-height: 99px;
  cursor: pointer;
  box-shadow: 0 0 2px 2px rgba(0, 117, 209, 0.4);
  transition: 0.3s all ease;

  &::before {
    position: absolute;
  }

  &:hover {
    background-color: rgba(255, 255, 255, 0.05);
  }

  @media (max-height: 999px) {
    padding: 0.5vw 0.5vw 0.2vw 0.4vw;
    margin-bottom: 0.8vw;
    min-height: 8.5vh;
  }

  @include desktop-medium {
    padding: 0.5vw 0.5vw 0.2vw 0.4vw;
    margin-bottom: 0.8vw;
    min-height: 5.2vw;
  }

  @include desktop-small {
    @media (max-height: 767px) {
      padding: 0.5vw 0.5vw 0.2vw 0.4vw;
      margin-bottom: 0.8vw;
      min-height: 8.5vh;
    }
  }

  @include tablet {
    min-height: 60px;// auto;
    padding: 5px 5px 2px 4px;
    margin-bottom: 8px;

    // @media (max-height: 699px) {
    //   padding: 0.5vw 0.5vw 0.2vw 0.4vw;
    //   margin-bottom: 0.8vw;
    //   min-height: 8.5vh;
    // }
  }

  @include mobile-wide {
    padding: 10px 3px 14px;
    min-height: auto;
    margin-bottom: 10px;
    letter-spacing: -0.015em;
  }
}

.switch-advantages__list-item:nth-of-type(3) .switch-advantages__link,
.switch-advantages__list-item:nth-of-type(4) .switch-advantages__link {
  padding-top: 4px;
  padding-bottom: 13px;

  @include desktop-medium {
    padding-top: 0.2vw;
    padding-bottom: 0.6vw;
  }

  @include mobile-wide {
    padding-top: 10px;
    padding-bottom: 1px;

    .switch-advantages__orange-text {
      margin-bottom: 0;
      line-height: 27px;
    }
  }
}

.switch-advantages__list-item:nth-of-type(4) .switch-advantages__link {
  @include mobile-wide {
    .switch-advantages__orange-text {
      letter-spacing: 0;
    }

    .switch-advantages__white-text {
      line-height: 15px;
      padding-bottom: 1px;
    }
  }
}

.switch-advantages__orange-text {
  font-family: $font-bebasneue;
  color: $color-orange;
  font-size: 35px;
  line-height: 36px;
  letter-spacing: 0.03em;
  margin: 0;
  margin-bottom: 3px;

  @media (max-height: 999px) {
    font-size: 0.875em;
    line-height: 1.028;
    letter-spacing: 0.03em;
    margin: 0;
    margin-bottom: 0.1vw;
  }


  @include desktop-small {
    @media (max-height: 767px) {
      font-size: 0.875em;
      line-height: 1.028;
      letter-spacing: 0.03em;
      margin: 0;
      margin-bottom: 0.1vw;
    }
  }

  @include desktop-medium {
    font-size: 0.875em;
    line-height: 1.028;
    letter-spacing: 0.03em;
    margin: 0;
    margin-bottom: 0.1vw;
  }

  @include tablet {
    font-size: 20px;
    line-height: 22px;

    @media (max-height: 700px) and (min-height: 500px) {
      font-size: 0.875em;
      line-height: 1.028;
      letter-spacing: 0.03em;
      margin: 0;
      margin-bottom: 0.1vw;
    }
  }

  @include mobile-wide {
    margin-bottom: 0;
    font-size: 28px;
    line-height: 30px;
  }
}

.switch-advantages__white-text {
  font-family: $font-merel;
  color: $color-white;
  font-size: 18px;
  line-height: 19px;
  margin: 0;
  font-weight: 500;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-height: 999px) {
    font-size: 0.75em;//0.6em;
    line-height: 1.055;
  }

  @include desktop-medium {
    font-size: 0.75em;//0.6em;
    line-height: 1.055;
  }


  @include desktop-small {
    @media (max-height: 767px) {
      font-size: 0.75em;//0.6em;
      line-height: 1.055;
    }
  }

  @include tablet {
    font-size: 14px;
    line-height: 16px;
  }

  @include mobile-wide {
    margin-bottom: 3px;
    font-size: 14px;
    line-height: 16px;
  }
}

.switch-advantages__link-before,
.switch-advantages__link-after {
  position: absolute;
}

.switch-advantages__link-after {
  width: 120px;
  height: 1px;
  position: absolute;
  z-index: -1;
  cursor: default;

  @include desktop-medium {
    width: 6.3vw;
  }

  @include tablet {
    width: 80px;
  }
}

.switch-advantages__link--active {
  position: relative;
  border: 1px solid $color-orange;
  animation-name: animation__orange-stroke-link;
  animation-duration: 2s;
  animation-fill-mode: both;
  animation-delay: 2s;
  box-shadow: 0 0 2px 2px rgba(237, 133, 40, 0.4);

  .switch-advantages__link-before-line {
    position: absolute;
    height: 1px;
    background-color: $color-orange;
    animation-duration: 1s;
    animation-fill-mode: both;
    animation-delay: 2s;
    box-shadow: 0 0 5px 0 $color-orange;
    transform-origin: 0 0;
    left: 0;
    bottom: 0;
    animation-name: animation__orange-line-end;
  }

  .switch-advantages__link-before {
    position: absolute;
    cursor: default;
  }

  .switch-advantages__link-after {
    &::after {
      content: "";
      position: absolute;
      height: 1px;
      left: 0;
      top: 0;
      background-color: $color-orange;
      animation-name: animation__orange-line-start;
      animation-duration: 0.8s;
      animation-fill-mode: both;
      animation-delay: 1.2s;
      box-shadow: 0 0 5px 0 $color-orange;
    }
  }

  @include desktop-medium {
    .switch-advantages__link-before-line {
      box-shadow: 0 0 0.2vw 0 $color-orange;
    }

    .switch-advantages__link-after {
      width: 6.3vw;
    }

    .switch-advantages__link-after::after {
      box-shadow: 0 0 0.2vw 0 $color-orange;
    }
  }

  @include tablet {
    .switch-advantages__link-after {
      width: 80px;
    }
  }

  @include mobile-wide {
    animation-name: none;

    .switch-advantages__link-before,
    .switch-advantages__link-after {
      display: none;
    }
  }
}
