.cookies-popup {
  background: rgba(42, 42, 50, 0.92);
  overflow: hidden;
  position: fixed;
  top: 0px;
  left: -1px;
  z-index: 999999999;
  width: 100%;
  height: 100%;

  .cookies-popup_content {
    @include min-width(320px) {
      position: absolute;
      bottom: 1px;
      height: auto;
      height: 420px;
      width: 100%;
      background: linear-gradient(180.25deg, #3b3b47 41.29%, #31313b 99.92%),
        linear-gradient(180.12deg, #353540 41.16%, #31313b 99.91%);
    }
    @include min-width(365px) {
      height: 350px;
    }
    @include min-width(512px) {
      height: 250px;
    }
    @include min-width(783px) {
      height: 180px;
    }

    & > img {
      @include min-width(320px) {
        height: 180px;
        display: none;
      }
      @include min-width(850px) {
        display: block;
        display: flex;
        justify-content: flex-end;
      }
    }

    & > p {
      @include min-width(320px) {
        max-width: 100%;
        font-family: $font-merel;
        font-size: 14px;
        line-height: 23px;
        color: #e5e5e5;
        position: absolute;
        top: 1px;
        padding: 60px 15px 30px 12px;
      }
      @include min-width(850px) {
        padding: 30px 0px 30px 5vw;
        max-width: 90vw;
        font-size: 16px;
      }
      @include min-width(1055px) {
        font-size: 20px;
      }
      @include min-width(1379px) {
        max-width: 1400px;
      }
    }

    .popup__close_btn {
      background: none;
      border: none;
      position: absolute;
      top: 23px;
      right: 30px;
      margin-left: 30px;
    }
  }
}
