@keyframes loading {
  0% {
    width: 300px;
  }

  100% {
    width: 0;
  }
}

.loader {
  background-color: $color-dark-gray-background;
  min-height: 100vh;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding-top: calc(50vh - 44px);
  z-index: 1000;
}

.integrate .loader {
  height: 100vh;
}

.loader__img-container {
  width: 267px;
  height: auto;
  margin: auto;
  position: relative;

  @include mobile-wide {
    width: 200px;
  }
}

.loader__button {
  background: transparent;
  border: none;
}

.loader__animating-panel {
  position: absolute;
  top: 0;
  right: 0;
  background-color: $color-dark-gray-background;
  animation-name: loading;
  animation-duration: 2s;
  animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
  height: 100%;
  z-index: 100;
}

.loader__img {
  height: 100%;
  width: 100%;
}
