.vacancy-popup {
  @include min-width(320px) {
    background: rgba(42, 42, 50, 0.92);
    overflow: hidden;
    position: absolute;
    top: 0px;
    left: -1px;
    z-index: 999999999;
    width: 100%;
    height: 100%;
  }
  .vacancy-popup__wrapper {
    @include min-width(320px) {
      position: relative;
      width: 100vw;
      background: linear-gradient(180.1deg, #3b3b47 41.16%, #31313b 99.91%);
      padding: 62px 0;
    }
    @include min-width(992px) {
      width: get-vw(1194px);
      height: get-vw(1129px);
      margin: 11vh auto;
      padding: get-vw(80px) get-vw(50px) get-vw(179px);
    }
    .vacancy-popup__content {
      .content__header {
        @include min-width(320px) {
          align-items: center;
          display: flex;
          border-bottom: 1px solid #c4c4c4;
          justify-content: space-between;
          padding: 0px 15px 10px;
          font-family: $font-bebasneue;
          font-weight: 100;
        }
        @include min-width(992px) {
          font-size: get-vw(40px);
          padding: 0px 0px get-vw(10px);
        }
        & > h2 {
          @include min-width(320px) {
            max-width: 500px;
            font-size: 24px;
            font-weight: 100;
            font-size: 24px;
          }
          @include min-width(992px) {
            font-size: get-vw(40px);
            font-weight: 100;
          }
        }
        & > p {
          @include max-width(992px) {
            font-size: 24px;
          }
          color: #ed8528;
        }
      }
      .content__main {
        @include min-width(320px) {
          padding: 20px 15px;
          font-size: 14px;
        }
        @include min-width(992px) {
          padding: get-vw(60px) 0;
          font-size: get-vw(24px);
          display: flex;
          justify-content: space-between;
        }
        .content__main_about {
          @include min-width(320px) {
          }
          @include min-width(992px) {
            max-width: get-vw(522px);
            width: 100%;
          }
          & > p {
            @include min-width(320px) {
              font-weight: 100;
              padding-top: 16px;
              line-height: 16px;
            }
            @include min-width(992px) {
              padding-top: get-vw(24px);
              line-height: get-vw(33px);
            }
          }
          p:first-child {
            padding-top: 0px;
          }
        }
        .content__main_responsibilities {
          @include min-width(320px) {
            width: 100%;
            padding-right: get-vw(5px);
            padding-top: 20px;
          }
          @include min-width(992px) {
            padding-top: 0px;
            max-height: get-vw(702px);
            max-width: get-vw(511px);
            overflow-y: scroll;
          }
          & > h3 {
            @include min-width(320px) {
              text-transform: none;
              font-weight: 500;
              font-size: 20px;
            }
            @include min-width(992px) {
              font-size: get-vw(34px);
            }
          }
          & > p {
            @include min-width(320px) {
              font-size: 14px;
              padding-top: 16px;
              font-weight: 100;
              line-height: 16px;
            }
            @include min-width(992px) {
              padding-top: get-vw(24px);
              line-height: get-vw(29px);
              font-size: get-vw(20px);
            }
          }
        }
        ::-webkit-scrollbar {
          width: get-vw(4px);
        }
      }
    }
    .vacancies__request {
      @include min-width(320px) {
        margin: 0;
        position: absolute;

        left: 50%;
        transform: translate(-50%);

        bottom: 15px;
        max-width: 290px;
        width: 290px;
        font-size: 14px;
        text-align: center;
      }
      @include min-width(992px) {
        font-size: get-vw(20px);
        max-width: get-vw(731px);
        text-align: right;
        bottom: get-vw(60px);

        width: 100%;
        transform: none;
        left: 0;
      }
    }
    .vacancy-popup_close {
      @include min-width(320px) {
        background: none;
        border: none;
        position: absolute;
        top: 20px;
        right: 20px;
      }
      @include min-width(992px) {
        top: get-vw(-40px);
        right: get-vw(-70px);
      }
      .close-icon {
        @include min-width(320px) {
          width: 22px;
          height: 22px;
        }
        @include min-width(992px) {
          width: get-vw(26px);
          height: get-vw(26px);
        }
      }
    }
  }
}
