.desktop-contacts__representations-area {
  .representations-area__offices {
    @include min-width(320px) {
      margin-top: get-vw(60px);
      padding: 10px 15px 20px;
      background: linear-gradient(180.02deg, #3b3b47 41.16%, #31313b 99.91%);
      box-shadow: 0px 0px 10px rgba(0, 17, 31, 0.25);
      position: absolute;
      left: 0px;
      width: 100%;
    }
    @include min-width(992px) {
      padding: get-vw(30px) get-vw(200px) get-vw(40px);
    }
    & > h2 {
      @include min-width(320px) {
        padding-bottom: 21px;
        text-transform: none;
        font-size: 18px;
        line-height: 21px;
        font-weight: 900;
      }
      @include min-width(992px) {
        font-weight: 100;
        font-size: get-vw(30px);
        line-height: get-vw(35px);
        padding-bottom: get-vw(30px);
      }
    }
    .swiper-wrapper {
      justify-content: space-between;
      display: flex;
      .offices__contacts {
        @include min-width(320px) {
          max-width: 240px;
          width: 100%;
        }
        @include min-width(992px) {
          max-width: get-vw(232px);
        }
        & > h3 {
          @include min-width(320px) {
            font-weight: 100;
            text-transform: none;
            font-size: 15px;
            line-height: 18px;
          }
          @include min-width(992px) {
            font-size: get-vw(20px);
            line-height: get-vw(23px);
          }
        }
        & > p {
          @include min-width(320px) {
            font-size: 14px;
            line-height: 16px;
          }
          @include min-width(992px) {
            font-size: get-vw(14px);
            line-height: get-vw(14px);
          }
          & > a {
            color: #0075d1;
          }
          .contacts__mail {
            color: white;
          }
        }
        h3,
        p {
          @include min-width(320px) {
            padding-bottom: 6px;
          }
          @include min-width(992px) {
            padding-bottom: get-vw(20px);
          }
        }
        :last-child {
          padding-bottom: 0;
        }
      }
    }
  }
  .representations-area__search {
    @include min-width(320px) {
      max-width: 290px;
      margin: 272px auto 0;
      display: flex;
      padding-bottom: get-vw(85px);
    }
    @include min-width(992px) {
      max-width: none;
      padding-left: 0;
      margin: get-vw(443px) auto 0;
    }
    h2 {
      @include min-width(320px) {
        font-size: 18px;
        line-height: 21px;
        text-transform: none;
        padding-bottom: 10px;
        font-weight: 100;
      }
      @include min-width(992px) {
        font-size: get-vw(30px);
        line-height: get-vw(35px);
        font-weight: 900;
        padding-bottom: get-vw(30px);
      }
    }
    .search__choose-region {
      display: flex;

      & > p {
        @include min-width(320px) {
          display: none;
          font-size: get-vw(20px);
          line-height: get-vw(23px);
          margin: get-vw(10px) get-vw(20px);
        }
        @include min-width(992px) {
          display: block;
        }
      }
      .search__dropdown_wrapper {
        .search__dropdown {
          @include min-width(320px) {
            background: #494957;
            width: 290px;
            height: 50px;
            box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
            font-size: 14px;
            line-height: 100%;
            padding: 16px 10px;
            display: flex;
            justify-content: space-between;
            align-items: center;
          }
          @include min-width(992px) {
            width: get-vw(420px);
            padding: get-vw(15px) get-vw(10px);
            font-size: get-vw(20px);
          }
        }
        .search__dropdown_content {
          @include min-width(320px) {
            background: rgba(49, 49, 59, 0.8);
            border-radius: 10px;
            max-height: 570px;
            overflow-y: scroll;
            margin-top: 20px;
          }
          @include min-width(992px) {
            margin-top: get-vw(30px);
            max-height: get-vw(770px);
          }

          .dropdown__content_item {
            @include min-width(320px) {
              cursor: pointer;
              padding: 14px 15px 6px 37px;
              background: rgba(49, 49, 59, 0.8);
              font-size: 18px;
            }
            @include min-width(992px) {
              font-size: get-vw(18px);
              padding: get-vw(25px) 0 get-vw(15px) get-vw(34px);
            }
            &:hover {
              background: #3b3b47;
              transition: 0.2s ease;
            }
            & > h3 {
              @include min-width(320px) {
                font-size: 20px;
                font-family: $font-bebasneue;
                line-height: 84.6%;
                font-weight: 100;
              }
              @include min-width(992px) {
                font-size: get-vw(25px);
              }
            }
            & > p > a {
              color: #6b98d3;
            }
            .dropdown__content_phone {
              display: flex;
              align-items: center;

              & > p {
                @include min-width(320px) {
                  padding-left: 10px;
                }
                @include min-width(992px) {
                  padding-left: get-vw(10px);
                }
              }
              & > p > a {
                color: white;
              }
            }
            h3,
            p,
            div {
              @include min-width(320px) {
                padding-bottom: 4px;
              }
              @include min-width(992px) {
                padding-bottom: get-vw(5px);
              }
            }
            :last-child {
              padding-bottom: 0px;
            }
          }
        }
      }
    }

    .address__map {
      @include min-width(320px) {
        width: 57.63vw;
        position: absolute;
        right: 0;
        display: none;
      }
      @include min-width(992px) {
        display: block;
        height: 45vw;
      }
    }
  }

  // scroll-bar
  ::-webkit-scrollbar {
    width: get-vw(6px);
  }
}
