    &[data-hidden="false"] {
      border-radius: 21.5px;
      transition: all $style-change-duration $style-change-function;

      .select-modification__value {
        display: flex;
        align-items: center;
        border-top-left-radius: 21.5px;
        border-top-right-radius: 21.5px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        padding: 17px 20px 13px 20px;
        background-color: $color-blue;
        transition: all $style-change-duration $style-change-function;
        outline: none;

        & svg {
          transform: rotate(-90deg);
        }
      }

      .swiper-wrapper {
        display: block;
        position: relative;
        padding-top: 5px;
        padding-bottom: 25px;
        background-color: $color-gray-blue-input;
        border-bottom-left-radius: 21.5px;
        border-bottom-right-radius: 21.5px;
        animation: open-select 0.2s ease-in;
      }
    }
  }

  .select-modification__options {
    padding-top: 40px;
    display: flex;
    flex-wrap: wrap;

    .modification__active {
      color: #0075d1;
    }

    @include tablet {
      // display: block;
      // width: auto;
      // height: auto;
    }
    @include max-width(992px) {
      display: none;
    }
  }

  .break {
    flex-basis: 100%;
    height: 0;
  }

  .mrInd {
    @include min-width(1174px) {
      margin-right: 0px !important;
    }
    @include min-width(1370px) {
      margin-right: 127px !important;
    }

    // @include min-width (1700px) {
    //   margin-right: 127px!important;
    // }
  }
  #margin-right__firs-strip {
    margin-right: 0px;

    @include min-width(992px) {
      margin-right: 20px;
    }
    @include min-width(1200px) {
      margin-right: 60px;
    }
  }
  #margin-right__second-strip {
    margin-right: 40px;

    @include min-width(992px) {
      margin-right: 5px;
    }
    @include min-width(1200px) {
      margin-right: 20px;
    }
  }
  #margin-right__third-strip {
    margin-right: 70px;

    @include min-width(992px) {
      margin-right: 10px;
    }
    @include min-width(1200px) {
      margin-right: 60px;
    }
  }

  .select-modification__item {
    position: relative;
    font-family: $font-bebasneue;
    box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    flex-shrink: 1;
    margin-bottom: 20px;

    padding-top: get-vw(20px);
    padding-bottom: get-vw(20px);

    font-size: get-vw(20px);
    line-height: get-vw(26px);
    text-align: center;
    text-transform: uppercase;
    background: $color-gray-blue-input;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 100px;
    border: 1px solid $color-blue;
    cursor: pointer;

    @include min-width(1200px) {
      font-size: get-vw(22px);
    }
    @include min-width(1600px) {
      font-size: get-vw(20px);
    }

    .modification__item_icon {
      @include min-width(992px) {
        width: get-vw(14px);
        height: get-vw(14px);
        position: absolute;
        right: get-vw(17px);
      }
      @include min-width(1600px) {
        right: get-vw(23px);
      }
    }

    @include hover-focus-color($color-blue);

    &:nth-child(3n) {
      margin-right: 0;
    }

    &[data-active="true"] {
      color: $color-blue;
    }

    // @include tablet {
    //   display: block;
    //   margin: 0;
    //   // padding: 17px 15px 8px 47px;
    //   text-transform: uppercase;
    //   font-size: 27px;
    //   line-height: 29px;
    //   text-align: left;
    //   border-radius: 0;
    //   box-shadow: none;
    //   cursor: pointer;

    //   &[data-active="true"] {
    //     color: $color-white;
    //     background-color: $color-darknes-gray;
    //   }

    //   @include hover-focus-color ($color-white);
    //   @include hover-focus-background-color ($color-darknes-gray);

    //   @include tablet {
    //     padding: 17px 20px 13px 20px;
    //     font-size: 16px;
    //     line-height: 16px;
    //     border: none;
    //   }
    // }

    @include laptop {
      margin-right: 5px;
    }
  }

  .select-modification__pagination {
    display: flex;
    justify-content: center;

    & span {
      margin: 0;
      width: 81px;
      height: 7px;
      border-radius: 25px;
      background-color: $color-gray;
      opacity: 1;
      outline: none;
    }

    &.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
      margin-right: 0.4737vw;

      &:last-of-type {
        margin-right: 0;
      }
    }

    & .swiper-pagination-bullet-active {
      background: $color-gradient-blue;
    }

    @include tablet {
      // display: none;
    }
  }

  @keyframes open-select {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }
}
