.partners__title {
  padding-left: 28px;
  padding-right: 28px;

  @include desktop-medium {
    padding-left: 1.4vw;
    padding-right: 1.4vw;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      margin-bottom: 0;
    }
  }

  @include tablet {
    padding-left: 0;
    padding-right: 0;
  }

  @include mobile-wide {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.partners__container {
  padding-left: 5px;
  padding-right: 5px;
  max-width: auto;

  @include mobile-wide {
    padding-top: 16px;
    padding-left: 0;
    padding-right: 0;
  }
}

.partners__slider-wrap {
  position: relative;
  max-width: 1495px;
  margin-left: auto;
  margin-right: auto;

  @include desktop-medium {
    max-width: 78.6vw;
  }

  @include tablet {
    width: calc(100vw - 130px);
    max-width: 1495px;
  }

  @include mobile-wide {
    width: 100%;
  }
}

.partners__slider .swiper-slide {
  @include mobile-wide {
    width: 228px;
  }
}

.partners__slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 284px;
  height: 126px;
  padding-left: 28px;
  box-sizing: border-box;

  &--gasp {
    padding-left: 60px;
    padding-bottom: 20px;
  }

  &--rosatom {
    padding-left: 20px;
    padding-top: 5px;
  }

  @include desktop-medium {
    width: 14.9vw;
    height: 6.6vw;
    padding-left: 1.4vw;

    &--gasp {
      padding-left: 3.1vw;
      padding-bottom: 1vw;
    }

    &--rosatom {
      padding-left: 1vw;
      padding-top: 5px;
    }
  }

  @include tablet {
    width: 153px;
    height: 68px;
    padding-left: 28px;
    max-width: 100%;

    &--gasp {
      padding-left: 60px;
      padding-bottom: 20px;
    }

    &--rosatom {
      padding-left: 20px;
      padding-top: 5px;
    }
  }

  @include mobile-wide {
    max-width: none;
    width: 228px;
    height: 100px;
    padding-left: 0;

    &--sber {
      padding-left: 6px;
    }

    &--rosatom {
      padding-top: 0;
      padding-bottom: 6px;
    }

    & img {
      width: 173px;
    }

    &--sber img {
      width: 173px;
      height: 56px;
    }

    &--gasp img {
      width: 135px;
      height: 64px;
    }

    &--rosatom img {
      width: 160px;
      height: 60px;
    }
  }
}

.partners .slider-button {
  &--prev {
    left: -52px;
    top: 35px;
  }

  &--next {
    right: -50px;
    top: 35px;
  }

  @include desktop-medium {
    &--prev {
      left: -2.7vw;
      top: 1.8vw;
    }

    &--next {
      right: -2.6vw;
      top: 1.8vw;
    }
  }

  @include tablet {
    &--prev {
      left: -52px;
      top: 35px;
    }

    &--next {
      right: -50px;
      top: 35px;
    }
  }

  @include mobile-wide {
    display: none;
  }
}

.desk-norm {
  .partners__title {
    padding-left: 150px;
    padding-right: 28px;
    margin-bottom: 60px;
    @include min-width(768px) {
      padding-top: 60px;
      padding-bottom: 70px;
      padding-left: 150px;
    }
    @include max-width(767px) {
      padding-left: 15px;
      padding-right: 15px;
    }
    @include max-width(1023px) {
      padding-left: 0;
      padding-right: 0;
    }
    @include max-width(1899px) {
      padding-left: 1.4vw;
      padding-right: 1.4vw;
    }
    @include min-width(320px) {
      margin-bottom: 0;
    }
  }
}
