.title {
  margin: 0 0 30px;
  font-family: $font-bebasneue;
  font-size: 40px;
  line-height: 43px;
  font-weight: normal;

  @include desktop-medium {
    margin: 0 0 1.9vw;
    font-size: 1em;
    line-height: 1.075;
  }

  @include tablet {
    margin: 0 0 30px;
    font-size: 40px;
    line-height: 43px;
  }

  @include mobile-wide {
    font-size: 24px;
    line-height: 24px;
  }

  @include tablet {
    margin-bottom: 9px;
    font-size: 24px;
    line-height: 24px;
  }
}
