.markTb__cables_tb {
  .markTb__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: flex-start;

    @include max-width(654px) {
      justify-content: center;
    }

    .cable {
      @include min-width(320px) {
        width: calc(((100% - 80px) / 4));
        // min-width: 290px;
        margin: 0 9px 30px; // sec auto
      }
      @include min-width(1277px) {
        // margin: 0 2px 30px; // sec auto
      }

      @include min-width(700px) {
        // margin: 0 20px 30px 0;
      }

      @include max-width(1024px) {
        width: calc(((100% - 60px) / 3));
      }
      @include max-width(838px) {
        width: calc(((100% - 60px) / 2));
        // max-width: 290px;
        min-width: 290px;
      }
    }
  }
}
.markTb__container {
  .select-scope-application[data-hidden="false"] {
    .select-scope-application__options {
      position: absolute;
    }
  }
}
.markTb__presentation {
  // padding-top: 20px !important;
}
.markTb__cables_list {
  font-size: get-vw(20px);

  @include max-width(992px) {
    display: none;
  }

  & > p {
    margin: 0;
  }
  .tb__item {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    background: #33333d;
    padding: get-vw(21px) get-vw(50px) get-vw(17px);
    font-family: $font-merel;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: get-vw(20px);
    &:hover {
      background: #494957;
    }
    .coordinat {
      padding-left: get-vw(30px);
      display: flex;
      justify-content: space-between;
      width: get-vw(810px);
    }
    .param_strench {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .value-padd {
      padding-left: get-vw(20px);
    }

    .tb__item_title {
      font-family: $font-bebasneue;
      font-weight: normal;
      font-size: get-vw(25px);
      line-height: 120%;
      & > span {
        color: #c4c4c4;
      }
      & > h2 {
        font-size: get-vw(25px);
      }
    }

    .tb__item_left-param-block,
    .tb__item_right-param-block {
      padding: get-vw(5px) 0px get-vw(5px);
      display: flex;
      justify-content: space-between;
    }
    .tb__item_right-uses-block {
      padding: get-vw(4px) 0px get-vw(4px);
    }
    .tb__item_right-prise-block {
      padding-left: 0px;

      .item__price {
        color: #ed8528;
        font-size: get-vw(25px);
        line-height: get-vw(25px);
        letter-spacing: 0.03em;
        font-family: $font-bebasneue;
        padding-top: get-vw(4px);
      }
    }
    .tb__item_btn {
      background: #0075d1;
      border-radius: 100px;
      border: none;
      color: white;
      max-height: 52px;
      padding: get-vw(13px) get-vw(60px) get-vw(12px);
      text-transform: uppercase;
      font-size: get-vw(20px);
      font-family: $font-bebasneue;
      margin-top: get-vw(5px);
    }
  }
}
.cables__tb_mobile {
  .cable__mobile {
    display: block;
    width: 290px;

    margin: 0 auto 20px;

    @include min-width(992px) {
      display: none;
    }
    .cable__header-category {
      text-align: left;
      width: 169px;
    }
    .cable__footer_btn-area {
      background: #33333d;
      display: flex;

      .cable__footer_btn {
        color: white;
        background: #0075d1;
        border-radius: 30px;
        border: none;
        font-size: 16px;
        font-family: $font-bebasneue;
        padding: 16px 68px 13px;
        margin: 0 auto;
        margin-top: 11px;
        margin-bottom: 10px;
      }
    }
  }
}
