.button {
  padding: 19px 66px 14px;
  font-family: $font-bebasneue;
  font-size: 20px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  background: $color-gray-blue-input;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 100px;
  border: 1px solid $color-blue;
  cursor: pointer;

  @include hover-focus-color ($color-blue);

  &:nth-child(2n) {
    margin-right: 0;
  }
}
