.map-of-site {
  @include min-width(320px) {
    font-family: $font-merel;
    padding-bottom: 30px;
  }
  @include min-width(992px) {
    padding-bottom: 90px;
  }
  .map-of-site__container {
    @include min-width(320px) {
      padding: 0;
      margin: 0px 0px 0px 0px;
    }
    @include min-width(992px) {
      padding: 0px 120px 0px 120px;
    }

    .map-of-site__container_content {
      @include min-width(320px) {
        margin: 0px 0px 0px 15px;
      }
      @include min-width(768px) {
        margin: 0px 0px 0px 50px;
      }
      @include min-width(1400px) {
        margin: 0px 0px 0px 80px;
      }

      .search__title {
        @include min-width(320px) {
          padding-top: 30px;
          font-weight: 100;
          font-size: 24px;
          line-height: 107.1%;
          text-transform: uppercase;
          font-family: $font-bebasneue;
        }
        @include min-width(768px) {
          padding-top: 54px;
          font-size: 30px;
        }
        @include min-width(992px) {
          font-size: 50px;
        }
      }

      .map-of-site__content {
        @include min-width(320px) {
          font-family: $font-merel;
          font-size: 18px;
          line-height: 32px;
          padding-top: 20px;
          width: 80vw;
          display: flex;
          justify-content: space-between;
          text-decoration: none;
          flex-direction: column;
        }
        @include min-width(768px) {
          font-size: 25px;
        }
        @include min-width(992px) {
          flex-direction: row;
          padding-top: 60px;
          font-size: 20px;
          margin: 0px;
          font-size: 27px;
          font-weight: 900;
        }
        @include min-width(1600px) {
          font-size: 27px;
        }
        a {
          color: white;
          padding-bottom: 15px;
          display: block;
        }
        .content__secondary {
          padding-left: 20px;
          padding-bottom: 10px;
          font-weight: 100;

          a {
            @include min-width(320px) {
              font-size: 14px;
              color: #e5e5e5;
              padding-bottom: 5px;
              display: block;
            }
            @include min-width(768px) {
              font-size: 20px;
            }
            @include min-width(992px) {
              font-size: 20px;
              padding-bottom: 8px;

            }
            @include min-width(1600px) {
              font-size: 27px;
            }
          }
        }
      }
    }
  }
}
