.sertificate__wrapper {
  margin-bottom: 33px;
}

.integrate__item--patents {
  & .sertificate__img {
    @include min-width (768px) {
      opacity: 0;
    }

    @include max-width (767px) {
      opacity: 1;
    }
  }
}

.sertificate__container {
  max-width: 1452px;
  padding-left: 15px;
  padding-top: 52px;

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 8px;
    }
  }

  @include mobile-wide {
    padding-top: 52px;
  }
  
  @include mobile {
    padding-left: 0;
    padding-top: 20px;
  }

  @include min-width(481px) {
    padding-left: 0;
  }
}

.sertificate__img {
  position: relative;
  min-width: 208px;
  min-height: 298px;
  max-height: 450px;
  max-width: 314px;
  padding: 2vh 0.6316vw;
  background-color: $color-white;
  border-radius: 10px;
  box-sizing: border-box;
  cursor: pointer;

  @include tablet {
    width: 208px;
    flex-shrink: 0;
  }

  @include min-width(1024px) {

    &:hover .sertificate__img-full-size,
    &:focus .sertificate__img-full-size {
      opacity: 1;
    }
  }

  &:last-of-type {
    margin-right: 0;
  }
}

.sertificate__img-full-size {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 10px;
  background: linear-gradient(0deg, rgba(58, 58, 69, 0.5), rgba(59, 59, 71, 0.5)), linear-gradient(0deg, rgba(42, 50, 56, 0.5), rgba(42, 50, 56, 0.5));
  opacity: 0;
  transition: opacity $style-change-duration $style-change-function;

  & button {
    position: relative;
    width: 87.8%;
    padding: 0.9vw 2.6vh 0.74vw;
    font-family: $font-bebasneue;
    font-size: 1.563em;
    line-height: 21px;
    text-align: center;
    border: 2px solid $color-blue;
    background: transparent;
    border-radius: 100px;
    color: $color-white;
    outline: none;
    box-sizing: border-box;
    transition: all $style-change-duration $style-change-function;

    @include desktop-medium {
      font-size: 0.63em;
    }

    & svg {
      position: absolute;
      width: 17px;
      height: 17px;
      top: 50%;
      right: 30px;
      transform: translateY(-50%);
    }

    &:hover,
    &:focus {
      background: $color-blue;
    }
  }
}

.sertificate__slider-pagination {
  display: flex;
  justify-content: center;

  &.swiper-pagination.swiper-pagination-bullets {
    position: relative;
    bottom: 0;
    left: -4px;
  }

  & span {
    margin: 0;
    width: 81px;
    height: 7px;
    border-radius: 25px;
    background-color: $color-gray;
    opacity: 1;
    outline: none;
  }

  &.swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0;
    margin-right: 9px;

    &:last-of-type {
      margin-right: 0;
    }
  }

  & .swiper-pagination-bullet-active {
    background: $color-gradient-blue;
  }

  @include tablet {
    display: none;
  }
}

.patents--animate {
  & .sertificate__slider-pagination {
    animation: pagination-opacity 1.4s ease-in-out;
  }
}

.patents--reverse {
  & .sertificate__slider-pagination {
    animation: pagination-opacity-reverse 1.4s ease-in-out;
    animation-direction: reverse;
  }
}


.sertificate__slider--hidden {
  display: none;
}
