.advantages {
  margin-bottom: 0;
  flex-grow: 1;
  // overflow: hidden;

  @media (max-height: 999px) {
    font-size: 2.9vh;
  }

  @include desktop-small {
    @media (max-height: 767px) {
      font-size: 2.9vh;
    }

    @media (max-height: 399px) {
      font-size: 2.1vw;

      .main {
        position: static;
      }
    }
  }
}

.advantages__heading {
  font-family: $font-bebasneue;
  font-size: 40px;
  font-weight: 400;
  padding-top: 20px;
  position: absolute;
  top: 3px;
  left: 92px;
  z-index: 10;

  @include desktop-medium {
    font-size: 1.8em;
    padding-top: 1vw;
    top: 0.1vw;
    left: 12.2vw;
  }

  @include desktop-small {
    font-size: 1.5em;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 18px;
      margin-top: 0;
    }
  }
  
  @include tablet {
    font-size: 24px;
    position: relative;
    left: auto;
  }

  @include mobile-wide {
    position: relative;
    left: auto;
    font-size: 24px;
    margin-bottom: 20px;
  }

  @include mobile {
    text-align: left;
  }
}

.advantages__wrapper {
  display: flex;
  justify-content: space-between;

  @include tablet {
    justify-content: space-around;
    align-content: space-around;
  }

  @include tablet {
    @media (max-height: 649px) {
      padding-bottom: 120px;
    }
  }

  @include mobile-wide {
    padding-bottom: 0;
  }
}

@keyframes animation_rotate-8-reverse {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.advantages__drum-wrapper {
  position: relative;
  width: 967px;
  height: 730px;
  min-width: 967px;
  margin-left: 73px;

  @media (max-height: 999px) {
    font-size: 2.1vh;
    width: 90vh;
    height: 68.4vh;
    min-width: 0;
    margin-left: 3.8vh;
  }

  @include desktop-medium {
    font-size: 2.1vw;
    width: 50.8vw;
    height: 38.4vw;
    min-width: 50.8vw;
    margin-left: 3.8vw;

    @media (max-height: 999px) {
      font-size: 2.1vh;
      width: 84vh;
      height: 63.5vh;
      min-width: 84vh;
      margin-left: 3.8vh;
    }
  }

  @include desktop-small {

    @media (max-height: 999px) {
      font-size: 2.1vh;
      width: 84vh;
      height: 63.5vh;
      min-width: 84vh;
      margin-left: 3.8vh;
    }
  }

  @media (max-width: 1359px) {

    @media (max-height: 999px) {
      font-size: 2.1vh;
      width: 70vh;
      height: 53vh;
      min-width: 70vh;
      margin-left: 3.8vh;
    }
  }

  @media (max-width: 1100px) {

    @media (max-height: 999px) {
      font-size: 2.1vh;
      width: 60vh;
      height: 45.3vh;
      min-width: 60vh;
      margin-left: 3.8vh;
    }
    @media (max-height: 767px) {
      font-size: 2.1vh;
      width: 60vh;
      height: 45.3vh;
      min-width: 60vh;
      margin-left: 3.8vh;
    }
  }

  @include tablet {
    margin-left: 0;
    margin-top: 0;
    width: 450px;
    height: 340px;
    min-width: 450px;
  }

  @include mobile-wide {
    display: none;
  }

  .advantages__drum-after {
    position: absolute;
    left: 0;
    top: -5.7%;
    width: 100%;
    height: 129%;
    z-index: -2;
    // overflow: hidden;

    &::after {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 102.6%;
      content: "";
      border-radius: 50%;
      background-color: rgba(42, 42, 50, 0.5);
      background-image: url("../img/drum-border.svg");
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
    }

    @include desktop-medium {
      &::after {
        background-size: contain;
      }
    }

    @include tablet {
      width: 100%;
      height: 132.4%;
      left: -15%;

      &::after {
        height: 100%;
        background-size: cover;
      }
    }
  }
}

.advantages__drum-wrapper-2 {
  position: absolute;
  width: 100%;
  height: 110%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 1;

  @include tablet {
    height: 100%;
    overflow: visible;
  }
}

.advantages__drum-wrapper--animate {
  animation-name: animation_rotate-8-reverse;
  animation-fill-mode: both;
  animation-duration: 1s;
}

.advantages__drum {
  position: absolute;
  left: 0;
  top: -5.7%;
  width: 100%;
  height: 120.3%;
  border-radius: 50%;
  background-image: url("../img/drum-center.svg"), url("../img/drum-background.svg");
  background-repeat: no-repeat;
  background-position: center center, center center;

  @include desktop-medium {
    background-size: 10% 10.9%, 40% 40%;
  }

  @include tablet {
    width: 100%;
    height: 132.4%;
    left: -15%;
  }
}

.advantages__switch {
  width: 410px;
  padding-right: 12px;

  @include desktop-medium {
    width: 21.5vw;
    padding-right: 0.6vw;
  }

  @include tablet {
    width: 300px;
    margin: 0;
    padding-right: 0;
  }

  @include mobile-wide {
    width: 100%;
    max-width: 500px;
    margin-right: auto;
  }

  @include mobile {
    margin: 0 auto;
    padding-right: 0;
  }
}

.advantages--animate .advantages__heading {
  animation-name: animation_mainlinks;
  animation-fill-mode: both;
  animation-duration: 1s;
}

.advantages--reverse .advantages__heading {
  animation-name: animation_mainlinks-reverse;
  animation-fill-mode: both;
  animation-duration: 1s;
}

