.desk-norm {
  .integrate__item--catalog {
    & .cable {
      opacity: 0;

      @include max-width(767px) {
        opacity: 1;
      }
    }
  }

  .cable {
    width: calc((100% - 30px) / 4);
    max-width: 435px;
    border-radius: 9px;
    background-color: #24242b;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);

    @include tablet {
      opacity: 1;
      min-width: 248px;
    }
  }

  .cable__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.9vh 1.1vw;
    font-family: $font-bebasneue;
    background: #33333d;
    text-align: center;
    border-bottom-left-radius: 9px;
    border-bottom-right-radius: 9px;
    box-shadow: 0 1px 5px rgba(0, 17, 31, 0.3);
    box-sizing: border-box;
    color: white;

    @include min-width(992px) {
      padding: get-vw(21px) get-vw(20px) get-vw(17px);
    }

    & .cable__header-wrapper {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      @include min-width(320px) {
        font-size: 17px;
        line-height: 120%;
        max-width: 180px;
      }
      @include min-width(992px) {
        font-size: get-vw(25px);
        line-height: get-vw(29px);
        max-width: none;
      }
      @include min-width(1900px) {
        font-size: 25px;
        line-height: 29px;
      }
    }

    & .cable__header-category {
      color: $color-gray;
      // font-size: 1.563em;
      // line-height: 30px;

      @include desktop-medium {
        // font-size: 0.6269em;
      }

      & span {
        display: none;
      }

      // @include laptop {
      //   font-size: 16px;
      //   line-height: 18px;
      // }

      // @include laptop {
      //   font-size: 16px;
      //   line-height: 18px;
      // }

      @include mobile {
        & span {
          display: inline;
        }
      }
    }

    & .cable__title {
      margin: 0;
      // font-size: 1.563em;
      // line-height: 30px;

      @include desktop-medium {
        // font-size: 0.6269em;
      }

      @include min-width(320px) {
        font-size: 15px;
        line-height: 18px;
        font-weight: normal;
      }
      @include min-width(992px) {
        font-size: get-vw(25px);
        line-height: 120%;
      }
      @include min-width(1900px) {
        font-size: 25px;
        line-height: 29px;
      }
    }

    .cable__cost {
      @include min-width(320px) {
        font-size: 20px;
        display: flex;
        flex-direction: column;
        // align-items: flex-start;
        align-items: center;
        min-width: 64px;
      }
      @include min-width(992px) {
        font-size: get-vw(20px);
      }

      & .cable__price-title {
        text-transform: capitalize;
        font-family: $font-merel;

        @include min-width(320px) {
          font-size: 13px;
        }
        @include min-width(768px) {
          font-size: 20px;
        }
      }

      & .cable__price {
        @include min-width(320px) {
          // display: none;
          font-family: $font-bebasneue;
          line-height: 35px;
          letter-spacing: 0.03em;
          color: $color-orange;
          // display: block;
          font-size: 20px;
        }
        @include min-width(992px) {
          font-size: get-vw(35px);
        }

        @include min-width(1900px) {
          font-size: 35px;
        }
        // @include min-width(1200px) {
        //   font-size: 28px;
        // }

        // @include mobile {
        //   display: block;
        //   font-size: 20px;
        //   line-height: 20px;
        //   letter-spacing: 0.03em;
        //   flex-shrink: 0;
        // }

        & span {
          font-family: -apple-system, "Helvetica", sans-serif;
        }
      }
    }

    &.cable__header--active-price {
      display: flex;
      justify-content: space-between;
      text-align: left;

      & .cable__price {
        display: block;
        width: 100%;
        height: 100%;
      }
    }

    // @include max-width(320px){
    //   font-size: 15px;
    // }

    @include mobile {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 11px 5px 10px 11px;
      text-align: left;
    }
  }

  .cable__img-container {
    position: relative;
    cursor: pointer;
    border-radius: 9px 9px 0 0;

    & .cable__img-get-price {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      height: calc(100% - 40px);
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      opacity: 0;
      z-index: 1;
      border-radius: 6px 6px 0 0;
      transition: opacity $style-change-duration $style-change-function;

      & .cable__img-btn {
        position: relative;
        width: 220px;
        // padding: 1.5vh 1.09vw 1.4vh;
        padding: get-vw(23px) get-vw(30px) get-vw(21px);
        font-family: $font-bebasneue;
        font-size: get-vw(27px);
        line-height: 21px;
        text-align: center;
        border: 2px solid $color-blue;
        background: transparent;
        border-radius: 100px;
        color: $color-white;
        transition: all $style-change-duration $style-change-function;
        outline: none;
        transform: translateY(20px);

        @include desktop-medium {
          // font-size: 0.502em;
        }

        @include max-width(1700px) {
          width: 150px;
        }

        &:hover,
        &:focus {
          background: $color-blue;
        }
      }

      @include min-width(1024px) {
        &:hover,
        &:focus {
          opacity: 1;
          z-index: 20;
        }

        &:hover + .cable__img-overlay,
        &:focus + .cable__img-overlay {
          opacity: 1;
          z-index: 10;
        }
      }
    }

    @include mobile {
      max-height: 242px;

      &:hover .cable__img-get-price,
      &:focus .cable__img-get-price {
        display: none;
      }
    }

    & .cable__img-overlay {
      position: absolute;
      opacity: 0;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: -1;
      transition: opacity $style-change-duration $style-change-function;
      background: linear-gradient(0deg, rgba(59, 59, 71, 0.5), rgba(59, 59, 71, 0.5)),
        linear-gradient(0deg, rgba(42, 50, 56, 0.5), rgba(42, 50, 56, 0.5));
    }
  }

  .cable__img-pagination.swiper-pagination.swiper-pagination-clickable {
    bottom: 0;
  }

  .cable__img-pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 40px;

    & span {
      margin: 0;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: $color-white;
      opacity: 1;
      border: 3px solid $color-gray;
      outline: none;

      @include laptop {
        width: 5px;
        height: 5px;
      }
    }

    &.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
      margin-right: 7px;

      &:last-of-type {
        margin-right: 0;
      }
    }

    & .swiper-pagination-bullet-active {
      border-color: #2c89d2;
      background: #2c89d2;
    }
  }

  .cable__img-items {
    padding: 57px 0 81.55px 0;
    background-color: white;

    & img {
      display: block;
    }

    #cable__img-cabel2 {
      padding-top: 46px;
      padding-bottom: 27px;
    }
    #cable__img-cabel1 {
      @include min-width(320px) {
        padding-top: 4px;
        padding-bottom: 11px;
      }
      @include min-width(992px) {
        padding-top: 4px;
        padding-bottom: 10px;
      }
      @include min-width(1600px) {
        padding-top: 6px;
      }
      @include min-width(1800px) {
        padding-top: 9px;
      }
    }
  }
  .cable__img-steel {
    // padding-top: -20px;
    padding: 0;
    padding-bottom: 66.07px;
  }

  .cable__footer {
    padding: 2vh 1.6vw;
    font-size: 0.9375em;
    line-height: 16px;

    & .cable__table {
      font-size: 15px;
      width: 100%;
      font-family: $font-merel;
      font-weight: 500;

      @include min-width(320px) {
        font-size: 13px;
      }
      @include min-width(768px) {
        font-size: 15px;
      }
    }

    @include desktop-medium {
      font-size: 0.3762em;
    }

    @include laptop {
      padding: 20px 15px;
      // font-size: 10px;
      line-height: 14px;
    }

    @include mobile {
      padding: 5px 10px;
    }
  }

  .cable__table {
    & tr {
      padding: 0;
    }

    & td {
      padding: 0;
      padding-top: 5px;
    }

    & tr td:last-child {
      display: flex;
      justify-content: flex-end;
      font-family: $font-merel;
      font-weight: 400;
    }
  }

  .cable__tr-hidden {
    display: none;

    @include mobile {
      display: table-row;
    }
  }

  .cable__uses {
    display: flex;
  }

  .cable__uses-item {
    position: relative;
    margin-right: 5px;
    cursor: pointer;

    &:last-child {
      margin-right: 0;
    }

    & svg {
      @include min-width(1200px) {
        width: 32px;
        height: 32px;
      }
      width: 16px;
      height: 16px;
    }

    & .cable__uses-description {
      position: absolute;
      display: none;
      left: 65%;
      bottom: -40px;
      padding: 7px 15px;
      font-family: $font-roboto;
      font-size: 12px;
      font-weight: 400;
      line-height: 17px;
      letter-spacing: 0.004em;
      background: #5e5e70;
      border-radius: 4px;
      transform: translateX(-50%);
      box-shadow: 0 1px 5px rgba(0, 17, 31, 0.3);
      z-index: 99;

      &::before {
        position: absolute;
        content: "";
        top: -5px;
        left: 50%;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 8px 9px 8px;
        border-color: transparent transparent #5e5e70 transparent;
        transform: translateX(-50%);
      }
    }

    &:hover .cable__uses-description {
      display: block;
    }
  }
}
