.filter-popup{
  span {
    font-size: 16px;
  }
  // width:100%;
  // min-height:100%;
  background-color: rgba(0,0,0,0.5);
  overflow:hidden;
  position:fixed;
  top:0px;
  left: -1px;
  z-index: 999999999;

  @include min-width (320px) {
    width:100%;
    min-height:100%;
  }
  .dropdown-type,
  .dropdown-quantity,
  .dropdown-diametr,
  .dropdown-stretch {
    padding: 14px 40px 16px 10px!important;
  }
  .arrow__down--type,
  .arrow__up--type,
  .reset--type,
  .arrow__down--quantity,
  .arrow__up--quantity,
  .reset--quantity,
  .arrow__down--diametr,
  .arrow__up--diametr,
  .reset--diametr,
  .arrow__down--stretch,
  .arrow__up--stretch,
  .reset--stretch {
    margin: 19px 10px!important;
  }
}
.filter-popup .filter-popup-content{
  display: flex; 
  flex-direction: column;
  height: 80vw;
  width: 80vw;
  padding:25px;
  background-color: #474754;

  @include min-width (320px) {
    width:100vw;
    height:100vh;
    position: absolute;
  }

  .filter-popup__title {
    @include min-width(320px) {
      font-size: 14px;
      line-height: 16px;
      font-weight: 900;
      padding-bottom: 5px;
    
    }
    @include min-width(350px) {
      font-size: 17px;
    }

  }

  .dropdown-type-popup {
    width: 86vw;
  }
  .dropdown-quantity-popup {
    width: 86vw;
  }
  .dropdown-diametr-popup {
    width: 86vw;
  }
  .dropdown-stretch-popup {
    width: 86vw;
  }

  .catalog__filter-confBlock {
    padding-bottom: 15px;

    li {
      padding-top: 20px!important;
      font-size: 16px!important;
      margin-bottom: 10px;
    }
  }



  .filter-popup__btn_close {  
    border: none;
    align-self: flex-end;
    background: transparent;
    margin-bottom: 24px;
    position: relative;
    left: 13px;
    bottom: 5px;
  }
  .filter-popup__btn_apply {
    align-items: center;
    text-align: center;
    font-size: 16px;
    font-family: $font-bebasneue;
    background: transparent;
    color: white;
    border-radius: 30px;
    padding: 16px 64px 14px;
    text-transform: uppercase;
    border: 2px solid #0075D1;
    margin: 0 auto;
    margin-top: 5px;

    @include min-width (320px) {
      width: 190px;
    }
  }
  .filter-popup__btn_reset {
    position: relative;
    top: 80px;
    font-size: 16px;
    font-family: $font-merel;
    border: none;
    color: white;
    padding: 0;
    padding-bottom: 3px;
    margin: 0 auto;
    border-bottom: 1px dashed #FFFFFF;
    background: transparent;

    @include min-width (320px) {
      width: 68px;
    }
  }
}
