.desktop-404-theme {
  background: #363640;
  position: relative;

  .header {
    background: none;
  }
  .footer {
    @include min-width(320px) {
      background: none;
    }
    @include min-width(992px) {
      position: absolute;
      bottom: 0px;
    }
  }

  .desktop-404__background {
    @include min-width(320px) {
      position: absolute;
      top: 0px;
      right: 0px;
      display: none;
    }
    @include min-width(992px) {
      display: block;
      max-width: 1350px;
    }
    @include min-width(1350px) {
      max-width: none;
    }
  }
  .desktop-404__background_mobile {
    @include min-width(320px) {
      margin: 0 auto;
      width: 100%;
      padding: 110px 15px 40px;
    }
    @include min-width(470px) {
      max-width: 440px;
      margin: 0 auto;
      display: block;
    }
    @include min-width(992px) {
      display: none;
    }
  }
}
.desktop-404 {
  @include min-width(320px) {
    // min-height: get-vw(720px);
    min-height: 420px;
    font-family: $font-merel;
  }
  @include min-width(360px) {
    min-height: 480px;
  }
  @include min-width(992px) {
    min-height: 720px;
  }

  .header__search--in-menu {
    width: 400px;
    z-index: 1;

    @include max-width(992px - 1) {
      position: absolute;
      top: get-vw(150px);
      bottom: 0%;
      left: 50%;
      display: block;
      transform: translate(-50%);
      width: 400px;
    }
    @include max-width(600px) {
      width: 290px;
    }

    .header__search-input {
      @include max-width(992px - 1) {
        width: 400px;
      }
      @include max-width(600px) {
        width: 290px;
        font-size: 16px;
        height: 50px;

        &::placeholder {
          font-size: 16px;
        }
      }

      .header__search-button {
        svg {

          width: 10px;
          height: 10px;
        }
      }
    }
  }

  .desktop-404__content {
    @include min-width(320px) {
      padding-bottom: 50px;
    }
    @include min-width(992px) {
      position: absolute;
      top: get-vw(150px);
      bottom: 50%;
      left: get-vw(220px);
      padding-bottom: 0px;
    }
    @include min-width(1281px) {
      top: get-vw(90px);
    }
    & > h2 {
      @include min-width(320px) {
        font-weight: 100;
        font-size: 18px;
        line-height: 21px;
        color: #ed8528;
        text-transform: none;
        padding-bottom: 10px;
        text-align: center;
      }
      @include min-width(600px) {
        font-size: 26px;
        line-height: 26px;
      }
      @include min-width(992px) {
        font-size: 49px;
        line-height: 57px;
        padding-bottom: 30px;
        text-align: left;
      }
    }
    & > p {
      @include min-width(320px) {
        font-size: 14px;
        line-height: 16px;
        color: #c4c4c4;
        text-align: center;
      }
      @include min-width(600px) {
        font-size: 20px;
        line-height: 22px;
      }
      @include min-width(992px) {
        font-size: 27px;
        line-height: 32px;
        text-align: left;
      }
    }
  }
}
.color-white {
  color: white;
}
