.media {
  flex-grow: 1;
  padding-bottom: 65px;

  @include desktop-medium {
    padding-bottom: 3.4vw;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 18px;
      padding-bottom: 0;
    }
  }
  
  @include tablet {
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 65px;
  }

  @include mobile-wide {
    padding-bottom: 20px;
    padding-left: 0;
    padding-right: 0;
  }
}

.media__header {
  display: flex;
}
