.starlink-main {
  .select-modification {
    @include tablet {
      transition: all $style-change-duration $style-change-function;
    }

    & .select-modification__value {
      display: none;

      @include tablet {
        display: block;
        position: relative;
        padding-left: 47px;

        & span {
          margin-right: 20px;
          font-size: 27px;
          line-height: 29px;
          text-transform: uppercase;

          @include tablet {
            font-size: 16px;
            line-height: 16px;
          }
        }

        & svg {
          width: 10px;
          height: 17px;
          margin-left: auto;
          fill: $color-white;
          transition: transform $style-change-duration $style-change-function;
          transform: rotate(90deg);

          @include mobile {
            width: 12px;
            height: 12px;
          }
        }
      }
    }

    @include tablet {
      position: relative;
      width: 100%;
      background-color: $color-gray-blue-input;
      box-shadow: 0 1px 5px $color-box-shadow-input;
      border-radius: 100px;
      cursor: pointer;

      .swiper-wrapper {
        margin-bottom: 0;
      }

      &[data-hidden="true"] {
        .select-modification__value {
          display: flex;
          align-items: center;
          border-radius: 36.5px;
          padding: 17px 20px 13px 20px;
          transition: all $style-change-duration $style-change-function;
          outline: none;

          @include hover-focus-background-color($color-blue);

          @include tablet {
            background-color: $color-gray-blue-input;
            padding: 17px 20px 13px 20px;
          }
        }

        .swiper-wrapper {
          display: none;
        }
      }
    }

    &[data-hidden="false"] {
      border-radius: 21.5px;
      transition: all $style-change-duration $style-change-function;

      .select-modification__value {
        display: flex;
        align-items: center;
        border-top-left-radius: 21.5px;
        border-top-right-radius: 21.5px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        padding: 17px 20px 13px 20px;
        background-color: $color-blue;
        transition: all $style-change-duration $style-change-function;
        outline: none;

        & svg {
          transform: rotate(-90deg);
        }
      }

      .swiper-wrapper {
        display: block;
        position: relative;
        padding-top: 5px;
        padding-bottom: 25px;
        background-color: $color-gray-blue-input;
        border-bottom-left-radius: 21.5px;
        border-bottom-right-radius: 21.5px;
        animation: open-select 0.2s ease-in;
      }
    }
  }

  .select-modification__options {
    display: flex;
    flex-wrap: wrap;

    @include tablet {
      display: block;
      width: auto;
      height: auto;
    }
  }

  .break {
    flex-basis: 100%;
    height: 0;
  }

  .select-modification__item {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    flex-shrink: 1;
    margin-bottom: 2vh;
    margin-right: 0.79vw;
    padding: 0.79vw 1vh 0.7vw;
    font-size: 1.1875em;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    background: $color-gray-blue-input;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 100px;
    border: 1px solid $color-blue;
    cursor: pointer;

    @include desktop-medium {
      font-size: 0.67644em;
    }

    @include hover-focus-color($color-blue);

    &:nth-child(3n) {
      margin-right: 0;
    }

    &[data-active="true"] {
      color: $color-blue;
    }

    @include tablet {
      display: block;
      margin: 0;
      padding: 17px 15px 8px 47px;
      text-transform: uppercase;
      font-size: 27px;
      line-height: 29px;
      text-align: left;
      border-radius: 0;
      box-shadow: none;
      cursor: pointer;

      &[data-active="true"] {
        color: $color-white;
        background-color: $color-darknes-gray;
      }

      @include hover-focus-color($color-white);
      @include hover-focus-background-color($color-darknes-gray);

      @include tablet {
        padding: 17px 20px 13px 20px;
        font-size: 16px;
        line-height: 16px;
        border: none;
      }
    }

    @include laptop {
      margin-right: 5px;
    }
  }

  .select-modification__pagination {
    display: flex;
    justify-content: center;

    & span {
      margin: 0;
      width: 81px;
      height: 7px;
      border-radius: 25px;
      background-color: $color-gray;
      opacity: 1;
      outline: none;
    }

    &.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
      margin-right: 0.4737vw;

      &:last-of-type {
        margin-right: 0;
      }
    }

    & .swiper-pagination-bullet-active {
      background: $color-gradient-blue;
    }

    @include tablet {
      display: none;
    }
  }

  @keyframes open-select {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }
}
