.about-company__links {
  @include min-width(320px) {
    flex-direction: column;
    padding: 0px 15px;
    align-items: flex-start;
  }
  @include min-width(768px) {
    display: flex;
    padding: 0px 0px;
    justify-content: space-between;
    flex-direction: row;
    padding: 3vw 20px;
  }
  @include min-width(1200px) {
    padding: 3vw get-vw(202px);
  }

  & > a {
    @include min-width(320px) {
      padding-bottom: 30px;
      display: block;
      text-transform: uppercase;
      font-family: $font-bebasneue;
      font-size: 30px;
      text-align: left;
      color: white;
      position: relative;
    }
    @include min-width(768px) {
      padding-bottom: 74px;
    }

    &:after {
      @include min-width(320px) {
        left: 1px;
        content: "";
        display: block;
        height: 1px;
        background: white;
        width: 50px;
        right: 1px;
        position: absolute;
        margin-top: 8px;
      }
      @include min-width(768px) {
        left: auto;
      }
    }
  }
}
