$max-height: 900px;
$max-height-small: 700px;
$max-height-laptop: 685px;

.news-list {
  @include list-reset;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-content: stretch;
  padding-top: 52px;

  @include desktop-medium {
    padding-top: 2.7vw;
  }

  @include mobile-wide {
    padding-top: 20px;
  }
}

.integrate .news-list__item {
  opacity: 0;

  @include mobile-wide {
    opacity: 1;
  }
}

.news-list__item.swiper-slide {
  width: 31.33%;
  height: auto;
  position: relative;
  border-radius: 9px;
  background-color: transparent;
  display: flex;
  align-self: stretch;

  @include tablet {
    width: 28%;
    min-width: 252px;
    margin-right: 8px;
    margin-left: 15px;
  }

  @include mobile-wide {
    &::after {
      position: absolute;
      height: 157px;
      background-color: $color-news-hover;
    }
  }
}

.news-list__link {
  width: 100%;
  height: 100%;
  text-decoration: none;
  text-decoration-color: transparent;
  display: flex;
  align-content: stretch;
  flex-direction: column;
  align-self: stretch;
  position: relative;
}

.news-list__img-wrapper {
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: 260px;
  z-index: 1;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, #3b3b47, #3b3b47);
    opacity: 0;
    border-radius: 10px 10px 0 0;
    z-index: 200;
    transition: 0.3s all ease;
  }

  @include min-width(1600px) {
    @media (max-height: $max-height) {
      // height: 8vw;
    }
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      height: 8vw;
    }
  }

  @include laptop {
    display: block;
    @media (max-height: $max-height-laptop) {
      display: none;
    }
  }

  @include desktop-medium {
    height: 13.6vw;

    @media (max-height: $max-height-small) {
      // height: 8vw;
    }
  }

  @include mobile-wide {
    display: block;
    height: 157px;
  }
}

.news-list__img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
}

.news-list__item:hover .news-list__img-wrapper::after {
  opacity: 0.8;
}

.news-list__item:hover .news-list__heading {
  color: $color-card-hover;
}

.news-list__heading {
  border-radius: 0 0 9px 9px;
  margin: 0;
  padding: 3px 10px;
  min-height: 115px;
  background-color: $color-gray-background;
  display: flex;
  position: relative;
  text-align: center;
  flex-grow: 1;
  transition: 0.3s all ease;
  color: $color-white;

  @include min-width(1600px) {
    @media (max-height: $max-height) {
      border-radius: 10px 10px 0 0;
    }
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      border-radius: 10px 10px 0 0;
    }
  }

  @include laptop {
    @media (max-height: $max-height-laptop) {
      border-radius: 10px 10px 0 0;
    }
  }

  @include desktop-medium {
    padding: 0.1vw 0.5vw;
    min-height: 6vw;
  }

  @include tablet {
    min-height: 70px;
  }

  @include mobile-wide {
    min-height: 108px;
    border-radius: 0;
  }
}

.news-list__item h3 {
  width: 100%;
  font-family: $font-merel;
  font-size: 20px;
  line-height: 27px;
  font-weight: 400;
  text-align: center;
  margin: 0;
  align-self: center;
  padding-bottom: 7px;
  text-decoration: none;
  flex-grow: 1;

  @include desktop-medium {
    font-size: 0.5em;
    line-height: 1.35;
    padding-bottom: 0.3vw;
  }

  @include laptop {
    font-size: 14px;
    line-height: 19px;
  }

  @include tablet {
    font-size: 14px;
    line-height: 19px;
  }

  @include mobile-wide {
    line-height: 20px;
    padding-top: 0;
    padding-bottom: 4px;
  }
}

.news-list__annotaion {
  font-family: $font-merel;
  border-radius: 0 0 9px 9px;
  font-size: 17px;
  line-height: 23px;
  margin: 0;
  position: relative;
  padding: 18px 11px;
  background-color: $color-darknes-gray;
  color: $color-gray;
  padding-bottom: 24px;

  p {
    margin: 0;
    padding: 0;
    text-decoration: none;
    overflow: hidden;
    overflow-y: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    flex-direction: column;
  }

  &::before {
    position: absolute;
    content: "";
    top: -20px;
    left: 0;
    width: 100%;
    height: 20px;
    background-color: $color-darknes-gray;
    z-index: -1;
  }

  @include desktop-medium {
    border-radius: 0 0 0.4vw 0.4vw;
    font-size: 0.425em;
    line-height: 1.352;
    padding: 0.9vw 0.5vw;
    padding-bottom: 1.2vw;

    &::before {
      top: -1vw;
      height: 1vw;
    }
  }

  @include laptop {
    font-size: 13px;
    line-height: 18px;
  }

  @include tablet {
    font-size: 13px;
    line-height: 18px;
  }

  @include mobile-wide {
    display: none;
  }
}

.news-list__item-bottom {
  border-radius: 0 0 9px 9px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: $color-news-background;
  font-family: $font-merel;
  font-size: 17px;
  line-height: 20px;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: -20px;
    left: 0;
    width: 100%;
    height: 20px;
    background-color: $color-news-background;
    z-index: -2;
  }

  @include desktop-medium {
    border-radius: 0 0 0.4vw 0.4vw;
    font-size: 0.425em;
    line-height: 1.176;

    &::before {
      top: -1vw;
      height: 1vw;
    }
  }

  @include laptop {
    font-size: 13px;
    line-height: 15px;
  }

  @include tablet {
    font-size: 13px;
    line-height: 15px;
    min-height: 36px;
  }
}

.news-list__read-more {
  color: $color-orange;
  text-decoration: none;
  margin-top: 10px;
  margin-left: 20px;

  @include desktop-medium {
    margin-top: 0.5vw;
    margin-left: 1vw;
  }

  @include tablet {
    margin-top: 10px;
  }

  @include mobile-wide {
    margin-top: 8px;
    margin-left: 11px;
  }
}

.news-list__date {
  color: $color-gray;
  margin-top: 10px;
  margin-right: 20px;

  @include desktop-medium {
    margin-top: 0.5vw;
    margin-right: 1vw;
  }

  @include tablet {
    margin-top: 10px;
    margin-right: 20px;
  }

  @include mobile-wide {
    margin-top: 8px;
    margin-right: 6px;
  }
}
