.gallery-popup {
  background: rgba(42, 42, 50, 0.92);
  overflow: hidden;
  position: fixed;
  top: 0;
  left: -1px;
  z-index: 999999999;
  width: 100%;
  height: 100%;

  .gallery-popup__content {
    margin: 0 auto;
    width: 67vw;
    height: 40vw;
    margin-top: 11vh;
    .gallery-popup__content_img {
      width: 100%;
      height: 100%;
      margin: 0 auto;
    }
    .popup__galley_close {
      background: 0 0;
      border: 0;
      position: absolute;
      top: calc(10% - 33px);
      margin-left: 30px;
    }
  }
}
