.starlink-main {
  .choose-other {
    padding-left: 6.64vw;
    font-family: $font-bebasneue;
    font-weight: 400;
    flex: 0 1 53%;

    @include min-width(1600px) {
      min-width: 730px;
    }

    @include desktop-small {
      min-width: 532px;
      padding-left: 70px;
    }

    @include laptop {
      min-width: 532px;
      padding-left: 30px;
    }

    @include tablet {
      min-width: 100%;
      padding: 0;
      padding-top: 30px;
    }
  }

  .choose-other__scope-application {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 5vh;

    & p {
      width: 100%;
      margin: 0;
      margin-bottom: 3.0457vh;
      font-size: 2.5em;
      line-height: 43px;
      text-transform: uppercase;

      @include desktop-medium {
        font-size: 1.004em;
      }

      @include tablet {
        font-size: 24px;
        line-height: 24px;
        margin-bottom: 9px;
      }
    }

    @include tablet {
      margin-bottom: 30px;
    }
  }

  .choose-other__modification {
    & p {
      margin: 0;
      margin-bottom: 2vh;
      font-size: 2.5em;
      line-height: 43px;
      letter-spacing: 0.03em;

      @include desktop-medium {
        font-size: 1.004em;
      }

      @include tablet {
        font-size: 24px;
        line-height: 24px;
        margin-bottom: 9px;
        letter-spacing: 0;
      }
    }

    @include tablet {
      max-width: 100%;
    }
  }

  .choose-other__modification-slider {
    &--short {
      display: none;
    }

    @include min-width(1600px) {
      @media (max-height: 790px) {
        &--short {
          display: block;
        }

        &--tall {
          display: none;
        }
      }
    }

    @include desktop-medium {
      @media (max-height: 790px) {
        &--short {
          display: block;
        }

        &--tall {
          display: none;
        }
      }
    }

    @include laptop {
      &--short {
        display: none;
      }

      &--tall {
        display: block;
      }

      @media (max-height: 650px) {
        &--short {
          display: block;
        }

        &--tall {
          display: none;
        }
      }
    }

    @include tablet {
      width: 100%;

      &--short {
        display: none;
      }

      &--tall {
        display: block;
      }
    }
  }

  .choose-other__form-item {
    & .swiper-wrapper {
      @include min-width(768px) {
        min-width: 730px;
        margin-bottom: 1.2vh;
      }
    }

    @include tablet {
      overflow: visible;
    }
  }
}
