.about-us {
  width: 56.48%;
  margin-right: auto;

  @include tablet {
    width: 50%;
  }

  @include mobile-wide {
    width: auto;
    margin-right: 0;
  }
}

.about-us__title {
  margin-left: 92px;
  font-weight: normal;

  @include desktop-medium {
    font-size: 1em;
    line-height: 1;
    margin-left: 4.8vw;
  }

  @include tablet {
    font-size: 24px;
    margin-left: 0;
  }

  @include mobile-wide {
    margin-left: 0;
    margin-bottom: 15px;
  }
}

.about-us__container {
  padding-bottom: 106px;

  @include desktop-medium {
    @media (max-height: 800px) {
      padding-bottom: 0;
    }
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-bottom: 0;
    }
  }

  @include tablet {
    padding-bottom: 30px;
  }

  @include mobile-wide {
    padding-bottom: 0;
  }
}

.about-us__slider-wrap {
  position: relative;
  width: 895px;
  margin-left: 0;
  margin-right: 0;
  padding-top: 95px;

  @include desktop-medium {
    width: 47.1vw;
    padding-top: 5.5vw;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      padding-top: 5.5vw;
    }
  }
  
  @include tablet {
    width: auto;
    min-width: auto;
    padding-top: 0;
  }

  @include mobile-wide {
    width: auto;
    padding-top: 0;
  }
}


.about-us__slider {
  @include mobile-wide {
    overflow: visible;
  }
}

.about-us__slider .swiper-wrapper {
  @include mobile-wide {
    display: block;
  }
}

.about-us__slider .swiper-slide:not(.swiper-slide-active) {
  opacity: 0 !important;

  @include mobile-wide {
    opacity: 1 !important;
  }
}

.about-us__slide {
  @include mobile-wide {
    padding-bottom: 29px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    text-align: center;
    background-repeat: repeat-x;
    background-position: 80% 26px;
    background-size: 553px 20px;
    background-image: url("../img/wave-orange-mobile.svg");

    .swiper-slide:first-child & {
      background-position: 80% 26px;
      background-image: url("../img/wave-orange-mobile.svg");
    }

    .swiper-slide:nth-child(2) & {
      background-position: 13% 30px;
      background-image: url("../img/wave-blue-mobile.svg");
    }

    .swiper-slide:nth-child(3) & {
      background-position: 86% 33px;
      background-image: url("../img/wave-gray-mobile.svg");
    }

    &--last {
      padding-bottom: 21px;
    }
  }
}

.about-us__numbers,
.about-us__text {
  animation-fill-mode: both !important;
}

$slidesAnimationFunction: ease-in-out;

$slidesCurrentAnimationTime: 0.5s;

$slidesNextAnimationTime: 0.8s;

.current .about-us__numbers {
  animation: slideLeftCurrent $slidesCurrentAnimationTime $slidesAnimationFunction;
}

.next .about-us__numbers {
  animation: slideLeftNext $slidesNextAnimationTime $slidesAnimationFunction;
}

.current-rev .about-us__numbers {
  animation: slideRightCurrent $slidesCurrentAnimationTime $slidesAnimationFunction;
}

.next-rev .about-us__numbers {
  animation: slideRightNext $slidesNextAnimationTime $slidesAnimationFunction;
}

.current .about-us__text {
  animation: slideLeftCurrent $slidesCurrentAnimationTime $slidesAnimationFunction;
  animation-delay: 0.2s;
}

.next .about-us__text {
  animation: slideLeftNext $slidesNextAnimationTime $slidesAnimationFunction;
  animation-delay: 0.2s;
}

.current-rev .about-us__text {
  animation: slideRightCurrent $slidesCurrentAnimationTime $slidesAnimationFunction;
  animation-delay: 0.2s;
}

.next-rev .about-us__text {
  animation: slideRightNext $slidesNextAnimationTime $slidesAnimationFunction;
  animation-delay: 0.2s;
}

.about-us .slider-button {
  right: -43px;
  top: 123px;

  &--prev {
    top: 183px;
  }

  @include desktop-medium {
    right: -2.2vw;
    top: 6.4vw;

    &--prev {
      top: 9.6vw;
    }
  }

  @include tablet {
    top: 10px;
    width: 11px;
    height: 24px;

    &--prev {
      top: 35px;
    }
  }

  @include mobile-wide {
    display: none;
  }
}

.about-us .slider-button--prev .slider-button__icon {
  stroke: #666666;
}

.about-us__wave {
  position: absolute;
  z-index: -1;
  top: 43%;
  right: -126px;
  stroke: #ed8528;
  transform: translate(0, -50%);
  transition: all 1s ease;

  @include desktop-medium {
    right: -6.6vw;
  }

  @include tablet {
    right: -126px;
    top: 40%;
    width: 1280px;
    height: 43.2px;
  }

  @include mobile-wide {
    display: none;
  }
}

.about-us__slider .swiper-slide-active:nth-child(2) ~ .about-us__wave {
  stroke: $color-blue;
  transform: translate(15%, -50%);
}

.about-us__slider .swiper-slide-active:nth-child(3) ~ .about-us__wave {
  stroke: $color-gray;
  transform: translate(30%, -50%);
}

.about-us__numbers {
  padding-left: 92px;
  transition: opacity 0.6s ease;
  font-family: $font-bebasneue;
  font-weight: normal;
  font-size: 200px;
  line-height: 1;
  letter-spacing: 6px;
  color: #363640;
  -webkit-text-stroke: 1px $color-gray;
  text-stroke: 1px $color-gray;

  @include desktop-medium {
    padding-left: 4.8vw;
    font-size: 5em;
  }

  @include tablet {
    padding-left: 20px;
    font-size: 78px;
    line-height: 82px;
  }

  @include mobile-wide {
    padding-left: 10px;
    font-size: 78px;
    line-height: 82px;
    text-align: left;
    letter-spacing: 2.2px;
    text-decoration: none !important;
    -webkit-text-stroke: 0.358px $color-gray;
    text-stroke: 0.358px $color-gray;
    color: $color-dark-gray-background;

    &--long {
      padding-left: 4px;
    }
  }
}

.about-us__text {
  margin-left: 92px;//4.8vw;
  font-family: $font-merel;
  font-size: 30px;
  line-height: 1.2;

  @include desktop-medium {
    font-size: 0.75em;
    margin-left: 4.8vw;
  }

  @include tablet {
    margin-left: 20px;
    font-size: 20px;
    line-height: 1;
  }

  @include mobile-wide {
    padding-left: 10px;
    padding-right: 10px;
    margin-left: 0;
    font-size: 18px;
    line-height: 1;
    text-align: left;
  }
}

.about-us__footer {
  display: flex;
  align-items: center;
}

.about-us__button {
  display: inline-block;
  text-decoration: none;
  margin-top: 114px;
  margin-left: auto;
  margin-right: auto;
  border-width: 1px;
  padding-left: 30px;
  padding-right: 34px;
  font-size: 20px;
  line-height: 1;

  @include desktop-medium {
    margin-top: 6vw;
    border-width: 1px;
    padding-left: 1.5vw;
    padding-right: 1.7vw;
    font-size: 0.5em;
  }

  @include tablet {
    margin-top: 25px;
    margin-left: 0;
  }

  @include mobile-wide {
    width: 200px;
    padding-top: 16px;
    padding-bottom: 12px;
    padding-left: 5px;
    padding-right: 5px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0;
    font-size: 16px;
    line-height: 16px;
    text-align: center;
  }
}

.about-us__skewedbg {
  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: -33.5%;
    bottom: 0;
    width: 100vw;
    transform: skew(-20.6deg, 0deg);
    background-color: #363640;
    box-shadow: 0 5px 10px rgba(0, 17, 31, 0.5);
    transition: opacity 0.5s ease;
    transition-delay: 0.4s;
  }

  @include tablet {
    &::before {
      content: none;
    }
  }
}

@keyframes transitionLeftToRightSkew {
  from {
    transform: translate(-20%) skew(-20.6deg, 0deg);
  }

  to {
    transform: translate(0) skew(-20.6deg, 0deg);
  }
}

$numbersShift: 100px;

@keyframes slideLeftCurrent {
  0% {
    opacity: 1;
    transform: translate(0);
  }

  80% {
    opacity: 0;
  }

  100% {
    opacity: 0;
    transform: translate(-$numbersShift);
  }
}

@keyframes slideLeftNext {
  0% {
    opacity: 0;
    transform: translate($numbersShift);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes slideRightCurrent {
  0% {
    opacity: 1;
    transform: translate(0);
  }

  80% {
    opacity: 0;
  }

  100% {
    opacity: 0;
    transform: translate($numbersShift);
  }
}

@keyframes slideRightNext {
  0% {
    opacity: 0;
    transform: translate(-$numbersShift);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes waveAnimation {
  0% {
    background-position: 0 center;
  }

  100% {
    background-position: -325px center;
  }
}

@keyframes fadeAndSlideIn {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }

  50% {
    opacity: 1;
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes fadeAndSlideOut {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  50% {
    // transform: translateY(20px);
  }

  100% {
    opacity: 0;
    transform: translateY(20px);
  }
}

@keyframes fadeAndSlideRight {
  0% {
    opacity: 1;
    transform: translateX(0);
  }

  100% {
    opacity: 0;
    transform: translateX(20px);
  }
}


@keyframes transitionLeftToRight {
  0% {
    opacity: 0;
    transform: translate(-10%);
  }

  50% {
    opacity: 1;
  }

  100% {
    transform: translate(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.animated-reverse {
  .about-us__skewedbg::before {
    animation: transitionLeftToRightSkew 0.7s ease;
    animation-fill-mode: both;
    animation-delay: 0s;
    animation-direction: reverse;
    opacity: 0;
  }

  .about-us__title {
    animation: fadeAndSlideIn 0.5s ease;
    animation-fill-mode: both !important;
    animation-delay: 0s;
    animation-direction: reverse !important;
  }

  .about-us__numbers {
    animation: transitionLeftToRight 0.5s ease-in-out;
    animation-fill-mode: both !important;
    animation-delay: 0s !important;
    animation-direction: reverse;
  }

  .about-us__text {
    animation: transitionLeftToRight 0.5s ease-in-out;
    animation-fill-mode: both !important;
    animation-delay: 0.1s !important;
    animation-direction: reverse;
  }

  .about-us__button {
    animation: fadeIn 0.5s ease-in;
    animation-fill-mode: both !important;
    animation-delay: 0.3s !important;
    animation-direction: reverse !important;
  }

  .about-us__slider-wrap {
    animation: fadeIn 0.5s ease-in;
    animation-fill-mode: both !important;
    animation-delay: 0.5s !important;
    animation-direction: reverse !important;
  }

  .docs__content {
    animation: fadeAndSlideIn 0.5s ease;
    animation-fill-mode: both;
    animation-delay: 0.3s;
    animation-direction: reverse;

    &:not(:first-child) {
      animation-delay: 0.6s;
    }
  }

  .partners__title {
    animation: fadeAndSlideOut 0.5s ease;
    animation-fill-mode: both;
    animation-delay: 0s;
  }

  .partners__container {
    animation: fadeAndSlideOut 0.4s ease-in-out;
    animation-fill-mode: both !important;
    animation-delay: 0.2s !important;
  }

  .projects__title {
    animation: fadeAndSlideOut 0.2s ease;
    animation-fill-mode: both;
    animation-delay: 0.4s;
  }

  .projects__item {
    animation: fadeAndSlideOut 0.5s ease;
    animation-fill-mode: both !important;
    animation-delay: 0.5s;

    &:nth-child(2) {
      animation-delay: 0.6s;
    }

    &:nth-child(3) {
      animation-delay: 0.7s;
    }
  }

  .tabs__tab {
    animation: fadeAndSlideOut 0.5s ease;
    animation-fill-mode: both;
    animation-delay: 0s;

    &:nth-child(2) {
      animation-delay: 0.2s;
    }
  }

  .gallery__slide .gallery__img-wrap {
    animation: fadeAndSlideRight 0.5s ease !important;
    animation-fill-mode: both !important;
    animation-delay: 0.2s !important;
    animation-direction: normal !important;
  }

  .swiper-slide-active + .gallery__slide .gallery__img-wrap {
    animation: fadeAndSlideRight 0.5s ease !important;
    animation-delay: 0.3s !important;
    animation-fill-mode: both !important;
    animation-direction: normal !important;
  }

  .swiper-slide-active + .gallery__slide + .gallery__slide .gallery__img-wrap {
    animation: fadeAndSlideRight 0.5s ease !important;
    animation-delay: 0.4s !important;
    animation-fill-mode: both !important;
    animation-direction: normal !important;
  }
}

.animated {
  .about-us__skewedbg::before {
    animation: transitionLeftToRightSkew 1s ease;
    animation-fill-mode: both;
    animation-delay: 0s;
  }

  .about-us__title {
    animation: fadeAndSlideIn 0.5s ease;
    animation-fill-mode: both;
    animation-delay: 0s;
    animation-direction: normal;
  }

  .about-us__numbers {
    animation: transitionLeftToRight 0.5s ease-in-out;
    animation-fill-mode: both !important;
    animation-delay: 0s !important;
    animation-direction: normal;
  }

  .about-us__text {
    animation: transitionLeftToRight 0.5s ease-in-out;
    animation-fill-mode: both !important;
    animation-delay: 0.1s !important;
    animation-direction: normal;
  }

  .about-us__button {
    animation: fadeIn 0.5s ease-in-out;
    animation-fill-mode: both !important;
    animation-delay: 0.3s !important;
    animation-direction: normal;
  }

  .docs__content {
    animation: fadeAndSlideIn 0.5s ease;
    animation-fill-mode: both;
    animation-delay: 0.3s;
    animation-direction: normal;

    &:not(:first-child) {
      animation-delay: 0.6s;
    }
  }

  .partners__title {
    animation: fadeAndSlideIn 0.5s ease;
    animation-fill-mode: both;
    animation-delay: 0s;
  }

  .partners__container {
    animation: fadeIn 0.5s ease-in-out;
    animation-fill-mode: both !important;
    animation-delay: 0.2s !important;
  }

  .projects__title {
    animation: fadeAndSlideIn 0.5s ease;
    animation-fill-mode: both;
    animation-delay: 0.4s;
  }

  .projects__item {
    animation: fadeAndSlideIn 0.5s ease;
    animation-fill-mode: both;
    animation-delay: 0.5s;

    &:nth-child(2) {
      animation-delay: 0.6s;
    }

    &:nth-child(3) {
      animation-delay: 0.7s;
    }
  }

  .tabs__tab {
    animation: fadeAndSlideIn 0.5s ease;
    animation-fill-mode: both;
    animation-delay: 0s;

    &:nth-child(2) {
      animation-delay: 0.2s;
    }
  }

  .gallery__slide {
    animation: fadeIn 0.5s ease-in-out !important;
    animation-fill-mode: both !important;
    animation-delay: 0.4s !important;
  }

  .swiper-slide-active + .gallery__slide {
    animation-delay: 0.5s !important;
  }

  .swiper-slide-active + .gallery__slide + .gallery__slide {
    animation-delay: 0.6s !important;
  }
}

