.dealers-list {
  padding: 0;
  margin: 0;
}

.dealers-list__list {
  @include list-reset;
  background: rgba(49, 49, 59, 0.8);
  border-radius: 10px;
  height: 552px;
  width: 350px;
  overflow-y: scroll;
  position: relative;
  padding-right: 11px;
  max-width: 350px;

  @include desktop-medium {
    height: 29vw;
    width: 18.4vw;
    padding-right: 0.5vw;
    max-width: 18.4vw;
  }

  @include tablet {
    width: 100%;
    max-width: 100%;
    padding-right: 0;
  }

  @include mobile-wide {
    height: 348px;
  }

  .ps__rail-x,
  .ps__thumb-x {
    display: none;
  }

  .ps__rail-y {
    margin: 10px 0;
    border-radius: 5px;
    display: block;
    opacity: 1;
    transition: background-color 0.2s linear, opacity 0.2s linear;
    width: 6px;
    right: 5px;
    position: absolute;

    @include mobile {
      right: 0;
    }
  }

  &.ps--active-y > .ps__rail-y {
    display: block;
    background-color: $color-dark-gray;
  }

  .ps__rail-y:hover,
  .ps__rail-y:focus,
  .ps__rail-y.ps--clicking {
    background-color: $color-dark-gray;
    opacity: 0.9;
  }

  .ps__thumb-y {
    background: $color-gradient-blue;
    border-radius: 4px;
    transition: 0.3s all ease;
    width: 4px;
    right: 1px;
  }

  .ps__rail-y:hover > .ps__thumb-y,
  .ps__rail-y:focus > .ps__thumb-y,
  .ps__rail-y.ps--clicking .ps__thumb-y {
    background: $color-gradient-blue;
    width: 4px;
  }
}

.dealers-list__item {
  transition: 0.3s all ease;
  padding: 0;
  padding-left: 33px;
  padding-right: 61px;
  padding-top: 23px;
  padding-bottom: 19px;
  position: relative;
  cursor: pointer;

  &::after {
    content: "";
    position: absolute;
    width: 320px;
    height: 1px;
    left: 0;
    bottom: 0;
    background: rgba(70, 70, 84, 0.5);
  }

  &:last-of-type {
    &::after {
      display: none;
    }
  }

  @include desktop-medium {
    padding-left: 1.7vw;
    padding-right: 3.2vw;
    padding-top: 1.2vw;
    padding-bottom: calc(1vw + 1px);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    &::after {
      width: 16.8vw;
    }
  }

  @include tablet {
    padding-left: 33px;
    padding-right: 61px;
    padding-top: 23px;
    padding-bottom: 19px;
  }

  @include mobile-wide {
    padding-left: 35px;
    padding-bottom: 9px;

    &::after {
      width: 320px;
    }
  }

  @include mobile {
    &::after {
      width: 100%;
    }
  }
}

.dealers-list__item:hover {
  background-color: #3b3b47;
}

.dealers-list__number {
  font-family: $font-merel;
  font-size: 20px;
  line-height: 23px;
  font-weight: 700;
  color: $color-orange;
  position: absolute;
  left: 20px;
  top: 8px;

  @include desktop-medium {
    font-size: 0.5em;
    line-height: 1.15;
    left: 1vw;
    top: 0.4vw;
  }

  @include tablet {
    font-size: 16px;
    line-height: 19px;
    left: 20px;
    top: 8px;
  }

  @include mobile-wide {
    font-size: 16px;
    line-height: 19px;
  }
}

.dealers-list__name {
  font-family: $font-bebasneue;
  font-size: 25px;
  line-height: 25px;
  color: $color-white;
  margin: 0;
  margin-bottom: 2px;

  @include desktop-medium {
    font-size: 0.625em;
    line-height: 1;
  }

  @include tablet {
    font-size: 20px;
    line-height: 20px;
  }

  @include mobile-wide {
    margin-bottom: 12px;
  }
}

.dealers-list__site {
  font-family: $font-merel;
  font-size: 20px;
  line-height: 23px;
  display: block;
  font-weight: 500;
  margin-bottom: 17px;
  color: $color-blue-map-letters;
  transition: 0.3s all ease;

  @include desktop-medium {
    font-size: 0.5em;
    line-height: 1.15;
    margin-bottom: 0.8vw;
  }

  @include tablet {
    font-size: 18px;
    line-height: 21px;
  }

  @include mobile-wide {
    padding-left: 2px;
    margin-bottom: 10px;
  }

  &:hover {
    color: $color-orange;
  }
}

.dealers-list__phone {
  font-family: $font-merel;
  font-size: 20px;
  line-height: 23px;
  font-weight: 500;
  padding-left: 30px;
  position: relative;
  transition: 0.3s all ease;

  color: $color-white;

  svg {
    width: 21px;
    height: 20px;
    position: absolute;
    left: 0;
    bottom: 2px;
    fill: transparent;
    stroke: $color-white;
    transition: 0.3s all ease;
  }

  @include desktop-medium {
    font-size: 0.5em;
    line-height: 1.15;
    padding-left: 1.5vw;

    svg {
      width: 1.1vw;
      height: 1vw;
    }
  }

  @include tablet {
    font-size: 18px;
    line-height: 21px;
    padding-left: 30px;

    svg {
      left: 0;
      width: 21px;
      height: 20px;
    }
  }

  &:hover {
    color: $color-orange;

    svg {
      stroke: $color-orange;
    }
  }
}
