.certificate-popup {
  @include min-width(320px) {
    background: rgba(42, 42, 50, 0.92);
    // overflow: hidden;
    position: absolute;
    top: 0px;
    left: -1px;
    z-index: 999999999;
    width: 100%;
    height: 100%;
  }

  .certificate-popup__wrapper {
    @include min-width(320px) {
      position: relative;
      width: 100vw;
      background: linear-gradient(180.1deg, #3b3b47 41.16%, #31313b 99.91%);
      padding: 62px 0 0;
    }
    @include min-width(992px) {
      width: get-vw(940px);
      height: get-vw(1333px);
      margin: 11vh auto 0px;
      padding: get-vw(80px) get-vw(50px) get-vw(179px);
    }

    .certificate-popup__content {
      .content__header {
        @include min-width(320px) {
          align-items: center;
          display: flex;
          border-bottom: 1px solid #c4c4c4;
          justify-content: space-between;
          padding: 0px 15px 10px;
          font-family: $font-bebasneue;
          font-weight: 100;
        }
        @include min-width(992px) {
          font-size: get-vw(40px);
          padding: 0px 0px get-vw(10px);
        }
        & > h2 {
          @include min-width(320px) {
            max-width: 500px;
            font-weight: 100;
            font-size: 20px;
            margin-right: 20px;
          }
          @include min-width(992px) {
            font-size: get-vw(40px);
            font-weight: 100;
            max-width: none;
            margin-right: 0px;
          }
        }
        .header__download {
          display: flex;
          align-items: center;

          .pdf-icon {
            width: 23px;
            height: 30px;
          }

          & > a {
            @include min-width(320px) {
              max-height: 27px;
              margin-right: 5px;
              margin-bottom: 10px;
              padding-bottom: 3px;

              font-size: 14px;
              line-height: 14px;

              text-transform: none;
              border-bottom: 1px dashed #ffffff;
              font-family: $font-merel;
              color: white;
            }
            @include min-width(992px) {
              font-size: get-vw(20px);
              line-height: get-vw(23px);
              max-height: get-vw(27px);
              margin-right: get-vw(27px);
              margin-bottom: get-vw(10px);
              padding-bottom: get-vw(3px);
            }
          }
        }
      }
      .content__main {
        @include min-width(320px) {
          padding: 20px 15px;
          font-size: 14px;
        }
        @include min-width(992px) {
          padding: get-vw(60px) 0;
          font-size: get-vw(24px);
          display: flex;
          justify-content: space-between;
        }
        .content__main_scroll-area {
          @include min-width(320px) {
            width: 100%;
            padding-right: get-vw(5px);
            padding-top: 20px;
          }
          @include min-width(992px) {
            padding-top: 0px;
            max-height: get-vw(1079px);
            overflow-y: scroll;
            padding-left: get-vw(46px);
            padding-right: get-vw(30px);
          }
          & > img {
            width: 768px;
            padding-bottom: 30px;
            margin: 0 auto;
            display: block;
          }
        }
        ::-webkit-scrollbar {
          width: get-vw(4px);
        }
      }
    }

    .certificate-popup_close {
      @include min-width(320px) {
        background: none;
        border: none;
        position: absolute;
        top: 20px;
        right: 20px;
      }
      @include min-width(992px) {
        top: get-vw(-40px);
        right: get-vw(-70px);
      }
      .close-icon {
        @include min-width(320px) {
          width: 22px;
          height: 22px;
        }
        @include min-width(992px) {
          width: get-vw(26px);
          height: get-vw(26px);
        }
      }
    }
  }
}

.starlink-main {
  .certificate-popup {
    .certificate-popup__wrapper {
      // overflow: hidden;

      @include min-width(992px) {
        height: 800px;
        height: get-vw(800px);
      }

      .content__main {
        max-height: get-vw(680px);
      }
    }
  }
}
