.starlink-main {
  .catalog {
    & .container {
      position: relative;
      max-width: 1550px;
      padding-top: 6vh;
      padding-left: 0;
      margin-bottom: 10px; 

      @include laptop {
        padding-right: 10px;
        padding-left: 20px;
        padding-bottom: 20px;

        @media (max-height: $max-height-small) {
          padding-bottom: 5px;
          padding-top: 18px;
        }
      }

      @include min-width(1024px) {
        max-width: 1100px;
        padding-top: 1.7vw;
        padding-left: 4.2vw;
        padding-right: 4.2vw;

        @media (max-height: $max-height-small) {
          padding-top: 18px;
        }
      }

      @include min-width(1366px) {
        max-width: 1400px;
        padding-left: 13.2vw;
        padding-right: 13.2vw;
      }

      @include min-width(1466px) {
        max-width: 100%;
        padding-left: 12.2vw;
        padding-right: 12.2vw;
      }

      @include min-width(1900px) {
        max-width: 1550px;
        padding-top: 6vh;
        padding-right: 10px;
        padding-left: 20px;
        padding-bottom: 20px;
      }

      @include mobile {
        min-height: 570px;
        margin-bottom: 0;
        padding-right: 5px;
        padding-left: 0;
        padding-top: 40px;
      }
    }

    .switch {
      @include mobile {
        padding-bottom: 0;
      }
    }
  }

  .catalog__container {
    max-width: 1390px;
    padding-left: 28px;
    padding-right: 28px;

    @include tablet {
      padding-right: 10px;
      padding-left: 0;
    }
  }

  .catalog__wrapper {
    padding-top: 25px;
    margin-bottom: 25px;

    @include tablet {
      margin-bottom: 35px;
    }

    @include mobile-wide {
      padding-top: 0;
    }
  }

  .catalog__products {
    width: 100%;

    @include tablet {
      margin-left: 0;
      padding: 0;
      padding-bottom: 30px;
    }

    @include mobile {
      padding-bottom: 0;
    }
  }

  .catalog__header {
    display: flex;
    padding-left: 35px;
    padding-right: 44px;
    align-items: center;
    font-family: $font-bebasneue;

    & .catalog__link {
      font-size: 2.5em;
      // line-height: 43px;
      // line-height: 1.075;
      line-height: 1.1;
      text-decoration: none;
      text-transform: uppercase;
      color: $color-blue;
      border-bottom: 1px dashed $color-blue;
      transition: all $style-change-duration $style-change-function;

      @include desktop-medium {
        font-size: 1.004em;
      }

      @include tablet {
        font-size: 24px;
        line-height: 1.1;
      }
    }

    & .catalog__link--active {
      color: $color-white;
      border: none;
    }

    & .catalog__new-cables {
      margin-right: 57px;

      @include laptop {
        margin-right: 37px;
      }

      @include mobile {
        margin-right: 11px;
      }
    }

    & .catalog__btn {
      position: relative;
      max-width: 200px;
      padding: 15px 43px 14px 43px;
      margin-left: auto;
      font-size: 1.25em;
      line-height: 20px;
      text-align: center;
      border: 1px solid $color-blue;
      background: transparent;
      border-radius: 100px;
      color: $color-white;
      transition: all $style-change-duration $style-change-function;
      outline: none;

      a {
        color: $color-white;
      }

      @include desktop-medium {
        padding: 0.75vw 2.2vw 0.65vw 2.2vw;
        font-size: 0.502em;
        line-height: 2vh;
      }

      @include tablet {
        position: absolute;
        width: 200px;
        margin-right: 0;
        padding: 16px 20px 15px;
        font-size: 16px;
        line-height: 16px;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
      }

      @include min-width(768px) {
        position: relative;
        left: auto;
        transform: none;
      }

      @include mobile {
        bottom: 0;
      }

      &:hover,
      &:focus {
        background: $color-blue;
      }
    }

    @include mobile {
      margin-bottom: 37px;
      padding-left: 15px;
    }

    @include min-width(481px) {
      padding-left: 0;
    }

    @include laptop {
      padding-right: 0;
      margin-bottom: 10px;
    }

    @include mobile-wide {
      margin-bottom: 20px;
    }

    @include min-width(1180px) {
      padding: 0;
    }

    @include min-width(1900px) {
      padding-left: 35px;
      padding-right: 44px;
      margin-bottom: 1.5vh;
    }
  }

  .catalog__pagination {
    display: flex;
    justify-content: center;

    &.swiper-pagination.swiper-pagination-bullets {
      position: relative;
      left: 12px;
      bottom: 0;
    }

    & span {
      margin: 0;
      width: 81px;
      height: 7px;
      border-radius: 25px;
      background-color: $color-gray;
      opacity: 1;
      outline: none;
    }

    &.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
      margin-right: 9px;

      &:last-of-type {
        margin-right: 0;
      }
    }

    & .swiper-pagination-bullet-active {
      background: $color-gradient-blue;
    }

    @include tablet {
      display: none;
    }
  }

  .catalog--animate {
    & .catalog__link {
      @include min-width(1024px) {
        &:first-of-type {
          animation: slide-down-link 0.5s ease-in-out;
        }

        &:last-of-type {
          animation: slide-down-link 0.6s ease-in-out;
        }
      }
    }

    & .catalog__btn {
      @include min-width(1024px) {
        animation: slide-right 0.7s ease-in-out;
      }
    }

    & .catalog__pagination {
      animation: pagination-opacity 1.4s ease-in-out;
    }
  }

  .catalog--reverse {
    & .catalog__link {
      @include min-width(1024px) {
        &:first-of-type {
          animation: slide-down-link-reverse 1s ease-in-out;
          animation-direction: reverse;
        }

        &:last-of-type {
          animation: slide-down-link-reverse 1.2s ease-in-out;
          animation-direction: reverse;
        }
      }
    }

    & .catalog__btn {
      @include min-width(1024px) {
        animation: slide-right-reverse 1.3s ease-in-out;
        animation-direction: reverse;
      }
    }

    & .catalog__pagination {
      animation: pagination-opacity-reverse 1.4s ease-in-out;
      animation-direction: reverse;
    }
  }

  @keyframes slide-down-link {
    from {
      opacity: 0;
      transform: translateY(-50px);
    }

    to {
      transform: translateY(0);
      opacity: 1;
    }
  }

  @keyframes slide-right {
    from {
      opacity: 0;
      transform: translateX(30px);
    }

    to {
      transform: translateX(0);
      opacity: 1;
    }
  }

  @keyframes pagination-opacity {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  @keyframes slide-down-link-reverse {
    from {
      opacity: 0;
      transform: translateY(-50px);
    }

    to {
      transform: translateY(0);
      opacity: 1;
    }
  }

  @keyframes slide-right-reverse {
    from {
      opacity: 0;
      transform: translateX(30px);
    }

    to {
      transform: translateX(0);
      opacity: 1;
    }
  }

  @keyframes pagination-opacity-reverse {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  .catalog__new--hidden {
    display: none;
  }

  .catalog__popular--hidden {
    display: none;
  }
}
