.gallery {
  position: relative;
  flex-grow: 1;
  margin-top: 54px;
  margin-left: 70px;

  @media (min-height: 1001px) {
    margin-top: -106px;
  }

  @include desktop-medium {
    max-width: 71.8vw;
    margin-top: 2.6vw;
    margin-left: 3.6vw;

    @media (min-height: 1001px) {
      margin-top: 0;
    }
  }

  @include desktop-small {
    @media (min-height: 740px) {
      margin-top: 0;
    }
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      max-width: 60vw;
      margin-top: 0;
    }
  }
  
  @include tablet {
    max-width: 100%;
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
  }

  @include mobile-wide {
    margin-top: 18px;
    margin-left: 0;
  }
}

.gallery__container {
  @media (min-height: 1001px) {
    margin-top: auto;
    margin-bottom: auto;
  }

  @include tablet {
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
  }


  @include mobile-wide {
    width: auto;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.gallery__slide {
  display: flex;

  @include mobile-wide {
    padding-bottom: 24px;
    text-align: center;
  }
}

.gallery__img-wrap {
  position: relative;

  &::before {
    content: "";
    display: block;
    position: absolute;
    z-index: 10;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    border-radius: 10px;
    opacity: 0.4;
    background-color: #2a2a32;
    transition: opacity 0.3s ease;
  }

  @include desktop-medium {
    width: 40.8vw;
    height: 25.5vw;
  }

  @include tablet {
    width: 74vw;
    height: 46.3vw;
    margin: 0 auto;
  }

  @include mobile-wide {
    width: auto;
    height: 100%;

    &::before {
      opacity: 0;
    }
  }
}

.gallery__slide img {
  display: block;
  border-radius: 10px;
  filter: grayscale(100%);

  @include desktop-small {
    margin: 0 auto;
  }

  @include tablet {
    filter: none;
  }

  @include mobile-wide {
    filter: none;
  }
}

.gallery__slide.swiper-slide-active img {
  filter: grayscale(0);
}

.gallery__slide.swiper-slide-active .gallery__img-wrap::before {
  opacity: 0;
}

//   3     
.swiper-slide-active + .swiper-slide + .swiper-slide .gallery__img-wrap::before {
  opacity: 0.8;

  @include mobile-wide {
    opacity: 0;
  }
}

.gallery .swiper-wrapper {
  @include mobile-wide {
    flex-direction: column;
  }
}

.gallery .slider-button {
  z-index: 10;
  top: 50%;

  transform: translateY(-50%);

  &--prev {
    left: -75px;
  }

  &--next {
    left: 825px;
  }

  @include desktop-medium {
    &--prev {
      left: -3.9vw;
    }

    &--next {
      left: 43.4vw;
    }
  }

  @include tablet {
    &--prev {
      left: 20px;
    }

    &--next {
      left: auto;
      right: 20px;
    }
  }
}



