.footer-nav {
  @include list-reset;
  margin: 0;
  margin-left: 112px;
  display: flex;
  flex-wrap: wrap;
  max-width: 706px;
  min-height: 120px;

  @include desktop-medium {
    margin-left: 5.7vw;
    max-width: 700px;
    min-height: 6.3vw;
  }

  @include desktop-small {
    margin-left: 0;
    width: 600px;
  }

  @media (max-width: 1200px) {
    display: none;
  }
}

.footer-nav__item {
  margin-bottom: 4px;
  width: 200px;

  &:nth-of-type(3n + 2) {
    margin-left: 53px;
  }

  &:nth-of-type(3n + 3) {
    margin-left: 27px;
  }

  @include desktop-medium {
    margin-bottom: 0.2vw;
    width: 10.5vw;
    font-size: 16px;
    line-height: 1;

    &:nth-of-type(3n + 2) {
      margin-left: 2.7vw;
    }

    &:nth-of-type(3n + 3) {
      margin-left: 1.4vw;
    }
  }

  @include tablet {
    width: 152px;

    &:nth-of-type(3n + 2) {
      margin-left: 0;
    }

    &:nth-of-type(3n + 3) {
      margin-left: 0;
    }
  }
}

.footer-nav__item a {
  max-width: 253px;
  font-family: $font-merel;
  text-decoration: none;
  color: $color-light-gray-letters;
  font-size: 16px;
  line-height: 107.1%;
  display: inline-block;

  @include desktop-medium {
    max-width: 13.3vw;
    font-size: 14px;
    line-height: 1.07;
  }

  @include desktop-small {
    font-size: 13px;
  }

  @media (max-width: 1200px) {
    font-size: 14px;
  }

  @include tablet {
    font-size: 14px;
    line-height: 107.1%;
    max-width: 253px;
  }
}
