.search {
  @include min-width(320px) {
    font-family: $font-merel;
    padding-bottom: 30px;
  }
  @include min-width(768px) {
    font-family: $font-merel;
    padding-bottom: 90px;
  }
  .search__container {
    @include min-width(320px) {
      max-width: 1800px;
      padding: 0px 15px;
    }
    @include min-width(768px) {
      padding: 0;
    }
    .search__container_header {
      @include min-width(320px) {
        max-width: 1800px;
        padding: 0px 0px;
      }
      @include min-width(768px) {
        padding: 0px 20px;
        max-width: 1800px;
      }
      @include min-width(1200px) {
        padding: 0px 80px;
        max-width: 1800px;
      }
    }
    .search__header {
      @include min-width(320px) {
        padding: 30px 0px 20px;
      }
      @include min-width(768px) {
        padding: 54px 82px 40px;
      }
      .search__title {
        @include min-width(320px) {
          font-family: $font-bebasneue;
          font-style: normal;
          font-weight: 100;
          font-size: 24px;
          line-height: 107.1%;
          text-transform: uppercase;
          position: relative;
        }
        @include min-width(768px) {
          font-size: 30px;
        }
        @include min-width(1200px) {
          font-size: 50px;
        }
        &:after {
          content: "";
          display: block;
          height: 1px;
          background: white;
          width: 150px;
          left: 1px;
          position: absolute;
          margin-top: 8px;
        }
      }
      .search__sort-block {
        @include min-width(320px) {
          padding-top: 20px;
        }
        @include min-width(768px) {
          padding-top: 55px;
        }
        .search__sort-block_catalog,
        .search__sort-block_articles {
          @include min-width(320px) {
            font-family: $font-bebasneue;
            font-style: normal;
            font-weight: 100;
            font-size: 24px;
            line-height: 107.1%;
            text-transform: uppercase;
            background: transparent;
            color: white;
            border: none;
          }
          @include min-width(768px) {
            font-size: 30px;
          }
          @include min-width(1200px) {
            font-size: 40px;
          }
        }
        .search__sort-block_articles {
          @include min-width(320px) {
            margin-left: 0px;
          }
          @include min-width(768px) {
            margin-left: 67px;
          }
        }
        .search__sort-block_active-btn {
          border-bottom: 1px dashed #0075d1;
          color: #0075d1;
        }
      }
    }
    .desktop-news-slider__current-page {
      @include min-width(320px) {
        text-align: center;
        font-family: $font-merel;
        font-size: 18px;
        line-height: 28px;
        color: #c4c4c4;
        position: absolute;
        bottom: -15px;
        left: 50%;
        right: 50%;
        margin-right: -50%;
        transform: translate(-50%, -50%);
        display: inline-block;
        width: 100%;
        padding-right: 15px;
      }
      @include min-width(768px) {
        bottom: -3px;
        font-size: 24px;
        padding-right: 0px;
      }
    }
  }
  .search-paginator_hidden {
    display: none !important;
  }
  .search-paginator {
    @include min-width(320px) {
      padding-bottom: 60px;
      flex-wrap: wrap;
      display: flex;
    }
    @include min-width(612px) {
      width: 95vw;
    }
    .cable__img-wrapper {
      flex-wrap: nowrap;
    }
    .cable {
      @include min-width(320px) {
        max-width: 435px;
        min-width: 290px;
        margin: 0 auto 30px;
      }
      @include min-width(1277px) {
        width: calc((100% - 5vw) / 4);
        margin: 0 get-vw(20px) get-vw(30px) 0;
      }
    }
  }
  .desktop-news-slider__pagination {
    & > span {
      width: 100px;
      width: 81px;
      height: 7px;
      border-radius: 25px;
      background-color: #c4c4c4;
      opacity: 1;
      outline: 0;
    }
    .swiper-pagination-bullet-active {
      background-color: #4694d2;
    }
  }
  .swiper-pagination {
    @include min-width(320px) {
      bottom: 110px;
      padding-right: 15px;
    }
    @include min-width(768px) {
      padding-right: 0px;
    }
  }
  .swiper-wrapper {
    .swiper-slide {
      @include min-width(992px) {
        margin-left: 1px;
      }
    }
    .article-slide {
      padding-bottom: 60px;

      .article-slide_item {
        @include min-width(320px) {
          font-family: $font-merel;
          max-width: 1200px;
          margin: 0px 0px;
          cursor: pointer;
          padding-bottom: 30px;
          position: relative;
        }
        @include min-width(768px) {
          margin: 0px 50px;
        }
        @include min-width(1200px) {
          margin: 0px 100px;
        }
        @include min-width(1500px) {
          margin: 0px 300px;
        }
        & > h2 {
          @include min-width(320px) {
            font-size: 18px;
            text-transform: none;
            line-height: 100%;
          }
          @include min-width(768px) {
            font-size: 26px;
            line-height: 30px;
          }
        }
        & > p {
          @include min-width(320px) {
            padding-top: 10px;
            font-size: 14px;
            line-height: 23px;
            color: #e5e5e5;
          }
          @include min-width(768px) {
            font-size: 20px;
          }

          & > a {
            color: #0075d1;
          }
        }
        &:hover:before {
          content: "";
          border-left: 1px solid #ed8528;
          position: absolute;
          top: 1px;
          left: -20px;
          height: 120px;
          transition: 0.5s ease;
        }
        &:hover {
          & > h2 {
            color: #ed8528;
            transition: 0.2s ease;
          }
        }
      }
    }
  }
  .search__btn {
    a {
      color: white;
    }
    @include min-width(320px) {
      background: #0075d1;
      border-radius: 100px;
      font-family: $font-bebasneue;
      font-style: normal;
      font-weight: 100;
      font-size: 16px;
      line-height: 107.1%;
      text-align: center;
      border: none;
      color: white;
      padding: 15px 44px 15px;
      margin: 0 auto;
      display: block;
      box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
      margin-top: -20px;
    }
    @include min-width(768px) {
      font-size: 27px;
      padding: 24px 48px 21px;
      margin-top: 0px;
    }
  }
}
.search__not-found {
  @include min-width(320px) {
    margin: 0 auto;
    text-align: center;
    float: none;
    font-family: $font-merel;
    font-size: 22px;
    line-height: 30px;
    padding-bottom: 100px;
  }
  @include min-width(768px) {
    font-size: 26px;
    padding-bottom: 300px;
    padding-right: 100px;
    padding-left: 100px;
  }
  & > a {
    color: #0075d1;
  }
}
