.desk-norm {
  .select-scope-application {
    position: relative;
    min-width: 58%;
    border-radius: 36.5px;
    transition: all $style-change-duration $style-change-function;
    box-shadow: 0 1px 5px $color-box-shadow-input;
    cursor: pointer;
    box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
    background-color: #31313b;
    font-family: $font-bebasneue;
    line-height: 16px;

    @include laptop {
      min-width: 75%;
    }

    @include tablet {
      width: 100%;
    }

    & .select-scope-application__value {
      position: relative;
      display: flex;
      align-items: center;
      padding: 2.51vh 2.5vw 2vh 2.6vw;
      // background-color: $color-gray-blue-input;
      border-top-left-radius: 36.5px;
      border-top-right-radius: 36.5px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      transition: all $style-change-duration $style-change-function;
      outline: none;
      z-index: 6;
      animation: close-select 0s linear;

      // @include hover-focus-background-color ($color-blue);

      & span {
        margin-right: 20px;
        font-size: 1.6875em;
        line-height: 29px;
        text-transform: uppercase;

        @include desktop-medium {
          font-size: 0.677049em;
        }

        @include tablet {
          font-size: 16px;
          line-height: 16px;
        }
      }

      & svg {
        position: relative;
        width: 10px;
        height: 17px;
        top: -3px;
        left: -1px;
        margin-left: auto;
        fill: $color-white;
        transition: transform $style-change-duration $style-change-function;
        transform: rotate(90deg);

        @include mobile {
          width: 12px;
          height: 12px;
        }
      }

      @include tablet {
        padding: 17px 20px 13px 20px;
      }
    }

    & .select-scope-application__options {
      position: absolute;
      display: block;
      width: 100%;
      max-height: 0;
      overflow: hidden;
      background-color: #31313b;
      border-bottom-left-radius: 36.5px;
      border-bottom-right-radius: 36.5px;
      // box-shadow: 0 1px 5px $color-box-shadow-input;
      transition: all $style-change-duration $style-change-function;
      z-index: 2;

      @include tablet {
        border-bottom-left-radius: 21.5px;
        border-bottom-right-radius: 21.5px;
        animation: open-mobile-select 0.2s ease-in;
      }
    }

    .select-scope-application__item {
      display: block;
      padding: 0.8949vw 1.5vh 0.423vw 2.478vw;
      text-transform: uppercase;
      font-size: 1.6875em;
      line-height: 1.1em;
      cursor: pointer;

      @include desktop-medium {
        font-size: 0.677049em;
      }

      &[data-active="true"] {
        background-color: $color-darknes-gray;
      }

      @include hover-focus-background-color($color-darknes-gray);

      @include tablet {
        padding: 17px 20px 13px 20px;
        font-size: 16px;
        line-height: 16px;
      }
    }

    &[data-hidden="true"] {
      & .select-scope-application__value {
        border-radius: 36.5px;
        border: 1px solid #0075d1;
        transition: all $style-change-duration $style-change-function;
      }

      & .select-scope-application__options {
        transition: all $style-change-duration $style-change-function;
        animation: close-select 0.2s linear;
      }
    }

    &[data-hidden="false"] {
      .select-scope-application__value {
        // background-color: $color-blue;
        transition: all $style-change-duration $style-change-function;
        background-color: #31313b;

        & svg {
          transform: rotate(-90deg);
        }

        @include tablet {
          border: 1px solid #0075d1;
          padding: 17px 20px 13px 20px;
          border-top-left-radius: 21.5px;
          border-top-right-radius: 21.5px;
        }
      }

      .select-scope-application__options {
        padding-top: 5px;
        padding-bottom: 25px;
        max-height: inherit;
        animation: open-desktop-select 0.2s linear;
        border: 1px solid #0075d1;

        @include tablet {
          // position: relative;
        }
      }
    }
  }

  @keyframes open-desktop-select {
    from {
      opacity: 0;
      transform: translateY(-50px);
    }

    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @keyframes open-mobile-select {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  @keyframes close-select {
    from {
      opacity: 1;
      max-height: inherit;
      transform: translateY(0);
    }

    to {
      opacity: 0;
      max-height: 0;
      transform: translateY(-50px);
    }
  }
}
