.desk-norm {
  .product {
    @include min-width(320px) {
      display: block;
      padding-top: 0px;
      padding-bottom: 0px;
    }
    @include min-width(768px) {
      padding-top: 54px;
      padding-bottom: 80px;
      display: flex;
    }
    .catalog__products {
      @include min-width(320px) {
        padding-top: 30px;
        padding-bottom: 25px;
        display: block;
      }
      @include min-width(768px) {
        display: none;
      }
      .catalog__wrapper {
        margin-bottom: 15px;
      }
      .cable__img-items {
        padding: 0;
      }
      .catalog__wrapper {
        position: relative;
        left: -15px;
      }

      .cable {
        @include min-width(320px) {
          min-width: 290px;
        }
        .swiper-slide__hover {
          position: absolute;
          top: 1px;
          padding: 110px;
          opacity: 0.7;
          cursor: pointer;

          &:hover {
            opacity: 1;
            transition: 0.5s ease;
          }
        }
      }
      #cable__img-cabel1 {
        padding-top: 70px;
        padding-bottom: 46px;
      }
      .cable__img-container {
        border-radius: 0px;
        max-height: 290px;
      }
      .catalog__pagination {
        display: block;
        padding: 0;
      }
      .catalog__pagination span {
        width: 34px;
      }
    }
    .product__face {
      @include min-width(320px) {
        display: none;
      }
      @include min-width(768px) {
        position: relative;
        display: flex;
        max-height: 678px;
        overflow: hidden;
      }
      .product__img {
        margin-left: 20px;
        width: 678px;
        max-height: 679px;
        background-color: white;

        @include min-width(768px) {
          height: 35vw;
        }
        @include min-width(1730px) {
          height: 678px;
        }

        & img {
          padding-top: 5vw;
          background-color: white;
          width: 678px;

          @include min-width(1730px) {
            padding-top: 163px;
          }
        }
      }
    }
    .product__desc {
      @include min-width(320px) {
        padding-left: 0px;
        font-family: $font-merel;
      }
      @include min-width(768px) {
        padding-left: 20px;
      }
      @include min-width(992px) {
        padding-left: 25px;
      }
      @include min-width(1200px) {
        padding-left: 80px;
      }

      .product__desc_title {
        @include min-width(320px) {
          max-width: 250px;
          font-size: 24px;
          font-family: $font-bebasneue;
          font-weight: normal;
          line-height: 107.1%;
          padding-bottom: 10px;
        }
        @include min-width(768px) {
          max-width: 500px;
          font-size: 30px;
          padding-bottom: 20px;
        }
        @include min-width(992px) {
        }
        @include min-width(1200px) {
          font-size: 50px;
        }
      }
      .product__desc_about {
        @include min-width(320px) {
          font-size: 16px;
          line-height: 23px;
          max-width: 775px;
          color: #c4c4c4;
          padding-bottom: 26px;
        }
        @include min-width(768px) {
          font-size: 17px;
          padding-bottom: 70px;
          padding-bottom: 20px;
        }
        @include min-width(992px) {
          font-size: 18px;
        }
        @include min-width(1200px) {
          font-size: 20px;
        }
      }
      .product__desc_additional {
        @include min-width(320px) {
          display: block;
          max-width: 680px;
          align-items: center;
        }
        @include min-width(768px) {
          display: flex;
          justify-content: space-between;
        }
        .additional__promise {
          color: #c4c4c4;

          @include min-width(320px) {
            padding-bottom: 20px;
          }
          @include min-width(768px) {
            padding-bottom: 0px;
          }

          .promise_item {
            display: flex;
            flex-direction: row;
            padding-bottom: 12px;

            & > p {
              @include min-width(320px) {
                padding-left: 10px;
                font-size: 16px;
              }
              @include min-width(768px) {
                font-size: 15px;
              }
              @include min-width(1388px) {
                font-size: 18px;
              }
              @include min-width(1200px) {
                font-size: 20px;
              }
            }
          }
        }
        .additional__links {
          @include min-width(320px) {
            display: flex;
            justify-content: space-between;
          }
          @include min-width(768px) {
            display: block;
          }
          .links__download {
            @include min-width(320px) {
              padding-bottom: 25px;
              display: block;
              align-items: center;
              font-size: 16px;
            }
            @include min-width(768px) {
              display: flex;
              font-size: 20px;
            }
            .links__download_title {
              @include min-width(320px) {
                color: white;
                border-bottom: 1px dashed white;
                margin-right: 10px;
                display: block;
                max-width: 130px;
                margin-bottom: 16px;
              }
              @include min-width(768px) {
                max-width: 170px;
                display: block;
                margin-bottom: 0px;
                font-size: 15px;
              }
              @include min-width(992px) {
                font-size: 18px;
              }
              @include min-width(1200px) {
                font-size: 20px;
              }
            }
          }
          .links__network {
            @include min-width(320px) {
              display: block;
              color: #e5e5e5;
            }
            @include min-width(768px) {
              display: flex;
            }
            & > p {
              @include min-width(320px) {
                padding-bottom: 31px;
              }
              @include min-width(768px) {
                padding-bottom: 0;
              }
            }
            & > a {
              @include min-width(320px) {
                margin-left: 0px;
                margin-right: 20px;
              }
              @include min-width(768px) {
                margin-left: 10px;
                margin-right: 0px;
              }
              @include min-width(992px) {
              }
              @include min-width(1200px) {
                margin-left: 20px;
                margin-right: 0px;
              }
            }
          }
        }
      }
      .product__desc_btn {
        @include min-width(320px) {
          margin-bottom: 30px;
          margin-top: 76px;
          position: relative;
          left: 50%;
          transform: translate(-50%, 0);
          color: white;
          background: #0075d1;
          border-radius: 100px;
          border: none;
          padding: 17px 51px 13px;
          font-size: 16px;
          font-family: $font-bebasneue;
        }
        @include min-width(768px) {
          margin-bottom: 0px;
          margin-top: 10px;
          padding: 13px 40px 11px;
          font-size: 20px;
          position: static;
          transform: none;
        }
        @include min-width(992px) {
          margin-bottom: 0px;
          margin-top: 20px;
          padding: 23px 80px 21px;
          font-size: 25px;
        }
        @include min-width(1400px) {
          margin-bottom: 0px;
          margin-top: 140px;
          padding: 23px 80px 21px;
          font-size: 27px;
          position: static;
          transform: none;
        }
      }
    }
  }
  // section 2
  .product__about {
    @include min-width(320px) {
      font-family: $font-merel;
      padding-bottom: 0px;
      display: block;
      justify-content: space-between;
    }
    @include min-width(768px) {
    }
    @include min-width(992px) {
    }
    @include min-width(1200px) {
      display: flex;
      padding-bottom: 80px;
    }
    .about__specifications {
      @include min-width(320px) {
        width: 100%;
        color: #c4c4c4;
      }
      @include min-width(769px) {
        color: #c4c4c4;
      }
      @include min-width(992px) {
        width: 738px;
        color: #c4c4c4;
      }
      @include min-width(1200px) {
        width: 738px;
        color: #c4c4c4;
      }
      & > h2 {
        @include min-width(320px) {
          font-size: 18px;
          text-transform: none;
          padding-bottom: 0px;
          font-weight: normal;
          color: white;
          font-weight: 100;
        }
        @include min-width(768px) {
          font-size: 20px;
          padding-bottom: 20px;
          text-transform: uppercase;
        }
        @include min-width(992px) {
          font-size: 25px;
          padding-bottom: 20px;
          text-transform: uppercase;
        }
      }
      //mobile bread crumps
      .product__mobile-bread-crumps {
        padding-bottom: 25px;

        .mobile-bread-crumps__header {
          display: flex;
          justify-content: space-between;
          padding-top: 10px;
          align-items: flex-end;

          & > p {
            @include min-width(320px) {
              font-size: 14px;
              display: block;
              line-height: 23px;
            }
            @include min-width(768px) {
              font-size: 20px;
            }
          }
        }
        a {
          @include min-width(320px) {
            color: #0075d1;
            line-height: 23px;
            text-align: right;
            display: inline;
            display: block;
            padding-top: 5px;
            font-size: 14px;
          }
          @include min-width(768px) {
            font-size: 20px;
          }
        }
      }
      .show-on-mobile {
        @include min-width(320px) {
          display: block;
        }
        @include min-width(768px) {
          display: none;
        }
      }
      .show-on-desktop {
        @include max-width(768px) {
          display: none;
        }
      }
      .specifications__table {
        width: 100%;
        border-spacing: 0px;
        border-collapse: collapse;

        .specifications__table_row {
          @include min-width(320px) {
            font-size: 14px;
            border-bottom: 0.5px solid #b6b6b6;
          }
          @include min-width(768px) {
            font-size: 20px;
          }
          & > td > a {
            color: #0075d1;
            line-height: 23px;
            text-align: right;
            display: inline;
          }
          & > td {
            padding-top: 21px;
            padding-bottom: 4px;
            vertical-align: bottom;
          }
          & > td:last-child {
            text-align: right;
            color: white;
            vertical-align: bottom;
          }
          .table__row_links {
            @include min-width(320px) {
              margin-right: 0;
              text-align: right;
            }
            @include min-width(769px) {
              width: 475px;
            }
          }
          .table__row_linksLast {
            @include min-width(320px) {
              margin-right: 0;
              text-align: right;
            }
            @include min-width(769px) {
              width: 475px;
            }
          }
        }
        .row__mobile_links {
          @include min-width(320px) {
            border: none;
          }
          @include min-width(768px) {
            border-bottom: 0.5px solid #b6b6b6;
          }
          & > td {
            @include min-width(320px) {
              vertical-align: top;
            }
            @include min-width(768px) {
              vertical-align: bottom;
            }
          }
        }
      }
      .table__row_slash {
        padding: 0px 1px 0px 4px;
        @include max-width(768px) {
          display: none;
        }
      }
    }
    .about__advantages {
      @include min-width(320px) {
        margin-right: 0px;
        font-family: $font-merel;
        margin-bottom: 10px;
      }
      @include min-width(768px) {
        max-width: 738px;
        margin-top: 30px;
      }
      @include min-width(1200px) {
        max-width: 738px;
        margin-right: 0px;
        margin-left: 20px;

        margin-top: 0px;
        margin-bottom: 0px;
      }
      @include min-width(1700px) {
        max-width: 738px;
        margin-right: 150px;
        margin-left: 0px;
      }
      .advantages__spoiler_content-01,
      .advantages__spoiler_content-02,
      .advantages__spoiler_content-03 {
        @include min-width(320px) {
          transition: all 0.5s ease-in-out;
          overflow: hidden;
          margin-bottom: 12px;
        }
        @include min-width(768px) {
          margin-bottom: 20px;
        }
      }
      #spoiler-01 {
        @include min-width(320px) {
          height: 213px;
        }
        @include min-width(356px) {
          height: 160px;
        }
        @include min-width(448px) {
          height: 140px;
        }
        @include min-width(1200px) {
          height: 233px;
        }
        @include min-width(1255px) {
          height: 213px;
        }
      }
      #spoiler-02 {
        @include min-width(320px) {
          height: 171px;
        }
        @include min-width(356px) {
          height: 150px;
        }
        @include min-width(381px) {
          height: 130px;
        }
        @include min-width(448px) {
          height: 120px;
        }
        @include min-width(786px) {
          height: 188px;
        }
      }
      #spoiler-03 {
        @include min-width(320px) {
          height: 170px;
        }
        @include min-width(768px) {
          height: 211px;
        }
      }
      .spoiler-toggle-01,
      .spoiler-toggle-02,
      .spoiler-toggle-03 {
        cursor: pointer;
      }
      .rotate180 {
        transform: rotate(180deg);
      }
      .about__advantages_title-block {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      &_title {
        @include min-width(320px) {
          font-size: 18px;
          line-height: 35px;
          padding-bottom: 10px;
          text-transform: none;
        }
        @include min-width(768px) {
          font-size: 30px;
        }
      }
      &_desc {
        @include min-width(320px) {
          font-size: 14px;
          line-height: 16px;
        }
        @include min-width(768px) {
          line-height: 23px;
          font-size: 20px;
        }
      }
      &_paragraph {
        @include min-width(320px) {
          padding-bottom: 0px;
        }
        @include min-width(768px) {
          padding-bottom: 20px;
        }
      }
      .advantages__desc_links {
        color: #0075d1;
      }
    }
  }
  .products__list {
    .catalog__products {
      @include min-width(320px) {
        padding-bottom: 0px;
      }
      @include min-width(768px) {
        padding-bottom: 60px;
      }
    }
    .catalog__products_title {
      @include min-width(320px) {
        text-align: left;
        padding-top: 0;
        text-transform: uppercase;
      }
      @include min-width(768px) {
        text-align: center;
      }
    }
    .catalog__pagination {
      justify-content: center;
    }
    .catalog__btn {
      display: none;
    }
  }

  // ======================  SLIDER  ======================
  .product__gallery_item {
    cursor: pointer;
    padding: 30px 0px 20px;
    box-sizing: padding-box;
  }
  .product__swiper {
    display: flex;
    align-items: center;
    position: relative;
    height: 36vw;

    @include min-width(1337px) {
      height: 34.5vw;
    }
    @include min-width(1402px) {
      height: 36vw;
    }

    @include min-width(1850px) {
      height: 675px;
    }
    .swiper-slide {
      padding: 0;
      position: relative;
      top: 8px;
    }
    .swiper-slide__hover {
      position: absolute;
      padding: 28px;
      opacity: 0.7;
      cursor: pointer;
      &:hover {
        opacity: 1;
        transition: 0.5s ease;
      }
    }
    .swiper-button-prev {
      position: absolute;
      top: calc(4% + 1px);
      left: 30%;
      bottom: auto;
      width: 46px;
      height: 21px;
      stroke-width: 3px;

      // @include max-width(1200px) {
      //   top: calc(4% + 10px);
      // }
      @include max-width(1600px) {
        // 1330
        top: calc(4% + 10px);
      }

      &::after {
        content: none;
      }
    }
    .swiper-button-next {
      position: absolute;
      left: 30%;
      top: 100%;
      width: 46px;
      height: 21px;
      stroke-width: 3px;

      &::after {
        content: none;
      }
    }
    .slider-button__icon {
      stroke-width: 3px;
    }
    .slider-button--disabled {
      stroke-width: 1px;
    }
  }
  .product__slider {
    width: 100%;
    height: calc(100% - 100px);
    overflow: hidden;

    .swiper-slide {
      width: 125px;
      height: 125px !important;
      text-align: center;
      font-size: 18px;
      background: #fff;
      color: black;
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
    }
  }
  // POPUP
  .product__popup_video {
    background: rgba(42, 42, 50, 0.92);
    overflow: hidden;
    position: fixed;
    top: 0px;
    left: -1px;
    z-index: 999999999;
    width: 100%;
    height: 100%;

    .popup__video_content {
      margin: 0 auto;
      width: 67vw;
      height: 40vw;
      margin-top: 11vh;

      @include max-width(768px) {
        width: 90vw;
        height: 50vw;
      }

      .popup__video_close {
        background: none;
        border: none;
        position: absolute;
        top: calc(10% - 33px);
        margin-left: 30px;

        @include max-width(768px) {
          margin-left: -30px;
        }
      }

      iframe {
        width: 100%;
        height: 100%;
        margin: 0 auto;
      }
    }
  }
}
