@keyframes animation-video {
  from {
    opacity: 0.5;
  }

  to {
    opacity: 0;
  }
}

@keyframes animation-main-blocks {
  from {
    opacity: 0;
    transform: translateY(-10%);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes animation-main-blocks-reverse {
  from {
    opacity: 1;
    transform: none;
  }

  to {
    opacity: 0;
    transform: translateY(-50px);
  }
}

.main-page {
  background-color: $color-dark-gray-background;
  font-family: $font-bebasneue;
  position: relative;
  min-height: calc(100vh - 2px);


  @media (max-height: 930px) {
    font-size: 2.1vw;
  }

  @include desktop-medium {
    font-size: 2.1vw;
  }

  @include tablet {
    font-size: auto;
  }
}

.main-page--rolled {
  max-height: 100vh;
  overflow: hidden;
}

.main-page--separate {
  background-color: $color-dark-gray-background;
  font-family: $font-bebasneue;
  position: relative;
  min-height: calc(100vh - 2px);

  @include mobile-wide {
    background-color: $color-dark-gray-background;
    background-image: url("../img/background-mobile@1x.png");
    background-repeat: no-repeat;
    background-size: 100% auto;

    background-position: left top;

    @include retina {
      background-color: $color-dark-gray-background;
      background-image: url("../img/background-mobile@2x.png");
    }
  }
}

.main-page__main {
  width: 100%;
  z-index: 1;
}

.main-page__video-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;

  @include mobile-wide {
    display: none;
  }
}

.main-page__video {
  position: absolute;
  z-index: -1;
  opacity: 0.5;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;

  width: auto;
  height: auto;

  @include mobile-wide {
    display: none;
  }
}

.main-page--animation-reverse video {
  animation-name: animation-video;
  animation-duration: 1s;
  animation-fill-mode: both;
  z-index: -1;
}

.main-page__content {
  padding: 197px 0 100px 0;
  display: flex;
  justify-content: space-between;

  @include desktop-medium {
    padding: 10.3vw 0 5.2vw 0;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
  
      padding-top: 5vw;
      padding-bottom: 0;//3px;
    }
  }
  
  @include tablet {
    padding: 55px 0 10px 0;
  }

  @include mobile-wide {
    flex-direction: column;
    padding: 0;
  }
}

.main-page__left {
  width: 50%;
  max-width: 500px;
  font-family: $font-bebasneue;

  @include desktop-medium {
    max-width: 26.3vw;
  }

  @include mobile-wide {
    width: 100%;
    max-width: none;
  }
}

.main-page__right {
  width: 23.5%;
  padding-top: 177px;

  @include desktop-medium {
    padding-top: 9.3vw;
    width: 364px;
  }

  @include tablet {
    width: 33%;
  }

  @include mobile-wide {
    display: flex;
    order: -1;
    width: 100%;
    padding-top: 30px;
    padding-bottom: 83px;
    margin-left: auto;
  }
}

.main-page__right-container {
  display: flex;
  width: 100%;
  justify-content: space-between;

  @include mobile-wide {
    justify-content: flex-end;
    padding-top: 30px;
    padding-bottom: 83px;
    margin-left: auto;
  }
}

.main-page__video-button {
  border-radius: 50%;
  width: 115px;
  min-width: 115px;
  height: 115px;
  position: relative;
  border: 6px solid $color-blue-gradient-start;
  background-image: url("../img/menu2@1x.png");
  background-size: cover;
  transition: all 0.3s ease;
  outline: none;

  svg {
    width: 30.4%;
    height: 46.9%;
    position: absolute;
    top: 25%;
    left: 40%;
    fill: $color-blue-gradient-start;
    transition: all 0.3s ease;
  }

  &:hover {
    border: 6px solid $color-orange;

    svg {
      fill: $color-orange;
    }
  }

  @include desktop-medium {
    width: 6vw;
    min-width: 6vw;
    height: 6vw;
    border: 0.3vw solid $color-blue-gradient-start;

    &:hover {
      border: 0.3vw solid $color-orange;
    }
  }

  @include mobile-wide {
    width: 70px;
    min-width: 70px;
    height: 70px;
    border: 4px solid $color-blue-gradient-start;

    svg {
      width: 21px;
      height: 33px;
      top: 24%;
      left: 38%;
      fill: $color-blue-gradient-start;
      transition: all 0.3s ease;
    }
  }
}

.main-page__text {
  margin: 0;
  font-size: 50px;
  line-height: 54px;
  margin-bottom: 103px;
  font-weight: 400;
  text-transform: uppercase;

  @include desktop-medium {
    font-size: 1.25em;
    line-height: 1.08;
    margin-bottom: 5.4vw;
  }

  @include tablet {
    font-size: 23px;
  }

  @include mobile-wide {
    width: 280px;
    font-size: 29px;
    line-height: 29px;
    margin-bottom: 32px;
  }
}

.main-page__text-bold {
  font-weight: 700;
  letter-spacing: 0.01em;
  padding-right: 200px;

  @include mobile {
    padding-right: 50px;
  }
}

.main-page__video-description {
  margin: 0;
  font-size: 20px;
  line-height: 29px;
  font-family: $font-merel;
  font-weight: 700;
  text-align: right;
  padding-right: 14px;
  align-self: center;

  @include desktop-medium {
    padding-right: 0.7vw;
    padding-bottom: 0.7vw;
  }

  @include tablet {
    font-size: 16px;
    line-height: 1.2;
    padding-bottom: 10px;
  }

  @include mobile-wide {
    max-width: 240px;
    font-size: 20px;
    padding-right: 14px;

    width: 50%;
    line-height: 20px;
    padding-bottom: 0;
  }

  @include mobile {
    display: none;
  }
}

.main-page--animation .main-page__content {
  animation-name: animation-main-blocks;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

.main-page--animation-reverse .main-page__content {
  animation-name: animation-main-blocks-reverse;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}
