.bread-crumbs__container {
  @include min-width(320px) {
    padding-left: 15px;
    max-width: 1660px;
    width: 100%;
    margin: 0 auto;
  }
  @include min-width(992px) {
    padding-left: 40px;
  }
  @include min-width(1750px) {
    padding-left: 0px;
  }

  .bread-crumbs {
    font-family: $font-merel;
    font-size: 20px;
    line-height: 23px;
    text-decoration: none;
    color: inherit;

    @include min-width(320px) {
      font-size: 14px;
      padding-top: 3px;
    }
    @include min-width(992px) {
      font-size: 18px;
      padding-top: 10px;
    }
    @include min-width(1600px) {
      font-size: 20px;
    }

    .bread_cramps--active {
      color: white;
    }

    & a {
      @include link-reset;
      color: #c4c4c4;
    }

    .bread_crumps__chevron {
      padding: 0 6px 0 6px;
      color: #ed8528;
    }
  }
}
