.dealers__list-dealers {
  @include mobile-wide {
    margin: 0 auto;
    width: 100%;
    max-width: 450px;
  }

  @include mobile {
    margin: 0;
    max-width: 100%;
  }
}

.integrate .dealers__list-dealers {
  opacity: 0;

  @include mobile-wide {
    opacity: 1;
  }
}

.dealers__list-heading {
  font-family: $font-bebasneue;
  font-size: 40px;
  line-height: 107.1%;
  color: $color-white;
  font-weight: 400;
  margin-top: 36px;
  margin-bottom: 33px;

  @include desktop-medium {
    font-size: 1em;
    margin-top: 1.8vw;
    margin-bottom: 1.7vw;
  }

  @include desktop-small {
    @media (max-height: $max-height-small) {
      margin-top: 14px;
      margin-bottom: 0;
    }
  }

  @include tablet {
    font-size: 24px;
    line-height: 24px;
    margin-top: 20px;
    margin-bottom: 16px;
  }
}

.dealers__list-heading--desktop {
  @include mobile-wide {
    display: none;
  }
}

.dealers__list-heading--mobile {
  display: none;
  font-size: 24px;
  line-height: 24px;
  margin-top: 20px;
  margin-bottom: 16px;

  @include mobile-wide {
    display: block;
    margin-top: 0;
  }
}

.dealers__map {
  margin-left: 50px;
  flex-grow: 1;

  @include desktop-medium {
    position: relative;
    max-width: 1100px;
    margin-left: 2.6vw;
  }

  @include mobile-wide {
    margin-left: 0;
    flex-grow: 0;
    display: none;
  }
}

.integrate .dealers__map {
  opacity: 0;
}

