.vacancies {
  .vacancies__container {
    max-width: 1680px;
    padding: 0;
    font-family: $font-merel;
    font-size: 20px;

    .vacancies__title {
      @include min-width(320px) {
        padding-top: 30px;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        line-height: 35px;
        text-transform: none;
        padding-left: 15px;
        text-transform: uppercase;
        font-family: $font-bebasneue;
      }
      @include min-width(768px) {
        padding-top: 54px;
        padding-left: 80px;
        font-size: 50px;
      }
    }
    .vacancies__main {
      @include min-width(320px) {
        padding: 0px 15px 0px 15px;
        font-size: 14px;
      }
      @include min-width(768px) {
        padding: 60px 50px;
        font-size: 20px;
      }
      @include min-width(1044px) {
        padding: 60px 150px;
        font-size: 20px;
      }
      @include min-width(1544px) {
        padding: 60px 242px 0px 242px;
        font-size: 20px;
      }
      & > p {
        line-height: 23px;
        padding-top: 23px;
      }
    }
  }
  .vacancies-block_container {
    margin: 0px auto;

    .vacancies-block {
      @include min-width(320px) {
        margin-top: 60px;
        display: flex;
        flex-direction: column;
      }
      @include min-width(768px) {
        flex-direction: row;
      }

      &__item {
        @include min-width(320px) {
          position: relative;
          cursor: pointer;
          margin: 0px 15px 20px;
          background: linear-gradient(180.07deg, #3b3b47 41.16%, #31313b 99.91%);
          box-shadow: 4.67742px 0px 9.35484px rgba(0, 17, 31, 0.5);
          border-radius: 4.67742px;
        }
        @include min-width(768px) {
          margin: 0px 5px 0px;
        }
        @include min-width(923px) {
          margin: 0px 20px 0px;
        }
        @include min-width(1460px) {
          margin: 0px 57px 0px;
        }
        & > img {
          @include min-width(320px) {
            height: 113px;
            width: 100%;
          }
          @include min-width(768px) {
            height: auto;
            width: auto;
          }
        }
        &:last-child {
          @include min-width(320px) {
            margin-right: 15px;
          }
          @include min-width(768px) {
            margin-right: 25px;
          }
          @include min-width(1460px) {
            margin-right: 57px;
          }
        }
        &:hover {
          color: #c4c4c4;
          background: linear-gradient(180.07deg, #3b3b47 41.16%, #31313b 99.91%);
          box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
          border-radius: 4.67742px;
          transition: 0.3s ease;
        }
        & > p {
          @include min-width(320px) {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-family: $font-bebasneue;
            font-style: normal;
            font-weight: normal;
            font-size: 21px;
            line-height: 107.1%;
            text-align: center;
            text-transform: uppercase;
            font-weight: 100;
            width: 100%;
            padding: 0px 20px;
          }
          @include min-width(768px) {
            font-size: 20px;
          }
          @include min-width(1044px) {
            font-size: 24px;
          }
          @include min-width(1668px) {
            font-size: 34px;
          }
        }
      }
    }
  }

  #vacantions {
    cursor: default;

    &:after {
      display: none;
    }
  }
}
.space {
  @include min-width(320px) {
  }
  @include min-width(1300px) {
    width: 100%;
    height: 50px;
  }
  @include min-width(1700px) {
    height: 200px;
  }
  @include min-width(1800px) {
    height: 200px;
  }
}
.vacancies__request {
  @include min-width(320px) {
    font-family: $font-merel;
    margin-top: 30px;
    margin-bottom: 40px;
    padding-bottom: 5px;
    margin-left: 15px;
    margin-right: 15px;
    display: block;
    border-bottom: 1px solid #c4c4c4;
    text-align: left;
    font-size: 14px;
    font-weight: 100;
    line-height: 16px;
  }
  @include min-width(768px) {
    display: inline-block;
    width: 788px;
    text-align: right;
    margin-left: 0px;
    font-size: 20px;
    margin-right: 0px;
    margin-top: 70px;
    margin-bottom: 80px;
  }

  & > a {
    color: #0075d1;
  }
}
