.info {
  font-family: $font-merel;
}

.info__container {
  display: flex;

  @include mobile-wide {
    flex-direction: column;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.info__col {
  width: 49%;
  margin-right: 3.4%;
  padding-top: 54px;

  &:nth-child(2n) {
    width: 44%;
    margin-right: 0;
  }

  @include desktop-medium {
    padding-top: 2.8vw;
  }

  @include mobile-wide {
    padding-top: 54px;

    &:nth-child(n) {
      width: auto;
      padding-top: 16px;
      margin-right: 0;
    }
  }
}

.info__title {
  margin: 0;
  padding: 0;
  padding-bottom: 28px;
  font-size: 30px;
  line-height: 35px;
  color: $color-orange;

  @include desktop-medium {
    padding-bottom: 1.4vw;
    font-size: 0.75em;
    line-height: 1.166;
  }

  @include mobile-wide {
    padding-bottom: 16px;
    font-size: 18px;
    line-height: 21px;
  }
}

.info__col p {
  margin: 0;
  padding: 0;
  padding-right: 16px;
  padding-bottom: 30px;
  font-size: 20px;
  line-height: 26px;
  color: #ffffff;

  @include desktop-medium {
    padding-right: 0.8vw;
    padding-bottom: 1.5vw;
    font-size: 0.5em;
    line-height: 1.3;
  }

  @include mobile-wide {
    padding-right: 0;
    padding-bottom: 18px;
    font-size: 14px;
    line-height: 19px;

    &:last-of-type {
      padding-bottom: 3px;
    }
  }
}

.info__col b {
  font-weight: normal;
  color: #2486d5;
}
