.starlink-main {
  .cable-selection {
    transform: translateY(0);
    opacity: 1;

    .container {
      max-width: 1640px;
      padding-top: 6vh;
      padding-bottom: 16px;

      @include min-width(1024px) {
        padding-left: 13px;
      }

      @include desktop-small {
        @media (max-height: $max-height-small) {
          padding-top: 18px;
        }
      }

      @include tablet {
        flex-direction: column;
        min-height: 593px;
        padding-bottom: 30px;
      }

      @include mobile {
        padding-top: 30px;
        padding-bottom: 35px;
      }
    }

    @include tablet {
      .switch {
        display: none;
      }
    }
  }

  .cable-selection--animate {
    & .container {
      @include min-width(1024px) {
        animation: slide 0.5s ease-in-out;
      }
    }
  }

  .cable-selection--reverse {
    & .container {
      @include min-width(1024px) {
        animation: slide-revers 1.1s ease-in-out;
        animation-direction: reverse;
      }
    }
  }

  @keyframes slide {
    from {
      transform: translateY(-70px);
      opacity: 0;
    }

    to {
      transform: translateY(0);
    }
  }

  @keyframes slide-revers {
    from {
      transform: translateY(-70px);
      opacity: 0;
    }

    to {
      transform: translateY(0);
    }
  }
}
