.dealers-legislation {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: 10px;
  width: 300px;
  align-content: flex-start;

  a {
    text-decoration: none;
    font-size: 14px;
    line-height: 16px;
    font-family: $font-merel;
    color: $color-gray;
  }

  @include desktop-medium {
    padding-top: 0.5vw;
    width: 250px;

    a {
      font-size: 12px;
      line-height: 14px;
    }
  }

  @include tablet {
    width: 40%;
    min-width: 270px;

    a {
      font-size: 14px;
      line-height: 16px;
    }
  }

  @include mobile-wide {
    width: 100%;
    margin: 0 auto;
    margin-top: 20px;
    max-width: 290px;
    flex-direction: row;
  }
}

.dealers-legislation__first {
  width: 100%;
  max-width: 300px;
  min-height: 36px;

  @include desktop-medium {
    min-height: 1.8vw;
  }

  @include tablet {
    max-width: 300px;
    min-height: 20px;
  }

  @include mobile-wide {
    padding-left: 12px;
    margin-bottom: 9px;
  }
}

.dealers-legislation__second,
.dealers-legislation__third {
  width: 100%;
  max-width: 300px;
  min-height: 36px;

  @include desktop-medium {
    max-width: 15.7vw;
    min-height: 1.8vw;
  }

  @include tablet {
    max-width: 300px;
    min-height: 20px;
  }

  @include mobile-wide {
    width: auto;
  }
}

.dealers-legislation__second {
  @include mobile-wide {
    margin-bottom: 0;
  }
}
