.desktop-cable-selection-theme {
  background: #363640;

  .header {
    background: #363640;
  }
}

.desktop-cable-selection__drum {
  @include min-width(320px) {
    position: absolute;
    top: 34%;
    left: -2.3%;
    display: none;
  }
  @include min-width(992px) {
    display: block;
  }
}
.desktop-cable-selection__pages {
  @include min-width(320px) {
    display: flex;
    align-items: flex-end;
  }
  @include min-width(992px) {
    position: absolute;
    top: 60%;
    left: get-vw(370px);
  }
  @include min-width(1823px) {
    left: get-vw(570px);
  }

  .page__01 {
    width: get-vw(180px);
    height: get-vw(160px);
    position: relative;
  }
  .pages__08 {
    width: get-vw(96px);
    height: get-vw(65px);
  }
}

.desktop-cable-selection {
  padding-bottom: 80px;

  .desktop-cable-selection__container {
    @include min-width(320px) {
      max-width: 100%;
      margin: 0 auto;
      padding: 0px 15px;
      width: 100%;
      flex-direction: column;
    }
    @include min-width(992px) {
      justify-content: space-between;
      flex-direction: row;
      padding: 0px get-vw(100px);
    }
    @include min-width(1650px) {
      padding: 0px get-vw(200px);
    }

    justify-content: space-between;
    max-width: 100%;
    margin: 0 auto;
    padding: 0px get-vw(200px);
    width: 100%;

    .choose-analog {
      padding: 0px;

      &::after {
        content: "";
        width: 1px;
        height: 53vh;
        background-color: #b6b6b6;
        top: 62%;
        right: get-vw(-50px);
      }

      .module-of-selection {
        @include min-width(992px) {
          padding: 56px 0 0 82px;
        }

        & > h2 {
          @include min-width(320px) {
            margin-top: 30px;
            margin-bottom: 20px;
            font-weight: normal;
            font-size: 24px;
          }
          @include min-width(992px) {
            font-size: get-vw(50px);
            margin-bottom: get-vw(60px);
            margin-top: 0px;
          }
        }

        .choose-other__dropdown {
          .choose-other__scope-application {
            @include min-width(320px) {
              width: auto;
              margin-bottom: 20px;
            }
            @include min-width(992px) {
              margin-bottom: 30px;
            }

            & > p {
              @include min-width(320px) {
                font-size: 20px;
                margin-bottom: 10px;
                color: #ed8528;
              }
              @include min-width(992px) {
                font-size: get-vw(40px);
                margin-bottom: get-vw(30px);
              }
            }
          }
        }
      }
    }

    .choose-other__form {
      @include min-width(320px) {
        display: none;
      }
      @include min-width(992px) {
        max-width: get-vw(706px);
        display: block;
      }
    }
    .choose-other {
      // SLIDER
      padding-top: 187px;

      max-width: get-vw(706px);
      padding-left: 0;

      .swiper-wrapper {
        margin-bottom: 30px;
        max-width: get-vw(706px);
      }
      .choose-other__modification {
        .select-modification__options {
          padding-top: 0px;
          width: get-vw(706px);
          & > p {
            color: #ed8528;
            display: block;
            width: 100%;
            padding-bottom: 30px;
            margin-bottom: 0;
            font-size: get-vw(40px);
          }

          .select-desktop-modification-item {
            align-items: center;
            flex-grow: 1;
            -ms-flex-negative: 1;
            flex-shrink: 1;

            margin: 0 get-vw(15px) get-vw(20px) 0;
            padding: get-vw(20px) 0;
            font-size: get-vw(20px);
            line-height: get-vw(26px);

            text-align: center;
            text-transform: uppercase;
            background: #31313b;
            box-shadow: 0 get-vw(4px) get-vw(4px) rgba(0, 0, 0, 0.25);
            border-radius: 100px;
            border: 1px solid #0075d1;
            cursor: pointer;
            transition: color 0.2s ease-in-out;
            position: relative;

            &:hover {
              color: #0075d1;
            }

            &:nth-child(3n) {
              margin-right: 0;
            }

            .modification__item_icon {
              width: get-vw(14px);
              height: get-vw(14px);
              position: absolute;
              right: 10%;
              top: 40%;
            }
          }

          .select-modification__quantityOV {
            display: flex;

            .select-modification__rectangle-item {
              background: #31313b;
              border: 1px solid #c4c4c4;
              box-sizing: border-box;
              padding: 9px 15px 12px 17px;
              margin-right: 20px;
              cursor: pointer;
              font-family: $font-merel;
              font-size: 22px;

              &:hover {
                border: 1px solid #0075d1;
                transition: 0.3s;
              }
            }
            .select-modification__rectangle-item_active {
              border: 1px solid #0075d1;
            }
          }
        }
        .select-modification__item {
          margin-right: 15px;
        }
      }
      .choose-other__form_btns {
        display: flex;
        padding-bottom: 30px;

        .desktop-cable-select-swiper-button-next,
        .desktop-cable-select-swiper-button-prev {
          background-color: transparent;
          border: 1px solid #0075d1;
          box-sizing: border-box;
          border-radius: 100px;
          color: white;

          padding: get-vw(17px) get-vw(89px) get-vw(15px);
          font-size: get-vw(20px);

          &:disabled {
            background: #363640;
            box-sizing: border-box;
            border-radius: 100px;
            color: white;
            background-color: transparent;

            font-size: get-vw(20px);
            padding: get-vw(17px) get-vw(89px) get-vw(15px);
            width: get-vw(220px);
            margin-left: get-vw(30px);

            display: flex;
            align-items: center;
            justify-content: space-between;
          }
        }
        .desktop-cable-select-swiper-button-prev {
          padding: get-vw(17px) get-vw(30px) get-vw(15px) get-vw(89px);
          display: flex;
          width: get-vw(220px);
          align-items: center;
          justify-content: space-between;
          margin-left: get-vw(30px);
          background: #0075d1;
        }
      }
    }
  }

  .select-scope-application {
    // DROPDOWS

    & .select-scope-application__value {
      @include min-width(320px) {
        min-width: 290px;
        padding: 17px 20px 13px;

        display: flex;
        line-height: 107.1%;
        background: #363640;
        position: relative;
        display: flex;
        align-items: center;
        border-top-left-radius: 36.5px;
        border-top-right-radius: 36.5px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        transition: all $style-change-duration $style-change-function;
        outline: none;
        z-index: 6;
        animation: close-select 0s linear;
      }
      @include min-width(992px) {
        min-width: get-vw(415px);
        padding: get-vw(25px) get-vw(40px) get-vw(19px) get-vw(47px);
      }

      & > span {
        @include min-width(320px) {
          font-size: 16px;
        }
        @include min-width(992px) {
          font-size: get-vw(27px);
        }
      }
    }
    .value__selected {
      background: #0075d1;
    }
    &[data-hidden="true"] {
      & .select-scope-application__value {
        border: 1px solid #0075d1;
        border-bottom-left-radius: 36.5px;
        border-bottom-right-radius: 36.5px;
        transition: all $style-change-duration $style-change-function;
      }

      & .select-scope-application__options {
        transition: all $style-change-duration $style-change-function;
        animation: close-select 0.2s linear;
      }
    }

    &[data-hidden="false"] {
      .select-scope-application__value {
        transition: all $style-change-duration $style-change-function;
        border: 1px solid #0075d1;
        background: #363640;

        & svg {
          transform: rotate(-90deg);
        }

        @include tablet {
          border: 1px solid #0075d1;
          padding: 17px 20px 13px 20px;
          border-top-left-radius: 21.5px;
          border-top-right-radius: 21.5px;
        }
      }

      .select-scope-application__options {
        padding-top: 5px;
        padding-bottom: 25px;
        max-height: inherit;
        animation: open-desktop-select 0.2s linear;
        border: 1px solid #0075d1;
      }
    }
  }
}
