.desktop-videos {
  .desktop-videos__container {
    @include min-width(320px) {
      padding: 30px 15px;
      width: 100%;
      margin: 0 auto;
      max-width: 466px;
    }
    @include min-width(992px) {
      padding: get-vw(56px) 0px 0px;
      max-width: get-vw(1496px);
    }
    & > h1 {
      @include min-width(320px) {
        font-size: 24px;
        font-family: $font-bebasneue;
        font-weight: 100;
        line-height: 107.1%;
      }
      @include min-width(992px) {
        margin-bottom: get-vw(-20px);
        font-size: get-vw(50px);
      }
    }
  }
  // pagination
  .videos-paginator {
    @include min-width(320px) {
      flex-wrap: wrap;
      padding-bottom: 10px;
      display: flex;
      padding-top: 10px;
    }
    @include min-width(992px) {
      padding-top: get-vw(60px);
      padding-bottom: get-vw(60px);
    }
    .swiper-wrapper {
      .article-slide {
        @include min-width(320px) {
          padding-bottom: 30px;
          margin-right: 1px;
        }
        @include min-width(992px) {
          padding-bottom: get-vw(60px);
        }
        .article-slide__general {
          @include min-width(992px) {
            display: flex;
            justify-content: space-between;
          }
        }
        .article-slide__additional {
          @include min-width(320px) {
          }
          @include min-width(992px) {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
          }
          .article-slide_item {
            @include min-width(320px) {
            }
            @include min-width(992px) {
              width: get-vw(465px);
            }
            .article-slide__title {
              & > h2 {
                @include min-width(320px) {
                  padding: 0px 8px;
                  height: 70px;
                  width: 100%;
                }
                @include min-width(992px) {
                  height: get-vw(126px);
                  font-size: get-vw(22px);
                  line-height: get-vw(26px);
                  height: get-vw(126px);
                  padding: 0px get-vw(10px);
                }
              }
            }
            & > p {
              @include min-width(320px) {
                font-size: 14px;
                line-height: 16px;
              }
              @include min-width(992px) {
                font-size: get-vw(20px);
                line-height: get-vw(23px);
              }
            }
          }
        }
        .article-slide_item {
          @include min-width(320px) {
            font-family: $font-merel;
            padding-bottom: 20px;
            position: relative;
            width: 100%;
            max-width: 466px;
          }
          @include min-width(992px) {
            width: get-vw(690px);
            padding-bottom: get-vw(80px);
            max-width: none;
          }
          .article-slide__title {
            background: #2d2d36;
            box-shadow: 0px 1px 5px rgba(0, 17, 31, 0.3);
            border-radius: 0px 0px 9px 9px;
            & > h2 {
              @include min-width(320px) {
                padding: 0px 20px;
                text-transform: none;
                font-size: 14px;
                line-height: 16px;
                font-weight: 100;
                height: 70px;
                vertical-align: middle;
                display: table-cell;
              }
              @include min-width(992px) {
                font-size: get-vw(31.5px);
                line-height: get-vw(37px);
                height: get-vw(189px);
              }
            }
          }
          & > p {
            @include min-width(320px) {
              display: flex;
              justify-content: flex-end;
              padding: 3px 8px;
              font-size: 12px;
              line-height: 14px;
              color: #c4c4c4;
              background: #24242b;
              border-radius: 0px 0px 9px 9px;
            }
            @include min-width(992px) {
              font-size: get-vw(26.7px);
              line-height: get-vw(31px);
              padding: get-vw(10px) get-vw(40px);
            }
          }
          & > img {
            cursor: pointer;
            border-radius: 9px 9px 0px 0px;
          }
        }
        .videos__current-page {
          @include min-width(320px) {
            text-align: center;
            font-family: $font-merel;
            font-size: 18px;
            line-height: 28px;
            color: #c4c4c4;
            position: absolute;
            bottom: -30px;
            left: 50%;
            right: 50%;
            margin-right: -50%;
            transform: translate(-50%, -50%);
            display: inline-block;
            width: 100%;
            padding-right: 15px;
          }
          @include min-width(992px) {
            bottom: get-vw(-3px);
            font-size: get-vw(24px);
            padding-right: 0px;
          }
        }
        .current-page_white {
          color: white;
        }
      }
    }
    .desktop-videos-slider__pagination {
      & > span {
        @include min-width(320px) {
          width: 34px;
          height: 7px;
          border-radius: 25px;
          background-color: #c4c4c4;
          opacity: 1;
          outline: 0;
        }
        @include min-width(992px) {
          width: get-vw(81px);
          height: get-vw(7px);
        }
      }
      .swiper-pagination-bullet-active {
        background-color: #4694d2;
      }
    }
    .swiper-pagination {
      @include min-width(320px) {
        bottom: 24px;
        padding-right: 15px;
      }
      @include min-width(992px) {
        padding-right: 0px;
        bottom: get-vw(130px);
      }
    }
  }
}
