.docs {
  display: flex;
  width: 20.07%; // 325/1620

  @include tablet {
    width: auto;
  }

  @include mobile-wide {
    width: auto;
  }
}

.docs__container {
  margin-top: 190px;
  margin-left: 5px;

  @include desktop-medium {
    margin-top: 10.2vw;
    margin-left: 0.2vw;
  }

  @include desktop-small {
    @include desktop-small {
      @media (max-height: $max-height-small) {
        margin-top: 5.5vw;
      }
    }
  }
 
  @include tablet {
    margin-top: 0;
    margin-left: 95px;
  }

  @include mobile-wide {
    width: 100%;
    margin-top: 28px;
    margin-left: 0;
  }
}

.docs__content {
  padding-bottom: 76px;

  @include desktop-medium {
    padding-bottom: 4.2vw;
  }

  @include tablet {
    padding-bottom: 30px;
  }

  @include mobile-wide {
    padding-bottom: 28px;
  }
}

.docs__content-title {
  margin: 0;
  margin-bottom: 15px;
  padding: 0;
  // font-family: $font-bebasneue;
  // font-size: 40px;
  // line-height: 43px;
  font-weight: normal;

  @include desktop-medium {
    margin-bottom: 0.7vw;
  }

  @include tablet {
    font-size: 30px;
    line-height: 1;
  }

  @include mobile-wide {
    margin-bottom: 22px;
    font-size: 24px;
    line-height: 1;
  }
}

.docs__list {
  @include list-reset;
  display: flex;

  @include mobile-wide {
    justify-content: space-around;
    padding: 0 20px; // 6.25vw
  }
}

.docs__item {
  margin-right: 36px;

  @include desktop-medium {
    margin-right: 1.8vw;
  }

  @include mobile-wide {
    margin-right: 0;
  }
}

.docs__link {
  position: relative;
  display: block;
  text-decoration: none;

  &::before {
    content: "";
    display: block;
    width: 49px;
    height: 34px;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: block;
    background-color: #ffffff;
    opacity: 0;
    transition: opacity $style-change-duration $style-change-function;
  }

  &--ru::before {
    background-image: url("../img/flag-ru.svg");
  }

  &--gb::before {
    width: 50px;
    background-image: url("../img/flag-gb.svg");
  }

  &--es::before {
    background-image: url("../img/flag-es.svg");
  }

  &:hover::after {
    opacity: 0.5;
  }

  &:active::after,
  &:focus::after {
    opacity: 0.15;
  }

  @include desktop-medium {
    &::before {
      width: 2.5vw;
      height: 1.7vw;
    }

    &--gb::before {
      width: 2.6vw;
    }
  }

  @include tablet {
    &::before {
      width: 49px;
      height: 34px;
    }

    &--gb::before {
      width: 50px;
    }
  }
}

